import * as Cookies from 'js-cookie';
import currencyUtil from '../currency';
import { SL_EventBus } from '../event-bus';
import ga from '../dataReport/ga';
import { clickAdsData } from '../dataReport/ads';
import { clickFbData } from '../dataReport/fb';
import { getEventID } from '../report/tool';
import { pageMap } from './const';

const REPORT_ADD_CART = Symbol('REPORT_ADD_CART');
const PAYPAL_CLICK = Symbol('PAYPAL_CLICK');

const paypalPage = {
  Cart: 'Cart',
  MiniCart: 'MiniCart',
  FilterModal: 'FilterModal',
};

// base64
export const encode = (str) => {
  if (typeof window === 'undefined') return '';
  // 
  const ec = window?.encodeURI(str);
  // base64
  return window?.btoa(ec);
};

const isFn = (object) => typeof object === 'function';
const templateAlias = window.Shopline.uri.alias;

class TradeReport {
  constructor() {
    this.eventBus = SL_EventBus;
    this.storeCurrency = Cookies.get('currency_code');
    // this.pageMap = {
    //   // ProductDetail: 60006253,
    //   Cart: 60006254,
    //   MiniCart: 60006262,
    // };
    this.hdPage = {
      // ProductDetail: 'pdp',
      Cart: 'cart',
      MiniCart: 'cart',
    };
  }

  /**  */
  touch(data) {
    const { pageType, actionType, value } = data;
    const val = { ...value, ...{ currency: this.storeCurrency } };
    const gaParam = ga.click(pageType, actionType, val);
    const adsParams = clickAdsData(pageType, actionType, val);
    const fbParams = clickFbData(actionType, val);
    const params = {
      GAAds: adsParams,
      GA: gaParam,
      FBPixel: fbParams,
    };
    this.eventBus?.emit('global:thirdPartReport', params);
  }
}

const setAddtoCart = (payAmount, currency, eventID) => {
  const params = {
    payAmount,
    currency,
    eventId: eventID || `addToCart${getEventID()}`,
    eventTime: Date.now(),
    eventName: 'AddToCart',
  };
  return params;
};

const hdRpCheckout = (data, id) => {
  const products = data?.map((item) => ({
    product_id: item.productSeq,
    variantion_id: item.productSku,
    quantity: item.productNum,
    price: currencyUtil.formatNumber(Number(item?.productPrice) || 0).toString(),
    product_name: item.productName,
  }));
  window.HdSdk?.shopTracker?.report(id, { event_name: '105', page: 'cart', products });
};

const setIniiateCheckout = (seq, needReport) => {
  let eventID;
  if (isFn(needReport)) {
    eventID = needReport();
  }
  // cookiecookie
  const cookieMap = Cookies.get();
  Object.keys(cookieMap).forEach((key) => {
    if (/^\d+_fb_data$/.test(key)) {
      Cookies.remove(key);
    }
  });
  Cookies.set(`${seq}_fb_data`, {
    tp: eventID ? 2 : 1,
    et: Date.now(),
    ed: eventID || getEventID(),
  });
};

// 
const reportCheckout = (data) => {
  const { isCart, report, products } = data;
  if (isCart) {
    const event_id = pageMap[templateAlias] ? pageMap[templateAlias] : pageMap.MiniCart;
    hdRpCheckout(products, event_id);
  }
  // ads
  if (isFn(report)) {
    report();
  }

  // 
  sessionStorage.setItem(encode('checkout_track'), '[]');
};

const tradeReport = new TradeReport();
export {
  tradeReport,
  TradeReport,
  REPORT_ADD_CART,
  PAYPAL_CLICK,
  paypalPage,
  reportCheckout,
  setIniiateCheckout,
  setAddtoCart,
};
