import SkuTradeFlatten from '../../commons/js/sku-trade/sku-trade-flatten';
import SkuTradeSelect from '../../commons/js/sku-trade/sku-trade-select';
import { changeURLArg, delParam } from '../../../shared/utils/url';

export default function initSku({ id, sku, spu, mixins, onInit, onChange }) {
  const dataDom = $(`#product-sku-trade-data_${id}`);
  const skuStyle = dataDom.data('skustyle');
  const selectSku = dataDom.data('selectsku');

  const SkuClass = skuStyle === 'flatten' ? SkuTradeFlatten : SkuTradeSelect;

  const trade = new SkuClass({
    domReady: true,
    root: `#product-detail-sku-trade_${id}`,
    sku,
    spu,
    mixins,
    initialSkuSeq: selectSku,
    onInit: (tradeData, activeSku, root) => {
      onInit?.(tradeData, activeSku, root);

      window.SL_EventBus.emit('product:sku:init', [activeSku, id]);
    },
    onChange: (activeSku) => {
      if (id === 'productDetail') {
        if (activeSku) {
          window.history.replaceState({}, document.title, changeURLArg(window.location.href, 'sku', activeSku.skuSeq));
        } else {
          window.history.replaceState({}, document.title, delParam('sku'));
        }
      }

      window.SL_EventBus.emit('product:sku:change', [activeSku, id, trade.dataPool]);

      onChange?.(activeSku);
    },
  });
  return trade; // sku
}
