class EventAddCheckoutEnd {
  index = 0;

  name = 'event_add_checkout_by_le';

  constructor(name) {
    if (name) {
      this.name = name;
    }
  }

  static getUuid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      // eslint-disable-next-line no-bitwise
      const r = (Math.random() * 16) | 0;
      // eslint-disable-next-line no-bitwise
      const v = c === 'x' ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }

  getCheckoutKey() {
    this.index += 1;
    return `${this.name}_${EventAddCheckoutEnd.getUuid()}_${this.index}`;
  }

  getUuidAndMonitorCheckoutEnd(eventName, callback) {
    const key = this.getCheckoutKey();
    // data: { event_status: 1, stage, isCart: associateCart, products },
    window?.SL_EventBus.on(eventName, (data) => {
      const { event_status, stage } = data.data ?? {};
      if (stage === key) {
        callback(event_status);
      }
    });
    return key;
  }
}

export const checkoutEnd = new EventAddCheckoutEnd();
