import SkuUtil from '../../../utils/sku/skuUtil';
import { getSku, getSkuComMap, transSkuSpecList, getAttrValue } from '../../../utils/sku/skuFilter';

export default class BaseSkuTrade {
  constructor({
    sku,
    spu,
    initialSkuSeq, // skuSeq
    dataPool, // skutrade skutradedataPooldataPool

    root, // sku
    domReady, // dom
    onInit,
    onChange,
    onDestory,
    mixins,
  }) {
    this.mixins = mixins;
    this.root = $(root); // skuTrade

    if (dataPool) {
      this.dataPool = dataPool;
    } else {
      this.dataPool = {};
    }

    if (!this.dataPool.inited) {
      this.dataPool.sku = sku || {};
      this.dataPool.spu = spu || {};
      this.dataPool.attrArray = []; // 
      this.dataPool.currentSpecList = []; // id[`${nameId:${valueId}`]
      this.dataPool.skuType = ''; // 
      this.dataPool.validSkuMap = {};
      this.dataPool.invalidSkuMap = {};
      this.dataPool.activeSku = null;
      this.dataPool.initialSkuSeq = initialSkuSeq;
    }

    this.onInit = onInit;
    this.onChange = onChange;
    this.onDestory = onDestory;
    this.init(domReady);
  }

  beforeInitDom() {
    this.mixins?.forEach?.((item) => {
      item?.beforeInitDom?.call?.(this, this);
    });
  }

  afterInitDom() {
    this.mixins?.forEach?.((item) => {
      item?.afterInitDom?.call?.(this, this);
    });
  }

  beforeUpdate() {
    this.mixins?.forEach?.((item) => {
      item?.beforeUpdate?.call?.(this, this);
    });
  }

  afterUpdate() {
    this.mixins?.forEach?.((item) => {
      item?.afterUpdate?.call?.(this, this);
    });
  }

  init(domReady) {
    if (!this.dataPool.inited) {
      this.dataPool.skuUtil = new SkuUtil();
      this.initAttrArray();
      this.initSkuComMap();
      this.initFirstChecked();
    }
    this.beforeInitDom();

    if (domReady) {
      this.initDom();
    } else {
      this.createAndInitDom();
    }
    this.afterInitDom();
    this.render();

    this.dataPool.inited = true;
    try {
      this.onInit?.(this, this.dataPool.activeSku, this.root);
    } catch (e) {
      setTimeout(() => {
        throw e;
      });
    }
  }

  // sku
  initSkuComMap() {
    const { validSkuMap, invalidSkuMap } = getSkuComMap(this.dataPool.sku.skuList);

    this.dataPool.validSkuMap = this.dataPool.skuUtil.initSku(validSkuMap);
    this.dataPool.invalidSkuMap = invalidSkuMap;
  }

  // 
  initAttrArray() {
    this.dataPool.attrArray = transSkuSpecList(this.dataPool.sku.skuAttributeMap);

    if (this.dataPool.attrArray.length) {
      this.dataPool.skuType = 'multi';
    } else {
      this.dataPool.skuType = 'single';
    }
  }

  isPreview() {
    const currentUrl = window.location.pathname;
    return /^[/]preview[/].*/.test(currentUrl);
  }

  // initialSkuSeq
  initFirstChecked(allowNotAvailable) {
    if (this.dataPool.skuType === 'single') {
      [this.dataPool.activeSku] = this.dataPool.sku.skuList;
    } else {
      const { initialSkuSeq } = this.dataPool;

      if (initialSkuSeq) {
        const filterSkuList = this.isPreview() ? this.dataPool.sku.skuList : this.dataPool.sku.skuList?.filter?.((item) => item.available);
        const validSkuList = allowNotAvailable ? this.dataPool.sku.skuList : filterSkuList;

        if (validSkuList) {
          const hitSku = validSkuList.find((item) => item.skuSeq === initialSkuSeq);

          if (hitSku) {
            this.dataPool.activeSku = hitSku;
            this.dataPool.currentSpecList = hitSku?.skuAttributeIds?.map((item) => `${item.id}:${item.valueId}`);
          }
        }
      }

      // 
      this.dataPool.attrArray.forEach((spec, index) => {
        if (spec.hidden) {
          this.dataPool.currentSpecList[index] = spec.specAttrList[0].id;
        }
      });
    }
  }

  clearRoot() {
    this.root.empty();
  }

  getActiveSku() {
    if (this.dataPool.skuType === 'single') {
      return this.dataPool.sku.skuList?.[0] || null;
    }

    if (this.dataPool.skuType === 'multi') {
      // 
      if (this.dataPool.currentSpecList.filter(Boolean).length === this.dataPool.attrArray.length) {
        return (
          getSku(this.dataPool.currentSpecList, this.dataPool.validSkuMap, this.dataPool.sku.skuList) ||
          getSku(this.dataPool.currentSpecList, this.dataPool.invalidSkuMap, this.dataPool.sku.skuList) ||
          null
        );
      }
    }

    return null;
  }

  clickAttr(specIndex, attrIndex) {
    const item = this.dataPool.attrArray[specIndex]?.specAttrList[attrIndex];
    this.dataPool.currentSpecList = this.dataPool.skuUtil.getActionSpecList(this.dataPool.currentSpecList, item, specIndex);

    const activeSku = this.getActiveSku();

    if (activeSku !== this.dataPool.activeSku) {
      this.dataPool.activeSku = activeSku;
    }

    try {
      this.onChange?.(activeSku);
    } catch (e) {
      setTimeout(() => {
        throw e;
      });
    }

    this.render();
  }

  //  
  getAttrValue(index) {
    return getAttrValue(this.dataPool.attrArray, this.dataPool.currentSpecList[index], index);
  }

  destory() {
    this.clearRoot();
    this.dataPool = null;
    this.root = null;
    this.onInit = null;
    this.onChange = null;
    this.onDestory?.();
    this.onDestory = null;
  }
}
