import constant from './internal/constant';

async function applyCoupon(svc, couponCode) {
  return svc.request.get(constant.endpointCouponCode`${couponCode}`);
}

async function withdrawCoupon(svc, couponCode) {
  return svc.request.delete(constant.endpointCoupon, {
    data: couponCode,
  });
}

export default {
  applyCoupon,
  withdrawCoupon,
};
