/* eslint-disable max-len */
import request from './request';
import createLogger from './createLogger';
import { SL_State as store } from './state-selector';
import { adaptor } from './url-adaptor';
import { reportCheckout, setIniiateCheckout } from './tradeReport';
import { hiidoEventStatus } from './tradeReport/const';
import { SL_EventBus } from './event-bus';

function isJsonParse(str) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}

/**  */
const logger = {
  checkout: createLogger('checkout'),
};

/**  */
const services = {
  /**
   *
   * @param {Array<{
              productSku: string, //sku
              productSeq: string, //
              productNum: number //
          }>} products
  * @param {{ associateCart: boolean, discountCode: string | null, bundledActivitySeq: string | null }} others
  * @returns Promise<{ seq: string; mark: string; }>
  */
  save: async (
    products,
    { associateCart = false, useMemberPoint = null, discountCode = null, bundledActivitySeq = null } = {},
  ) => {
    return request.post('/trade/center/order/abandoned/save', {
      products,
      associateCart,
      discountCode,
      bundledActivitySeq,
      useMemberPoint,
    });
  },
};

const RouterPath = {
  SignIn: '/user/signIn',
  Checkout: '/trade/checkout',
};

const ADD_TO_CART_EVENT_KEY = 'Symbol(ADD_TO_CART)';

//  URL
/**
 *
 * @param {string} seq 
 * @param {boolean} associateCart  
 * @returns string
 */
export const getCheckoutUrl = (seq, { query = {}, associateCart, abandonedOrderMark = '' } = {}) => {
  const urlPrefix = `${window.location.protocol}//${window.location.host}`;

  const { url } = adaptor(`${urlPrefix}${RouterPath.Checkout}/${seq}`, {
    query: {
      buyScence: associateCart ? 'cart' : 'detail',
      ...query,
      mark: abandonedOrderMark,
    },
    fullQuery: false,
  });

  return url;
};

/**
 *
 * @param {Array<{
            productSku: string, //sku
            productSeq: string, //
            productNum: number //
        }>} products
 * @param {{
    associateCart: boolean;
    discountCode: string | null;
    bundledActivitySeq: string | null;
    associatePayPal: boolean;
    query: object;
    abandonedOrderSeq: string | null;
    needReport: () => void;
    onBeforeJump: () => any;
  }} extra
 * @returns Promise
 */
const save = async (products, extra = {}) => {
  const { stage, query = {}, associateCart = false } = extra; // 
  try {
    const settleConfig = store.get('tradeSettleConfig');
    const isLogin = store.get('request.cookie.osudb_uid');
    const { onBeforeJump, report, needReport, abandonedOrderSeq, abandonedOrderMark } = extra;

    const needLogin = settleConfig?.loginType === 'ONLY_LOGIN';

    const { discountCode, ...rest } = extra;
    let _discountCode = discountCode;
    // sessionStoragediscountCode
    if (!associateCart) {
      const tradeExtraInfoStr = sessionStorage.getItem('tradeExtraInfo');
      const tradeExtraInfo = isJsonParse(tradeExtraInfoStr) ? JSON.parse(tradeExtraInfoStr) : {};
      _discountCode = tradeExtraInfo?.discountCode?.value;
    }
    const response = abandonedOrderSeq
      ? await Promise.resolve({ data: { seq: abandonedOrderSeq, mark: abandonedOrderMark } })
      : await services.save(products, { associateCart, discountCode: _discountCode, ...rest }); // evenid
    logger.checkout.info(response.data);
    const redirectToSignIn = !isLogin && needLogin;
    const checkoutUrl = getCheckoutUrl(response.data.seq, {
      query: {
        ...query,
        spb: redirectToSignIn ? null : query.spb,
      },
      abandonedOrderMark,
      associateCart,
    });
    SL_EventBus.emit('trade:goToCheckout:report', {
      data: {
        event_status: response?.data?.seq ? hiidoEventStatus.SUCCESS : hiidoEventStatus.ERROR,
        stage,
        isCart: associateCart,
        products,
        spb: query?.spb,
      },
    });

    // capi
    setIniiateCheckout(response.data.seq, needReport);

    const urlPrefix = `${window.location.protocol}//${window.location.host}`;
    /**
     *  ->  > 
     */
    if (redirectToSignIn) {
      const { url } = adaptor(`${urlPrefix}${RouterPath.SignIn}`, {
        query: {
          redirectUrl: checkoutUrl,
        },
        fullQuery: false,
      });

      // eslint-disable-next-line no-unused-expressions
      typeof onBeforeJump === 'function' && onBeforeJump();
      /**
       * TODO:  @Steven
       * * 1.  fb eventId
       * * 2. 
       * * 3.  fb addToCart 
       */
      try {
        reportCheckout({
          products,
          isCart: associateCart,
          report, // 
        });
        if (associateCart) {
          window.SL_EventBus?.emit('global:hdReport:exit', [
            '60006254',
            {
              event_name: '999',
              page_dest: `${window.location.href}`,
            },
          ]);
        }
      } catch (e) {
        console.log(e);
      }
      return Promise.resolve({ ...response.data, url, needLogin });
    }

    // eslint-disable-next-line no-unused-expressions
    typeof onBeforeJump === 'function' && onBeforeJump();

    /**
     * TODO:  @Steven
     * * 1.  fb eventId
     * * 2. 
     * * 3.  fb addToCart 
     */
    try {
      reportCheckout({
        products,
        isCart: associateCart,
        report,
        needReport,
      });
      if (associateCart && window.location?.pathname === '/cart') {
        window.SL_EventBus?.emit('global:hdReport:exit', [
          '60006254',
          {
            event_name: '999',
            page_dest: `${window.location.href}`,
          },
        ]);
      }
    } catch (e) {
      logger.checkout.info(e);
    }
    return Promise.resolve({ url: checkoutUrl, needLogin: false, abandonedInfo: response.data });
  } catch (error) {
    SL_EventBus.emit('trade:goToCheckout:report', {
      data: { event_status: 0, stage, isCart: associateCart, products, spb: query?.spb },
    });

    return Promise.reject(error);
  }
};

const jump = async (products, extra = {}) => {
  const { url } = await save(products, extra);
  window.location.href = url;
};

let hasBoundAddToCartEvent = false;
let addToCartEventName;

const getAddToCartEventName = () => {
  if (addToCartEventName) {
    return addToCartEventName;
  }
  const eventNameList = window.SL_EventBus.eventNames() || [];
  return eventNameList.find((name) => name.toString() === ADD_TO_CART_EVENT_KEY);
};

/**
 *
 * @param {{ spuId: string, skuId: string, num: number, price: string, success: function, error: function, complete: function, eventID: string }} data
 */
const addToCart = (data) => {
  return window.SL_EventBus.emit(getAddToCartEventName(), data);
};

if (!hasBoundAddToCartEvent) {
  hasBoundAddToCartEvent = true;
  window.__SL_TRADE_EVENT__ = window.__SL_TRADE_EVENT__ || {};
  window.__SL_TRADE_EVENT__.addToCart = window.__SL_TRADE_EVENT__.addToCart || addToCart;
}

export default {
  jump,
  save,
};
