/**
 * return {
 *  [`${nameId1}:${valueId1};${nameId2}:${valueId2}`]: skuObj
 * }
 */
export function getSkuComMap(skuList) {
  const validSkuMap = {}; // available
  const invalidSkuMap = {}; // !available

  if (!skuList) {
    return {
      validSkuMap,
      invalidSkuMap,
    };
  }

  skuList.forEach((sku /* , index */) => {
    if (sku.skuAttributeIds) {
      const skuKey = sku.skuAttributeIds
        .sort((a, b) => (a.attributeWeight || 0) - (b.attributeWeight || 0))
        .map((item) => `${item.id}:${item.valueId}`)
        .join(';');

      if (!sku.available) {
        invalidSkuMap[skuKey] = { ...sku };
      } else {
        validSkuMap[skuKey] = {
          price: sku.price,
          stock: sku.stock,
          skuSeq: sku.skuSeq,
          spuSeq: sku.spuSeq,
        };
      }
    }
  });

  return {
    validSkuMap,
    invalidSkuMap,
  };
}

// sku
export function getSku(selectSkuArr, skuList, sourceSkuList) {
  const skuKey = selectSkuArr.map((item) => item).join(';');
  if (!skuKey) return null;

  const hitSku = sourceSkuList.find((item) => item.skuSeq === skuList[skuKey]?.skuSeq);
  return hitSku || null;
}

// 
// "skuSpecList": [
//   {
//     "specName": "SIZE",
//     "specAttrList": [
//       { "name": "S", "id" : 100 },
//       { "name": "M", "id" : 101 }
//     ]
//   }
// ]
// 
export function transSkuSpecList(skuAttributeMap) {
  const resultArr = [];
  if (!skuAttributeMap) {
    return resultArr;
  }
  Object.entries(skuAttributeMap)
    .sort(([, a], [, b]) => (a.attributeWeight || 0) - (b.attributeWeight || 0))
    .forEach(([nameId, item]) => {
      const specAttrListResult = [];
      Object.entries(item.skuAttributeValueMap)
        .sort(([, a], [, b]) => (a.attributeValueWeight || 0) - (b.attributeValueWeight || 0))
        .forEach(([attrId, attr]) => {
          const id = `${nameId}:${attrId}`;
          const name = attr.defaultValue;
          const imgUrl = attr.imgUrl || '';
          specAttrListResult.push({ id, name, imgUrl });
        });
      const skuSpecObj = {
        hidden: item.hidden,
        nameId,
        specName: item.defaultName,
        specAttrList: specAttrListResult,
        onlyShowAttrImg: specAttrListResult.every((item) => item.imgUrl),
      };
      resultArr.push(skuSpecObj);
    });

  return resultArr;
}

/**
 * sku
 * @param specList sku
 * @param currentAttrId skuAttr
 * @return {
 *  id: `${nameId}:${attrId}`,
 *  name: 'xxx',
 *  imgUrl: 'xxx'
 * }
 */
export function getAttrValue(specList, currentAttrId, index) {
  if (!Array.isArray(specList)) return '';
  return specList[index]?.specAttrList?.find((item) => item.id === currentAttrId) || null;
}
