import apiLogger from '../../../utils/api-logger';
import * as externalEvent from '../../enum';
import * as interiorEvent from '../../interior-event';

const logger = apiLogger(externalEvent.ADD_TO_CART);

const interior = window?.SL_EventBus;
const external = window?.Shopline.event;

const addToCart = () =>
  external?.on(externalEvent.ADD_TO_CART, async (argument) => {
    const { data, onSuccess, onError } = argument;
    try {
      logger.info(`[emit]`, data);
      interior.emit(interiorEvent.ADD_TO_CART, { ...data, success: onSuccess, error: onError });
    } catch (error) {
      onError(error);
    }
  });

addToCart.apiName = externalEvent.ADD_TO_CART;
export default addToCart;
