import { verifyType, ErrorTypeEnum } from './constant';

function getSkuId(model) {
  if (!model) return '';

  return model.skuId || '';
}

function getGroupId(model) {
  if (!model) return '';

  return model.groupId || '';
}

function getStock(model) {
  if (!model) return 0;
  return model.stock || 0;
}

