import Cookies from 'js-cookie';
import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';
import geEnv from '../get-env';
import { SL_State } from '../state-selector';

dayjs.extend(utc);
dayjs.extend(timezone);
class HdReport {
  constructor() {
    const { APP_ENV } = geEnv();
    const env = APP_ENV !== 'develop' ? 'product' : '';
    const debugMode = APP_ENV === 'staging' || APP_ENV === 'develop';
    const Shopline = window.Shopline || {};
    const pid = window.__PRELOAD_STATE__?.serverEventId;
    const timeOffset = Shopline.systemTimestamp ? +new Date() - Shopline.systemTimestamp : 0;
    window.HdSdk?.shopTracker?.setOptions({
      env,
      timezoneOffset: SL_State.get('storeInfo.timezoneOffset') ?? 0,
      disableIframeId: true,
      timeOffset,
      beforeSend: async (data) => {
        const warpData = {
          theme_id: SL_State.get('themeConfig.themeId'),
          store_region: SL_State.get('storeInfo.marketStorageRegion'),
          store_timezone: SL_State.get('storeInfo.timezone'),
          user_timezone: dayjs.tz.guess(),
          theme_name: Shopline.themeName,
          theme_version: Shopline.themeVersion,
          is_admin: Cookies.get('r_b_ined') || '0',
          pid,
          update_mode: Shopline.updateMode?.toString() || '',
          time_offset: timeOffset,
          ...data,
        };
        if (!Object.prototype.hasOwnProperty.call(data, 'iframe_id') || Number(data.iframe_id) === 1) {
          warpData.iframe_id = Cookies.get('n_u') || Cookies.get('sl_iframe_id');
        }
        return warpData;
      },
    });
    window.HdSdk?.shopTracker?.setDebugMode(debugMode);
    // pid
    window.HdSdk?.shopTracker.use('url', (url, payload) => {
      const enhancedUrl = `${url}${url.indexOf('?') === -1 ? `?` : '&'}_pid=${pid}`;
      const payloads = [].concat(payload);
      // act= act_id_ act_id_
      // o.gif
      const defaultEventId = -999;
      const obj = payloads.reduce((o, { source }) => {
        const result = o;
        const { act, eventid, event_id } = source;
        const item = eventid || event_id || defaultEventId;
        result[act] = result[act] ? [...result[act], item] : [item];
        return result;
      }, {});

      const joinStr = Object.keys(obj)
        .reduce((str, act) => {
          return `${str}:${act}_${obj[act].join(',')}`;
        }, '')
        .slice(1);

      return `${enhancedUrl}&_act=${joinStr}`;
    });
  }
}

const hidooRp = window.SL_Report?.hdReportInstance || new HdReport();

if (!window.SL_Report?.hdReportInstance) {
  window.SL_Report = window.SL_Report || {};
  window.SL_Report.hdReportInstance = hidooRp;
}

export { hidooRp, HdReport };
