export const LOADING = 'loading';

// TODO:  style, 
// <svg viewBox="25 25 50 50" class="mp-loading__circular">
//  <circle cx="50" cy="50" r="20" fill="none"></circle>
// </svg>

export const getTemplate = (options, type = 'default') => {
  const loadingColor = options.loadingColor || 'black';
  const templates = {
    [LOADING]: `
      <div class="mp-toast mp-toast--loading mp-toast--loading-style2 mp-toast__hidden ${options.fullscreen && 'mp-toast__fullscreen'} ${options.className || ''}">
        <div class="mp-loading mp-loading--circular mp-toast__loading">
          <span class="mp-loading__spinner mp-loading__spinner--circular">
            <svg class="mp-loading__circular" width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M18.3333 9.99999C18.3333 14.6024 14.6024 18.3333 10 18.3333C5.39762 18.3333 1.66666 14.6024 1.66666 9.99999C1.66666 5.39762 5.39762 1.66666 10 1.66666" stroke="${loadingColor}" stroke-width="2.5" stroke-linecap="round"/>
            </svg>
          </span>
        </div>
        <div class="mp-toast__content mp-toast__text">${options.content}</div>
      </div>
    `,
    default: `
      <div class="mp-toast mp-toast__hidden ${options.fullscreen && 'mp-toast__fullscreen'} ${options.className || ''}">
        <div class="mp-toast__content mp-toast__inner">${options.content}</div>
      </div>
    `,
  };
  return templates[type];
};

const OPTION_TARGET = 'body';

const defaultOptions = {
  duration: 1500,
  content: '',
  target: OPTION_TARGET,
};

export const HIDDEN_CLASSNAME = 'mp-toast__hidden';
export const CONTENT_CLASSNAME = 'mp-toast__content';
export default class Toast {
  constructor(options = {}) {
    this.options = { ...defaultOptions, fullscreen: !options.target || options.target === OPTION_TARGET, ...options };

    this.$toast = null;
    this.$target = null;
    this.timer = null;
    this.instance = null;

    this.render();
  }

  static type = null;

  static init(options) {
    return this.getSingleton(options);
  }

  static loading(options) {
    return this.getSingleton(options, LOADING);
  }

  static getSingleton(options = {}, type) {
    let { instance } = this;
    if (!instance) {
      instance = new Toast(options);
      this.instance = instance;
    }

    if (instance.type !== type) {
      instance.type = type;

      // 
      if (instance.$toast) {
        instance.$toast.remove();
      }

      instance.render();
    }

    instance.open(options.content || '', options.duration);

    return instance;
  }

  render() {
    // todo: 
    const template = getTemplate(this.options, this.type || this.options.type);

    this.$toast = $(template);
    this.$target = $(this.options.target);

    const { $target } = this;

    if ($target.css('position') === 'static') {
      $target.css('position', 'relative');
    }

    $target.append(this.$toast);
  }

  open(content = '', duration) {
    const { options, $target } = this;

    if ($target.css('position') === 'static') {
      $target.css('position', 'relative');
    }

    // 
    if (this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
    }

    const { $toast } = this;
    const $text = $toast.find(`.${CONTENT_CLASSNAME}`);

    $text.html(content || this.options.content || '');
    $toast.removeClass(HIDDEN_CLASSNAME);

    const durationTime = typeof duration === 'number' ? duration : options.duration;

    if (durationTime !== 0) {
      this.timer = setTimeout(this.close.bind(this), durationTime);
    }
  }

  close() {
    if (this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
    }

    this.$toast.addClass(HIDDEN_CLASSNAME);

    if (typeof this.options.onClose === 'function') {
      this.options.onClose();
    }

    // TODO:  position  style  bug
    this.$target.css('position', '');
  }
}
