/**
 *  header 
 */
import Base from '../../shared/base/BaseClass';
import { pathGetInfo, groupArr } from './desktop-site-nav';

class DrawerMenu extends Base {
  config = {
    namespace: 'stage:headerMobileMenu',
  };

  classes = {
    activedClass: 'is-actived',
  };

  selectors = {
    nav: '.header-menu__mobile-nav',
    navBtn: '.mobile-nav__btn',
    navList: '.mobile-nav__list',
  };

  attrs = {
    navLevel: 'nav-level',
    navParent: 'nav-parent',
    nav: 'nav',
  };

  constructor() {
    super();

    this.$setNamespace(this.config.namespace);
    this.init();
    this.bindEvent();
  }

  init() {
    this.$nav = $(this.selectors.nav);

    this.setNavLevel(1);

    this.getNodeImg(1);
  }

  bindEvent() {
    this.$on('click', this.selectors.navBtn, (event) => {
      const $target = $(event.currentTarget);
      const targetNav = $target.data(this.attrs.nav);
      const isBack = targetNav == null;

      const getPerentList = (target) => target.parents(this.selectors.navList).first();

      if (isBack) {
        const $firstParentList = getPerentList($target);
        const $secondParentList = getPerentList($firstParentList);
        this.setNavLevel(this.navLevel - 1, $secondParentList.css('height'));
        $firstParentList.removeClass(this.classes.activedClass);
      } else {
        const $dropdown = $target.siblings(`${this.selectors.navList}[data-nav-parent="${targetNav}"]`);
        this.setNavLevel(Number($dropdown.data(this.attrs.navLevel)), $dropdown.css('height'));
        $dropdown.addClass(this.classes.activedClass);

        // 
        if (!$target.hasClass('has-img-info')) {
          $target.addClass('has-img-info');
          this.getNodeImg(-1, targetNav);
        }
      }
    });
  }

  off() {
    this.$offAll();
  }

  /**
   * 
   *
   * @param {Number} level 
   * @param {String} height 
   */
  setNavLevel(level, height) {
    this.navLevel = level;
    this.$nav.attr(`data-${this.attrs.navLevel}`, level);
    this.$nav.css('height', height || 'auto');
  }

  /**
   * 
   *
   * @param {Number} level 
   * @param {String} targetNavId id
   */
  async getNodeImg(level, targetNavId) {
    const main_menu_link_list = $('.header-menu__mobile-nav')?.data('main-menu-link-list') || '';
    const nodeTree = window.SL_State.get('navigations')[main_menu_link_list]?.nodeTree || [];
    if (level === 1) {
      this.getSortationItems(nodeTree);
    } else {
      const childNodes = this.deepSearch(nodeTree, targetNavId);
      if (childNodes.length) {
        this.getSortationItems(childNodes);
      }
    }
  }

  /**
   * item
   *
   * @param {String} sortationIdS 
   * @param {Array} childNodes 
   */
  async getSortationItems(childNodes) {
    const sortationIdS = [];
    // id
    childNodes.forEach((nodeItem) => {
      if (nodeItem.nodeType === 3) {
        sortationIdS.push(nodeItem.pageLink);
      }
    });
    const ids = Array.from(new Set(sortationIdS));
    if (ids.length === 0) return;

    // step2: item
    const reList = await pathGetInfo(groupArr(ids, 20));
    const dataList = [];
    reList.forEach((res) => {
      if (res.status === 200 && res?.data?.data && res?.data?.data?.length) {
        dataList.push(...res.data.data);
      }
    });

    if (dataList.length) {
      childNodes.forEach((nodeItem) => {
        const sortationItem = dataList.find((i) => String(i.sortationId) === String(nodeItem.pageLink));
        const imgUrl = sortationItem?.sortationCoverImg || sortationItem?.productList[0]?.image;
        if (imgUrl) {
          $(`.mobile-nav__image[data-nav-id="${nodeItem.id}"`)
            .addClass('mobile-nav__image-block')
            .css({ 'background-image': `url(${imgUrl})` });
        }
      });
    }
  }

  /**
   * id 
   *
   * @param {Object} nodeItem 
   * @param {String} targetNavId id
   * @return {Array} 
   */
  deepSearch(nodeItem, targetNavId) {
    let childNodes = [];
    for (let i = 0; i < nodeItem.length; i++) {
      if (String(nodeItem[i].id) === String(targetNavId)) {
        childNodes = nodeItem[i].childNodes;
        break;
      }
      if (nodeItem[i].childNodes.length) {
        childNodes = this.deepSearch(nodeItem[i].childNodes, targetNavId);
        if (childNodes.length) break;
      }
    }
    return childNodes;
  }
}

let instance = new DrawerMenu();

$(document).on('shopline:section:load', () => {
  instance.off();
  instance = new DrawerMenu();
});
