import { ThirdPartReport } from '../enum';

const external = window?.Shopline.event;
const interior = window?.SL_EventBus;

let initialized = false;

function onThirdPartReport(data) {
  interior.emit('global:thirdPartReport', {
    ...(data || {}),
  });
}

function on() {
  if (!external) {
    console.error(`failed to init ${ThirdPartReport} listener: window?.Shopline.event not found`);
    return;
  }

  if (!interior) {
    console.error(`failed to init ${ThirdPartReport} listener: window?.SL_EventBus not found`);
    return;
  }

  if (initialized) return;

  external.on(ThirdPartReport, onThirdPartReport);
  initialized = true;
}

export default {
  on,
};
