import { t } from '@yy/sl-theme-shared/utils/i18n';
import FlashSale from '@yy/sl-theme-shared/components/hbs/flashSale';
import SkuStepper from '../../commons/js/sku-stepper';

export const initValue = 1;
export default class SkuQuality {
  constructor({ sku, spu, activeSku, id, onChange }) {
    this.activeSku = activeSku;
    this.sku = sku;
    this.spu = spu;
    this.id = id;
    this.root = `#product-detail-sku-quantity_${id}`;
    this.init();
    this.onChange = onChange;
  }

  /**
   * @description: 
   */
  getMax() {
    // sku999
    if (!this.activeSku) {
      return 999;
    }
    if (this.isTrackStock()) {
      return this.activeSku?.stock > 0 ? Math.min(999, this.activeSku.stock) : 1;
    }
    return 999;
  }

  /**
   * @description: 
   */
  isTrackStock() {
    return !this.activeSku?.infiniteStock && !this.activeSku?.allowOversold;
  }

  init() {
    this.skuStepper = new SkuStepper({
      domReady: true,
      root: $(`#product-detail-sku-stepper_${this.id}`),
      max: this.getMax(),
      value: initValue,
      min: 1,
      disabled: this.spu?.soldOut || (this.isTrackStock() && this.activeSku && this.activeSku?.stock < 1),
      onChange: (num) => this.onChange(num),
    });

    new FlashSale({
      id: `${this.id}`,
    }).init();
  }

  render() {
    const showTips = this.isTrackStock() && this.activeSku && this.activeSku?.stock < 10 && this.activeSku?.stock > 0;
    if (showTips) {
      const content = t(`common.stock-limit`, { stock: this.activeSku.stock });
      this.setErrorTips(showTips, content);
    } else {
      this.setErrorTips(showTips);
    }
  }

  /**
   * @description: 
   * @param {*} show 
   * @param {*} content 
   */
  setErrorTips(show, content) {
    if (show) {
      $(this.root).children('.stepper-tip').html(content).removeClass('hide');
    } else {
      $(this.root).children('.stepper-tip').addClass('hide');
    }
  }

  setActiveSku(sku) {
    let current = this.skuStepper.data.value < 0 ? 1 : this.skuStepper.data.value;
    if (!sku) {
      this.activeSku = null;
      const stepperData = {
        value: current,
        max: 999,
        disabled: false,
      };
      this.skuStepper.setStepperData(stepperData);
      this.render();
      return;
    }
    this.activeSku = sku;
    if (current > this.getMax()) {
      current = this.getMax();
      // skutoast
      if (!sku.soldOut) {
        this.skuStepper.toast.open(t(`common.stock-limit`, { stock: current }), 1000);
      }
    }
    const stepperData = {
      value: current,
      max: this.getMax(),
      disabled: this.spu.soldOut || (this.isTrackStock() && this.activeSku && this.activeSku?.stock < 1),
    };
    this.skuStepper.setStepperData(stepperData);
    this.render();
  }
}
