import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';

import cartEventBus from '../../utils/event-bus';

import cartSvc from '../../domain/adapter/svc/cart';
import voucherSvc from '../../domain/adapter/svc/voucher';
import couponSvc from '../../domain/adapter/svc/coupon';
import orderSvc from '../../domain/adapter/svc/order';

import storageConstants from '../../domain/adapter/storage/constant';

import responseCode from '../../constant/responseCode';

import cartModel from '../../domain/model/cart';
import cartItemModel from '../../domain/model/cartItem';
import responseModel from '../../domain/model/response';
import promotionCodeModel from '../../domain/model/promotionCode';
import cartVerifyItemModel from '../../domain/model/cartVerifyItem';
import modelHelper from '../../domain/model/helpers';

import hooks from './hooks';

const CartEventBusEnum = {
  UPDATE: 'cart:update',
};

const CartInfoKey = 'cartInfo';

class CartService {
  constructor(svcAdapter, storageAdapter) {
    this._svc = svcAdapter;
    this._storage = storageAdapter;
    this._cartDetail = SL_State.get(CartInfoKey) || null;

    this._inactiveCartItemListMemo = modelHelper.memo();
    this._activeCartItemListMemo = modelHelper.memo();
    this._cartItemListMemo = modelHelper.memo();

    //  service 
    // miniCartmainCart  service 
    cartEventBus.on(CartEventBusEnum.UPDATE, (data) => {
      this._cartDetail = data;
    });
  }

  get inactiveCartItemList() {
    return this._inactiveCartItemListMemo((cartDetail) => {
      return modelHelper.reducer(cartDetail).next(cartModel.getInactiveCartItemList).next(cartItemModel.mergeNthProduct)();
    }, this.cartDetail);
  }

  get activeCartItemList() {
    return this._activeCartItemListMemo((cartDetail) => {
      return modelHelper.reducer(cartDetail).next(cartModel.getActiveCartItemList).next(cartItemModel.mergeNthProduct)();
    }, this.cartDetail);
  }

  get cartItemList() {
    return this._cartItemListMemo((cartDetail) => {
      return modelHelper.reducer(cartDetail).next(cartModel.getCartItemList).next(cartItemModel.mergeNthProduct)();
    }, this.cartDetail);
  }

  get cartDetail() {
    return this._cartDetail;
  }

  async getCartDetail() {
    const res = await cartSvc.getCart(this._svc);
    if (responseModel.isResolved(res)) {
      const { data } = res;
      this._cartDetail = data;
      SL_State.set(CartInfoKey, data);
      cartEventBus.emit(CartEventBusEnum.UPDATE, data);
    }

    return res;
  }

  async addSku(spuId, skuId, quantity, dataReportReq) {
    if (!spuId || !skuId || quantity < 0) {
      return responseModel.rejectWithCode(responseCode.FA_INVALID_PARAMS);
    }

    const res = await cartSvc.addCartItem(this._svc, spuId, skuId, quantity, dataReportReq);
    if (responseModel.isResolved(res)) {
      await this.getCartDetail();
    }

    return res;
  }

  async editSku(spuId, skuId, num, groupId, productSource) {
    if (!spuId || !skuId || num < 0) {
      return responseModel.rejectWithCode(responseCode.FA_INVALID_PARAMS);
    }
    const skuInfo = {
      spuId,
      skuId,
      num,
      groupId,
      productSource,
    };
    const res = await cartSvc.putCartItem(this._svc, skuInfo);
    if (responseModel.isResolved(res)) {
      await this.getCartDetail();
    }

    return res;
  }

  async removeSku(skuInfo) {
    if (skuInfo) {
      const res = await cartSvc.deleteCartItemList(this._svc, [skuInfo]);
      if (!responseModel.isResolved(res)) {
        return res;
      }
    }

    return this.getCartDetail();
  }

  async removeSkuList(skuInfoList) {
    if (Array.isArray(skuInfoList) && skuInfoList.length) {
      const res = await cartSvc.deleteCartItemList(this._svc, skuInfoList || []);
      if (responseModel.isResolved(res)) {
        await this.getCartDetail();
      }

      return res;
    }

    return responseModel.resolveWithData();
  }

  /**
   * itemList: {skuId, spuId, num, name, price}[]
   * */
  getCheckoutParams(itemList) {
    const discountCode = modelHelper.reducer(this.cartDetail).next(cartModel.getPromotionInfo).next(promotionCodeModel.getCode)();
    const useMemberPoint = this.cartDetail?.memberPointInfo?.use;
    let abandonSeq = null;
    let abandonMark = null;

    const rawAbandonInfoFromCache = this._storage.getItem(storageConstants.KEY_CART_ABANDON_INFO);
    if (rawAbandonInfoFromCache) {
      ({ mark: abandonMark, seq: abandonSeq } = JSON.parse(rawAbandonInfoFromCache));
    }

    return modelHelper
      .reducer({
        associateCart: true,
        useMemberPoint,
      })
      .next(discountCode ? orderSvc.withAbandonOrderDiscountCode : null, discountCode)
      .next(abandonSeq ? orderSvc.withAbandonOrderInfo : null, abandonSeq, abandonMark)
      .next(
        orderSvc.withAbandonOrderProductList,
        (itemList || []).map((item) => ({
          productSku: item.skuId,
          productSeq: item.spuId,
          productNum: item.num,
          productPrice: item.price,
          productName: item.name,
          groupId: item.groupId,
          productSource: item.productSource,
        })),
      )();
  }

  async toggleVoucher(used) {
    const res = await voucherSvc.toggleVoucher(this._svc, !!used);
    if (responseModel.isResolved(res)) {
      await this.getCartDetail();
    }

    return res;
  }

  async applyCoupon(couponCode) {
    if (!couponCode) {
      return responseModel.rejectWithCode(responseCode.FA_COUPON_INVALID_CODE);
    }

    const res = await couponSvc.applyCoupon(this._svc, couponCode);
    if (responseModel.isResolved(res)) {
      await this.getCartDetail();
    }

    return res;
  }

  async withdrawCoupon() {
    const couponCode = modelHelper.reducer(this._cartDetail).next(cartModel.getPromotionInfo).next(promotionCodeModel.getCode)();
    if (!couponCode) {
      return responseModel.rejectWithCode(responseCode.FA_COUPON_INVALID_CODE);
    }

    const res = await couponSvc.withdrawCoupon(this._svc, couponCode);
    if (responseModel.isResolved(res)) {
      await this.getCartDetail();
    }

    return res;
  }

  async verifyCartItemList(cartItemList) {
    if (!cartItemList) {
      cartItemList = this.cartItemList;
    }

    return this._verifyCartItemList(cartItemList);
  }

  /**
   * ICartItem[]
   * */
  async _verifyCartItemList(cartItemList) {
    if (!Array.isArray(cartItemList)) {
      return responseModel.rejectWithCode(responseCode.FA_INVALID_PARAMS);
    }

    if (cartItemList.length <= 0) {
      if ((await hooks.verifyingActiveProductEmpty.callPromise()) !== false) {
        return responseModel.rejectWithCode(responseCode.FA_PRODUCT_ACTIVE_EMPTY);
      }
    }

    const checkRes = await cartSvc.verifyCartItemList(
      this._svc,
      cartItemList
        .map((item) => {
          return {
            spuId: cartItemModel.getSpuId(item),
            skuId: cartItemModel.getSkuId(item),
            num: cartItemModel.getQuantity(item),
            groupId: cartItemModel.getGroupId(item),
            productSource: cartItemModel.getProductSource(item),
          };
        })
        .filter((i) => !!i.spuId && !!i.skuId),
    );

    if (!responseModel.isResolved(checkRes)) {
      return checkRes;
    }

    return responseModel.resolveWithData(cartVerifyItemModel.makeVerifyList(cartItemList, responseModel.getData(checkRes).checkItemList));
  }

  async getMemberPoint(use) {
    const res = await cartSvc.memberPoint(this._svc, use);
    return res;
  }
}

export default {
  CartService,
  CartEventBusEnum,
  cartEventBus,
};
