import request from '../../../../utils/request';
import apiLogger from '../../../utils/api-logger';

/**
 * 
 */

const services = {
  /**
   * @return Promise
   */
  getCartOwnerId: async () => request.get('/carts/cart/owner-id'),
};

/**
 * ownerIdsessionId
 * 
 * @param data {any}
 */
const getCartOwnerId = async (data) => services.getCartOwnerId(data);

/**
 * ownerIdsessionId
 * 
 * @param {
 *  data: any;
 *  onSuccess?: ({ data: string; code: string; success: boolean }) => void;
 *  onError?: (typeof Error) => void;
 * }
 */

const EVENT_NAME = 'Cart::GetCartId';

const logger = apiLogger(EVENT_NAME);

const interior = window?.Shopline.event;

const getCartId = () =>
  interior?.on(EVENT_NAME, async (argument) => {
    const { data, onSuccess = () => {}, onError = () => {} } = argument;
    try {
      const result = await getCartOwnerId(data);
      logger.info(result);
      onSuccess?.(result);
    } catch (error) {
      logger.error(error);
      onError?.(error);
    }
  });

getCartId.apiName = EVENT_NAME;
export default getCartId;
