/**
 * locale-currency.js
 */
import Base from '../../shared/base/BaseClass';
import TopDrawer from '../../shared/components/topDrawer';

class DrawerMenu extends Base {
  config = {
    namespace: 'stage:mobileLocaleCurrencyDrawer',
  };

  classes = {};

  selector = {
    drawer: '.stage-top-drawer.top-drawer--open',
    localeCurrencyContainer: '.drawer-menu-locale-currency',
    currencyBtn: '.j-currency-drawer-btn',
    localeBtn: '.j-locale-drawer-btn',
  };

  drawers = {
    localeDrawer: null,
    currencyDrawer: null,
  };

  constructor() {
    super();
    this.$setNamespace(this.config.namespace);
    this.init();
    this.bindEvent();
    this.bindLocaleCurrencyChange();
  }

  init() {
    this.drawers.currencyDrawer = new TopDrawer('global-currency-drawer', { fullScreen: true });
    this.drawers.localeDrawer = new TopDrawer('global-locale-drawer', { fullScreen: true });
  }

  bindEvent() {
    this.$on('click', this.selector.localeBtn, () => {
      this.drawers.localeDrawer.open();
    });

    this.$on('click', this.selector.currencyBtn, (e) => {
      const $btn = $(e.currentTarget);
      this.setupCurrencyList($btn);
      this.drawers.currencyDrawer.open();
    });
  }

  setupCurrencyList($btn) {
    const hasSymbol = $btn.find('.currency-symbol').hasClass('show');
    const $drawerCurrencyList = $('.drawer-menu-locale-currency[data-currency] .currency-list');
    if (hasSymbol) {
      $drawerCurrencyList.addClass('show-symbol');
    } else {
      $drawerCurrencyList.removeClass('show-symbol');
    }
  }

  bindLocaleCurrencyChange() {
    // locale-currency#bindLanguageChange
    this.$on('click', `${this.selector.localeCurrencyContainer} li`, (e) => {
      const $target = $(e.currentTarget);
      const drawerId = $target.parents(this.selector.drawer).eq(0).attr('id');

      Object.keys(this.drawers).forEach((key) => {
        const drawer = this.drawers[key];
        if (drawer.id === drawerId) {
          drawer.close();
        }
      });
    });
  }

  off() {
    this.$offAll();
  }
}

let instance = new DrawerMenu();

$(document).on('shopline:section:load', () => {
  instance.off();
  instance = new DrawerMenu();
});
