const presetConfig = {
  ai: null,
  t(key) {
    return key;
  },
  lang: 'zh-hans-cn',
};

function getLangConfig(config) {
  return config?.lang || presetConfig.lang;
}

function getRequest(config) {
  return config.ai;
}

export default {
  getLangConfig,
  getRequest,
};

