import Cookies from 'js-cookie';
import { PageType, ClickType } from '../report/const';

class GoogleAnalysis {
  constructor(config) {
    this.config = config;
  }
  /* eslint-disable */
  sendEventLog(eventType, data) {
    const params = { ...data };
    if (params && !params.currency) {
      params.currency = Cookies.get('currency_code');
    }
    return ['event', eventType, params];
  }

  clickForEnhancedEcom(page, clickType, params) {
    let event;
    let value;
    let res = [];
    switch (clickType) {
      case PageType.CheckoutProgress:
      case PageType.PlaceOrder:
        event = 'set_checkout_option';

        value = {
          value: params.amount,
          checkout_step: params.step,
        };
        break;
      default:
        return res;
    }
    res = this.sendEventLog(event, value);
    return res;
  }

  load(page, params) {
    let res = [];
    res.push(
      this.sendEventLog('page_view', {
        page_title: document.title,
        page_location: window.location.href,
        page_path: window.location.pathname,
        ...params,
      }),
    );

    if (page === PageType.OrderConfirm) {
      res = [
        this.sendEventLog('purchase', {
          value: params.amount,
          currency: params.currency,
          shipping: params.shippingAmount,
          tax: params.taxAmount,
          transaction_id: params.orderId,
          items: params.items?.map((value) => {
            const { skuId, price, name, variant, quantity } = value;
            return {
              id: skuId,
              name,
              price,
              variant,
              quantity,
            };
          }),
        }),
      ];
    }

    return res;
  }

  click(page, type, params) {
    let value;
    let event;
    const res = [];

    switch (type) {
      case ClickType.AddToCart:
        event = 'add_to_cart';
        value = {
          items: [
            {
              id: params.skuId,
              name: params.name,
              price: params.price,
            },
          ],
        };
        break;
      case ClickType.RemoveFromCart:
        event = 'remove_from_cart';
        value = {
          items: [
            {
              id: params.skuId,
              name: params.name,
              price: params.price,
              quantity: params.quantity,
              variant: params.variant || '',
            },
          ],
        };

        if (Array.isArray(params.extraItems)) {
          params.extraItems.forEach((item) => {
            value.items.push({
              id: item.skuId,
              name: item.name,
              price: item.price,
              quantity: item.quantity,
              variant: item.variant || '',
            });
          });
        }
        break;
      case ClickType.PlaceOrder:
        event = 'place_order';
        break;
      case ClickType.CheckoutProgress:
        event = 'checkout_progress';
        break;
      case ClickType.BeginCheckout:
        event = 'begin_checkout';
        value = {
          value: params.amount,
        };
        if (this.config.enableEnhancedEcom) {
          value.currency = params.currency ?? Cookies.get('currency_code');
          value.items = params.items?.map((val) => {
            const { skuId, price, name, variant, quantity } = val;
            return {
              id: skuId,
              name,
              price,
              variant,
              quantity,
            };
          });
        }
        break;
      default:
        return [];
    }
    res.push(this.sendEventLog(event, value));
    if (this.config.enableEnhancedEcom) {
      res.push(this.clickForEnhancedEcom(page, type, params));
    }
    return res;
  }
}

const ga = new GoogleAnalysis({});
export default ga;
