// import verifyType from '../../constant/verifyType';
import { verifyType } from '@yy/sl-theme-shared/biz-com/trade/optimize-modal/constant';

function noError(vo) {
  return !vo || !vo.verifyType || vo.verifyType === verifyType.NIL;
}

function hasError(vo) {
  return !noError(vo);
}

function isUnderStock(vo) {
  return vo?.verifyType === verifyType.UNDER_STOCK;
}

export default {
  noError,
  hasError,
  isUnderStock,
};

