import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import isMobile from '../../../../shared/utils/isMobile';
import throttle from '../../../../shared/utils/throttle';
// import debounce from '../../../../shared/utils/debounce';
import { setFixedContentStyle, setStickyContAnimate, listenElementMutation, listenElementIntersection } from './helper';

const tradeSettleConfig = SL_State.get('tradeSettleConfig');
export const hasSecuritySection = tradeSettleConfig?.paymentSecurity?.displayPosition?.includes('CART') || tradeSettleConfig?.afterSaleGuarantee?.displayPosition?.includes('CART');

let hasInitialized = false;
const TOTAL_MARGIN_OF_EMPTY_WRAPPER = 155 * 2;

export const initMainCartSticky = () => {
  if (isMobile()) {
    $(window).on(
      'scroll',
      throttle(20, () => {
        setStickyContAnimate({
          viewportSelector: '.trade_cart .main_cart',
          containerSelector: '.cart__stick_container',
        });
      }),
    );

    listenElementMutation($('.trade-cart-sku-list').get(0), () => {
      setTimeout(() => {
        setStickyContAnimate({
          viewportSelector: '.trade_cart .main_cart',
          containerSelector: '.cart__stick_container',
        });
      }, 300);
    });

    setTimeout(() => {
      setStickyContAnimate({
        viewportSelector: '.trade_cart .main_cart',
        containerSelector: '.cart__stick_container',
      });
    }, 300);
  }
};

let intersectionObserver;

// 
let totalHeightOfStickyMarketingPlugins = null;

// 
const calculateMarketingPluginsOffsetHeight = () => {
  if (totalHeightOfStickyMarketingPlugins !== null) return;
  totalHeightOfStickyMarketingPlugins = 0;
  let tmp = $('#shopline-section-header');
  while (tmp && tmp.prev) {
    tmp = tmp.prev();
    if (!tmp.length) break;
    const { position } = window.getComputedStyle(tmp[0]);
    if (['fixed', 'sticky'].includes(position)) {
      totalHeightOfStickyMarketingPlugins += tmp.height();
    }
  }
};

// 
const getMaxHeightOfMiniCartWhenHeaderNotFixed = () => {
  let maxHeight;
  const vh = document.documentElement.clientHeight;
  const pluginsHeight = $('#shopline-section-header').position().top || 0;
  const headerMainHeight = $('.header__main').height();
  const headerMainTop = $('.header__main').position().top;
  const headerTopHeight = $('.header__top').height();
  const miniCartSlotTop = $('.mini-cart__drawer-slot').position().top;
  const stageHeaderTop = $('#stage-header').position().top;
  if (isMobile()) {
    maxHeight = vh - (pluginsHeight + headerMainHeight + headerMainTop + headerTopHeight);
  } else {
    maxHeight = vh - (pluginsHeight + miniCartSlotTop + stageHeaderTop + headerTopHeight);
  }
  return maxHeight;
};

// 
const isPluginsCovered = () => {
  return ['0', '0px'].includes($('#stage-header').css('top')) && $('#stage-header').hasClass('header__main--sticky');
};

/**
 *
 * 
 *
 * const pluginsHeight = $('#shopline-section-header').position().top;
 * const headerMainHeight = $('.header__main').height();
 * const headerMainTop = $('.header__main').position().top;
 * const stageHeaderHeight = $('#stage-header').height();
 * const headerTopHeight = $('.header__top').height();
 * const miniCartSlotTop = $('.mini-cart__drawer-slot').position().top
 * const stageHeaderTop = $('#stage-header').position().top
 *  totalHeightOfStickyMarketingPlugins
 *  clientHeight
 *
 *  & 
 * 1.
 *  maxHeight = clientHeight-(stageHeaderHeight + totalHeightOfStickyMarketingPlugins)
 * 2.
 *  2.1 
 *    2.1.1 mb maxHeight = clientHeight-(pluginsHeight+headerMainHeight+headerMainTop+headerTopHeight)
 *    2.1.2 pc maxHeight = clientHeight-(pluginsHeight+miniCartSlotTop+stageHeaderTop+headerTopHeight)
 *  2.2 
 *    2.2.1 pcmaxHeight = clientHeight-totalHeightOfStickyMarketingPlugins
 *    2.2.2 mbmaxHeight = clientHeight-(totalHeightOfStickyMarketingPlugins+68)
 */
const updateMaxHeightOfMiniCart = (isInit, stickyActive, stageHeaderHeight) => {
  calculateMarketingPluginsOffsetHeight();

  const vh = document.documentElement.clientHeight;
  let maxHeight;
  let isHeaderFixed = stickyActive;
  if (isInit || !stageHeaderHeight) {
    const $header = $('#stage-header');
    if ($header.hasClass('header__main--sticky')) {
      isHeaderFixed = true;
      stageHeaderHeight = $header.height();
    } else {
      isHeaderFixed = false;
    }
  }
  // 
  if (isHeaderFixed) {
    maxHeight = vh - (stageHeaderHeight + (isPluginsCovered() ? 0 : totalHeightOfStickyMarketingPlugins));
  } else {
    maxHeight = getMaxHeightOfMiniCartWhenHeaderNotFixed();
  }
  $('#cart-select .trade_cart_not_empty_wrapper').css('max-height', `${maxHeight}px`);
  $('#cart-select .trade_cart_empty_wrapper').css('max-height', `${maxHeight - TOTAL_MARGIN_OF_EMPTY_WRAPPER}px`);
  if (!isInit && hasSecuritySection) {
    setStickyContAnimate({
      viewportSelector: '.miniCart_vp_container',
      containerSelector: '.miniCart__stick_container',
    });
  }
};

export const updateSkuModuleStyle = (hideCloseBtn) => {
  const firstSkuItem = $('.trade-cart-sku-item').first();
  const parent = firstSkuItem.parent();
  if (parent?.hasClass('has-promotion')) {
    parent.css('padding-top', hideCloseBtn ? '30px' : 0);
  } else {
    firstSkuItem.css('padding-top', hideCloseBtn ? '20px' : 0);
  }
};

const toggleMiniCartCloseBtn = (isHeaderVisible) => {
  const miniCartCloseWrapper = $('.trade_mini_cart--close-btn');
  if (isHeaderVisible) {
    miniCartCloseWrapper.addClass('hide');
  } else {
    miniCartCloseWrapper.removeClass('hide');
  }
  updateSkuModuleStyle(isHeaderVisible);
};

const handleHeaderVisibleToggle = (isVisible) => {
  calculateMarketingPluginsOffsetHeight();
  const vh = document.documentElement.clientHeight;
  // 
  if (!isVisible) {
    $('.mini-cart__drawer-slot').css('position', 'fixed').css('top', totalHeightOfStickyMarketingPlugins);
    let extraHeight = 0;
    extraHeight += isMobile() ? 68 : 0;
    extraHeight += isPluginsCovered() ? 0 : totalHeightOfStickyMarketingPlugins;
    $('#cart-select .trade_cart_not_empty_wrapper').css('max-height', `${vh - extraHeight}px`);
    $('#cart-select .trade_cart_empty_wrapper').css('max-height', `${vh - extraHeight - TOTAL_MARGIN_OF_EMPTY_WRAPPER}px`);
  } else {
    // 
    $('.mini-cart__drawer-slot').css('position', 'absolute').css('top', '');
    const maxHeight = getMaxHeightOfMiniCartWhenHeaderNotFixed();
    $('#cart-select .trade_cart_not_empty_wrapper').css('max-height', `${maxHeight}px`);
    $('#cart-select .trade_cart_empty_wrapper').css('max-height', `${maxHeight - TOTAL_MARGIN_OF_EMPTY_WRAPPER}px`);
  }
  if (isMobile()) {
    // 
    toggleMiniCartCloseBtn(isVisible);
  }
};

const listenHeaderIntersection = () => {
  const isHeaderSticky = $('#stage-header').attr('data-sticky');
  // 
  if (isHeaderSticky !== 'true') {
    intersectionObserver = listenElementIntersection($('.header__layout-background').get(0), handleHeaderVisibleToggle);
    calculateMarketingPluginsOffsetHeight();
  }
  updateMaxHeightOfMiniCart(true);
};

//  BUG20-7387
const listenEditorSectionChange = () => {
  $(document).on('shopline:section:load', () => {
    totalHeightOfStickyMarketingPlugins = null;
    intersectionObserver?.disconnect();
    const observer = listenElementMutation($('#trade_mini_cart_ajax').get(0), () => {
      listenHeaderIntersection();
      observer.disconnect();
    });
    listenHeaderIntersection();
  });
};

const setMiniCartPosition = () => {
  const { clientWidth } = document.documentElement;
  const { right } = document.getElementById('stage-header-cart')?.getBoundingClientRect();
  const ele = $('.mini-cart__drawer-slot');
  const paddingRight = +ele.css('paddingRight')?.split('px')?.[0];
  ele?.css({ right: `${clientWidth - right - paddingRight}px` });
};

export const initMiniCartSticky = () => {
  if (!isMobile()) {
    setMiniCartPosition();
  }
  setTimeout(() => {
    listenHeaderIntersection();
    listenEditorSectionChange();
  }, 0);

  // ,scroll
  if (hasSecuritySection) {
    $('#cart-select .trade_cart_not_empty_wrapper').on(
      'scroll',
      throttle(20, () => {
        setStickyContAnimate({
          viewportSelector: '.miniCart_vp_container',
          containerSelector: '.miniCart__stick_container',
        });
      }),
    );

    listenElementMutation($('.trade-cart-sku-list').get(0), () => {
      setTimeout(() => {
        setStickyContAnimate({
          viewportSelector: '.miniCart_vp_container',
          containerSelector: '.miniCart__stick_container',
        });
      }, 300);
    });
  }
  hasInitialized = true;
};

export const initMiniStyleWhenOpen = () => {
  // 
  if (hasSecuritySection) {
    // fixed height
    // setStickyContainerHeight('.miniCart_fixed_block');
  } else if (SL_State.get('cartInfo')?.count > 0) {
    // 
    setFixedContentStyle('#cart-select .trade_cart_not_empty_wrapper', $('#cart-select .miniCart__stick_container_fixed').outerHeight() + 48);
  }
  // setFixedContentStyle('#cart-select .trade_cart_not_empty_wrapper', 300);

  if (!$('.trade_mini_cart--close-btn').hasClass('hide')) {
    updateSkuModuleStyle(false);
  }
};

export const toggleVisibility = (cartType, visibility) => {
  if (!isMobile()) return;
  const selector = cartType === 'main' ? '.cart__stick_container' : '.miniCart__stick_container';
  const isOpen = $(selector).attr('isOpen');
  if (!isOpen) return;
  const visible = typeof visibility === 'boolean' ? visibility : $(selector).css('display') !== 'block';
  $(selector)[visible ? 'show' : 'hide']();
};

window.SL_EventBus.on('stage:header:sticky', ({ stickyActive, headerHeight }) => {
  if (hasInitialized) {
    setTimeout(() => {
      updateMaxHeightOfMiniCart(false, stickyActive, headerHeight);
    }, 100);
  }
});

// top
window.SL_EventBus.on('stage:header:stickOffsetTopUpdate', ({ stickyActive }) => {
  if (hasInitialized) {
    setTimeout(() => {
      updateMaxHeightOfMiniCart(false, stickyActive, $('#stage-header').height());
    }, 100);
  }
});
