import { onDomReady } from './utils';

export function autoReport() {
  const shopTracker = window.HdSdk?.shopTracker;

  if (!shopTracker) return;

  onDomReady(() => {
    /** *******  -  ******** */
    // 142
    shopTracker.report('86000101', {
      event_name: '142',
    });
  });
}
