import apiLogger from '../../../utils/api-logger';
import { UPDATE_CHECKOUT_DETAIL } from '../../enum';
import { INTERIOR_TRADE_UPDATE_DETAIL } from '../../interior-event';

const logger = apiLogger(UPDATE_CHECKOUT_DETAIL);
const interior = window?.SL_EventBus;
const external = window?.Shopline.event;

/**
 * checkout detail
 */
const updateCheckoutDetailDebounceHandle = () => {
  let requesting = false;
  /**  */
  let emitDataList = [];
  /**  */
  let tempEmitDataList = [];

  /**  */
  function reset() {
    requesting = false;
    emitDataList = [...tempEmitDataList];
    tempEmitDataList = [];
    if (emitDataList.length) {
      emitFunc();
    }
  }
  /** callback */
  function successFunc(res) {
    emitDataList.map((cb) => cb?.onSuccess?.(res));
    reset();
  }
  /** callback */
  function errorFunc(e) {
    emitDataList.map((cb) => cb?.onError?.(e));
    reset();
  }
  /** checkoutdetail */
  function emitFunc() {
    try {
      interior.emit(INTERIOR_TRADE_UPDATE_DETAIL, {
        onSuccess: successFunc,
        onError: errorFunc,
      });
    } catch (e) {
      errorFunc(e);
    }
  }

  return function fn({ data, onSuccess, onError } = {}) {
    logger.info('[emit]', JSON.stringify(data));
    if (requesting) {
      /**  */
      tempEmitDataList.push({ onSuccess, onError });
    } else {
      requesting = true;
      emitDataList.push({ onSuccess, onError });
      emitFunc();
    }
  };
};

const updateCheckoutDetail = () => external?.on(UPDATE_CHECKOUT_DETAIL, updateCheckoutDetailDebounceHandle());

updateCheckoutDetail.apiName = UPDATE_CHECKOUT_DETAIL;
export default updateCheckoutDetail;
