import constant from './internal/constant';
import modelHelper from '../../model/helpers';

async function saveAbandonOrder(svc, config) {
  return svc.request.post(constant.endpointOrderSaveAbandonOrder, {
    associateCart: true,
    abandonedOrderSeqInfo: config.abandonedOrderSeqInfo || null,
    discountCode: config.discountCode || null,
    products: config.products || [],
  });
}

function withAbandonOrderInfo(config, seq, mark) {
  if (!seq) return config;
  return modelHelper.merge(config, { abandonedOrderSeqInfo: { seq, mark } });
}

function withAbandonOrderDiscountCode(config, discountCode) {
  if (!discountCode) return config;
  return modelHelper.merge(config, { discountCode });
}

function withAbandonOrderProductList(config, productList) {
  if (!productList) return config;
  return modelHelper.merge(config, { products: productList });
}

export default {
  saveAbandonOrder,
  withAbandonOrderInfo,
  withAbandonOrderDiscountCode,
  withAbandonOrderProductList,
};

