import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import { t } from '@yy/sl-theme-shared/utils/i18n';
import { setAddtoCart } from '@yy/sl-theme-shared/utils/tradeReport/tradeReport';
// import { DRAWER_EVENT_NAME } from '../components/drawer';
import { OPEN_MINI_CART, ADD_TO_CART } from '@yy/sl-theme-shared/events/trade/interior-event';
import CartSidebarRender from '@yy/sl-theme-shared/events/trade/developer-api/cart-sidebar-render';
import { DRAWER_EVENT_NAME, DRAWER_OPERATORS } from '../components/topDrawer';
import { listenCartReport } from '@yy/sl-theme-shared/utils/tradeReport/eventListen';
import { initMiniStyleWhenOpen, hasSecuritySection } from '../../cart/script/biz/sticky-cart';
import { setStickyContAnimate } from '../../cart/script/biz/sticky-cart/helper';
import Service from '../../cart/script/service';
import CartService from '../../cart/script/service/cart';
import dynamicImportMiniCart from '../utils/dynamicImportMiniCart';
import responseCodeVO from '../../cart/script/domain/vo/responseCode';
import Toast from '../components/toast';

/**  */
export const CLOSE_MINI_CART = Symbol('CLOSE_MINI_CART');
export { OPEN_MINI_CART, ADD_TO_CART };
const cartOpenType = SL_State.get('theme.settings.cart_open_type');

Service.init();

if (window.location.pathname.includes('/cart')) {
  dynamicImportMiniCart();
} else if (cartOpenType !== 'newpage') {
  setTimeout(dynamicImportMiniCart, 6000);
}

const interior = window.SL_EventBus;
const noop = () => {};
// 
listenCartReport();
// mini
interior.on(OPEN_MINI_CART, async ({ data = {}, onSuccess = noop } = {}) => {
  const cartId = 'cart-select';
  const { needToFetch = true } = data;
  if (cartOpenType === 'newpage') {
    onSuccess?.();
    window.location.href = '/cart';
  } else {
    await dynamicImportMiniCart();

    if (needToFetch) {
      await CartService.takeCartService().getCartDetail();
    }
    interior.emit(DRAWER_EVENT_NAME, { id: cartId, operator: DRAWER_OPERATORS.OPEN, onOpen: onSuccess });
    initMiniStyleWhenOpen();
    CartSidebarRender({ data: { dom: { id: cartId } } });
    setTimeout(() => {
      if (hasSecuritySection) {
        setStickyContAnimate({
          viewportSelector: '.miniCart_vp_container',
          containerSelector: '.miniCart__stick_container',
        });
      }
    }, 300);
  }
});

// mini
window.SL_EventBus.on(CLOSE_MINI_CART, () => {
  window.SL_EventBus.emit(DRAWER_EVENT_NAME, { id: 'cart-select', operator: DRAWER_OPERATORS.CLOSE });
});

// 
window.SL_EventBus.on(ADD_TO_CART, async ({ spuId, skuId, num, price, success, error, complete, eventID }) => {
  try {
    const dataReportReq = setAddtoCart(price, window?.SL_State.get('storeInfo.currency'), eventID);
    const res = await CartService.takeCartService().addSku(spuId, skuId, num, dataReportReq);

    if (!responseCodeVO.isOk(res)) {
      let errMsg = res.msg;

      if (responseCodeVO.is(res, 'TCAT0109')) {
        errMsg = t('modal.exceedCartTitle');
      }

      if (responseCodeVO.batchIs(res, 'TCAT0107,TCAT0112')) {
        errMsg = t('cart.active-stock-limit-2', { stock: res.data.maxPurchaseTotalNum > 0 ? res.data.maxPurchaseTotalNum : '0' });
      }

      if (responseCodeVO.is(res, 'TCAT0111')) {
        errMsg = t('cart.active-stock-limit-1', { stock: res.data.maxPurchaseTotalNum > 0 ? res.data.maxPurchaseTotalNum : '0' });
      }

      new Toast().open(errMsg, 1500);
      if (typeof error === 'function') {
        error();
      }
    } else {
      window.SL_EventBus.emit(OPEN_MINI_CART, { data: { needToFetch: false } });
      if (typeof success === 'function') {
        success();
      }
    }
  } catch (e) {
    console.warn('add to cart fail:', e);
  } finally {
    if (typeof complete === 'function') {
      complete();
    }
  }
});
