/* eslint-disable */
import productSkuChange from '@yy/sl-theme-shared/events/product/sku-change';
import productSkuChanged from '@yy/sl-theme-shared/events/product/sku-changed';
import dataReportViewContent from '@yy/sl-theme-shared/events/data-report/view-content';
import currency from '@yy/sl-theme-shared/utils/currency';
import SkuQuality from './product-quantity';
import ButtonEvent, { getVariant } from './product-button';
import setProductPrice from '../../commons/js/product-info';
import ProductImages from './product-swiper';
import setPosition, { listenPosition } from './layout';
import initSku from './sku-trade';
import InquiryPriceModal from '../inquiry-price-modal';
import createShadowDom from '../../commons/js/createShadowDom';

import ProductCollapse from './product-collapse';
import promotionTagsInit from '../../../shared/sales/snippets/salesPromotionTags';
import FlashSale from '../../../shared/sales/snippets/flashSale/index';

import AddToCartList from '../../../shared/sales/snippets/addToCartList';
import '../../../shared/sales/styles/snippets/add-to-cart.scss';
import '../../../shared/sales/styles/snippets/flashSale/index.scss';

import '../style/product-preview.scss';
import '@yy/sl-theme-shared/utils/productDetailDataBus.js';
import '@yy/sl-theme-shared/components/hbs/thirdPartyShare/js/index.js';

// 
import initDiscountCoupon from '../../sales/discount-coupon/js';

const { formatCurrency } = currency;
const emitProductSkuChange = (data) => {
  try {
    productSkuChange({ ...data, currency: window?.SL_State?.get('storeInfo.currency') });
  } catch (e) {
    console.error(e);
  }
};
const emitProductSkuChanged = (data) => {
  try {
    productSkuChanged({ ...data, currency: window?.SL_State?.get('storeInfo.currency') });
  } catch (e) {
    console.error(e);
  }
};
const emitViewContent = (data) => {
  try {
    dataReportViewContent(data);
  } catch (e) {
    console.error(e);
  }
};

function thirdPartReport({ activeSku, spu, sku }) {
  const newActiveSku = activeSku || sku?.skuList?.[0];
  window.SL_EventBus.emit('global:thirdPartReport', {
    GA: [
      [
        'event',
        'view_item',
        {
          items: [
            {
              id: newActiveSku?.skuSeq,
              name: spu?.title,
              price: formatCurrency(newActiveSku?.price),
              variant: getVariant(newActiveSku?.skuAttributeIds, sku?.skuAttributeMap),
            },
          ],
        },
      ],
    ],
    GAR: [
      [
        'event',
        'view_item',
        {
          value: formatCurrency(newActiveSku?.price),
          items: [{ id: newActiveSku.skuSeq, google_business_vertical: 'retail' }],
        },
      ],
    ],
    GARemarketing: [
      [
        'event',
        'view_item',
        {
          ecomm_prodid: newActiveSku.skuSeq,
          ecomm_pagetype: 'product',
          ecomm_category: spu?.sortationList?.[0]?.sortationId,
          ecomm_pcat: spu?.sortationList?.[0]?.sortationName,
          ecomm_totalvalue: formatCurrency(newActiveSku?.price),
        },
      ],
    ],
  });
}

/**
 *
 * @param {*} id sectionsection
 * 
 *  id=productDetail
 *  id=section.id
 *  id=spuSeq
 * @returns
 */
export default function initPreview({ id, statePath, filterShelves, offsetTop, container, onAddSuccess }) {
  const sku = window.SL_State.get(`${statePath}.sku`);
  const spu = window.SL_State.get(`${statePath}.spu`);

  if (filterShelves && !spu?.shelves) {
    console.error('no spu data or not shelves, init break');
    return () => undefined;
  }

  if (id === 'productDetail') {
    window.productDetailDataBus.set('spu', spu);
  }

  window.lozadObserver.observe();
  const removePositionListener = listenPosition({
    id,
    offsetTop,
    container,
  });
  createShadowDom();

  let productImagesInstance;
  try {
    productImagesInstance = new ProductImages({
      selectorId: id,
      heightOnChange: () => {
        setPosition({
          id,
          offsetTop,
          container,
        });
      },
    });
  } catch (e) {
    setTimeout(() => {
      throw e;
    });
  }

  // inquiry modal
  // // eslint-disable-next-line no-new
  const inquiryPriceModal = new InquiryPriceModal({ id, spu });

  const addToCartList = new AddToCartList(spu);
  addToCartList.init();

  const ButtonGroup = new ButtonEvent({
    id,
    cartRoot: `.pdp_add_to_cart_${id}`,
    buyNowRoot: `.pdp_buy_now_${id}`,
    payPayId: `pdp_paypal_${id}`,
    soldOutRoot: `.pdp_sold_out_${id}`,
    spu,
    sku,
    onAddSuccess: () => {
      if (typeof onAddSuccess === 'function') {
        onAddSuccess();
      }
    },
  });

  const quantityStepper = new SkuQuality({
    id,
    sku,
    spu,
    onChange: (num) => {
      ButtonGroup.setActiveSkuNum(num);
      if (id === 'productDetail') {
        window.productDetailDataBus.set('num', num);
        window.productDetailDataBus.emit('after:countChange', num);
      }
      window.SL_EventBus.emit('product:count:change', [num, id]);
    },
  });

  new ProductCollapse({
    selector: `.product-detail-collapse_${id}`,
  });
  const getSkuChangeData = (skuInfo) => {
    const { spuSeq, discount, skuSeq, price, originPrice, stock, weight, weightUnit, available, shelves, skuAttributeIds, imageList, soldOut, allowOversold, imageBeanList } =
      skuInfo || {};
    return {
      spuSeq,
      discount,
      skuSeq,
      price: formatCurrency(price || 0),
      originPrice: formatCurrency(originPrice || 0),
      stock,
      weight,
      weightUnit,
      available,
      shelves,
      skuAttributeIds,
      imageList,
      soldOut,
      allowOversold,
      imageBeanList,
    };
  };

  let activeSkuCache = {};

  const skuTrade = initSku({
    id,
    sku,
    spu,
    mixins: window.skuMixins,
    onInit: (trade, activeSku) => {
      thirdPartReport({ activeSku, spu, sku });
      // skuChangeshared
      activeSkuCache = activeSku;
      let content_sku_id = '';
      let price = null;

      console.log('sku Init', activeSku);
      inquiryPriceModal.setActiveSku(activeSku);
      if (id === 'productDetail') {
        window.productDetailDataBus.set('activeSku', activeSku);
      }
      if (activeSku) {
        ButtonGroup.setActiveSku(activeSku);
        quantityStepper.setActiveSku(activeSku);
        addToCartList.setActiveSku(activeSku);

        content_sku_id = activeSku?.skuSeq;
        price = formatCurrency(activeSku?.price || 0);
        emitProductSkuChange({
          type: 'init',
          quantity: quantityStepper?.skuStepper?.data?.value || 1,
          ...getSkuChangeData(activeSku),
        });
      }
      emitViewContent({
        content_spu_id: spu.spuSeq,
        content_sku_id,
        content_category: '',
        currency: window?.SL_State?.get('storeInfo.currency'),
        value: price,
        quantity: 1,
        price,
      });
      emitProductSkuChanged({
        type: 'init',
        id,
        instances: {
          productImages: productImagesInstance,
          buttonGroup: ButtonGroup,
          quantityStepper,
        },
        quantity: quantityStepper?.skuStepper?.data?.value || 1,
        ...getSkuChangeData(activeSku),
      });
    },
    onChange: (activeSku) => {
      if (id === 'productDetail') {
        window.productDetailDataBus.set('activeSku', activeSku);
        window.productDetailDataBus.emit('after:skuChange', activeSku);
      }
      inquiryPriceModal.setActiveSku(activeSku);
      activeSkuCache = activeSku;

      // skuChangeshared
      // activeskuactiveSku
      if (activeSku) {
        thirdPartReport({ activeSku, spu, sku });
        emitProductSkuChange({
          type: 'change',
          quantity: quantityStepper?.skuStepper?.data?.value || 1,
          ...getSkuChangeData(activeSku),
        });
      }

      // todo test sku image change
      productImagesInstance?.skuImageChange(activeSku?.imageBeanList?.[0]);
      // sku
      if (activeSku || quantityStepper.activeSku) {
        setProductPrice(id, statePath, activeSku);
        ButtonGroup.setActiveSku(activeSku);
        quantityStepper.setActiveSku(activeSku);
        addToCartList.setActiveSku(activeSku);
      }

      emitProductSkuChanged({
        type: 'change',
        id,
        instances: {
          productImages: productImagesInstance,
          buttonGroup: ButtonGroup,
          quantityStepper,
        },
        quantity: quantityStepper?.skuStepper?.data?.value || 1,
        ...getSkuChangeData(activeSku),
      });
    },
  });

  // 
  const unmountPromotionTags = promotionTagsInit(`#product-info_${id}`);

  // 
  const unmountedDiscountCoupon = initDiscountCoupon();

  //  
  window.SL_EventBus.on('global:currency:format', () => {
    setProductPrice(id, statePath, quantityStepper.activeSku);
  });

  // 
  try {
    const flashSale = new FlashSale();
    flashSale.init();
  } catch (e) {
    console.log('');
    console.error(e);
  }

  return {
    skuTrade,
    quantityStepper,
    productEventRepeat: () => {
      let content_sku_id = '';
      let price = null;
      if (activeSkuCache) {
        content_sku_id = activeSkuCache?.skuSeq;
        price = formatCurrency(activeSkuCache?.price || 0);
        emitProductSkuChange({
          type: 'init',
          quantity: quantityStepper?.skuStepper?.data?.value || 1,
          ...getSkuChangeData(activeSkuCache),
        });
      }
      thirdPartReport({ activeSku: activeSkuCache, spu, sku });
      emitViewContent({
        content_spu_id: spu.spuSeq,
        content_sku_id,
        content_category: '',
        currency: window?.SL_State?.get('storeInfo.currency'),
        value: price,
        quantity: 1,
        price,
      });
    },
    destroy: () => {
      inquiryPriceModal.unbindEvents();
      unmountPromotionTags?.();
      unmountedDiscountCoupon?.();
      removePositionListener();
      skuTrade.destory();
    },
  };
}
