/* eslint-disable */
export default class SkuUtil {
  constructor() {
    this.skuResult = {};
  }

  /**
   * sku
   * @param {Array} data
   */
  initSku(data) {
    // console.log('asdads', this.skuResult);
    if (!data) return {};
    const skuKeys = Object.keys(data);
    skuKeys.forEach((skuKey) => {
      const sku = data[skuKey];
      const skuKeyAttrs = skuKey.split(';');
      const combArr = SkuUtil.combInFlags(skuKeyAttrs);
      combArr.forEach((item) => {
        // sku
        this.skuOptionAttrResult(item, sku);
      });

      // 
      // this.skuResult[skuKey] = sku;
    });
    // console.log('this.skuResult; skudebug', this.skuResult);
    return this.skuResult;
  }

  /**
   * 
   * @param {Array} skuKeyAttrs  ;  ['5.5','16G','']
   * @return Array
   */
  static combInFlags(skuKeyAttrs) {
    if (!skuKeyAttrs || skuKeyAttrs.length <= 0) return [];
    const len = skuKeyAttrs.length;
    const result = [];
    for (let n = 1; n <= len; n++) {
      const flags = SkuUtil.getCombFlags(len, n);
      // console.log('flags', flags)
      flags.forEach((flag) => {
        const comb = [];
        flag.forEach((item, index) => {
          if (item === 1) {
            comb.push(skuKeyAttrs[index]);
          }
        });
        result.push(comb);
      });
    }
    return result;
  }

  /**
   *   m  n  10
   * @param m 
   * @param n 1 - 
   * @return Array
   */
  static getCombFlags(m, n) {
    const flagArrs = [];
    const flagArr = [];
    let isEnd = false;
    for (let i = 0; i < m; i += 1) {
      flagArr[i] = i < n ? 1 : 0;
    }
    flagArrs.push(flagArr.concat());
    // n0mn
    if (n && m > n) {
      while (!isEnd) {
        let leftCnt = 0;
        for (let i = 0; i < m - 1; i++) {
          if (flagArr[i] === 1 && flagArr[i + 1] === 0) {
            for (let j = 0; j < i; j++) {
              flagArr[j] = j < leftCnt ? 1 : 0;
            }
            flagArr[i] = 0;
            flagArr[i + 1] = 1;
            const aTmp = flagArr.concat();
            flagArrs.push(aTmp);
            if (aTmp.slice(-n).join('').indexOf('0') === -1) {
              isEnd = true;
            }
            break;
          }
          flagArr[i] === 1 && leftCnt++;
        }
      }
    }
    return flagArrs;
  }

  /**
   * 
   * @param {*} combArrItem
   * @param {*} sku
   */
  skuOptionAttrResult(combArrItem, sku) {
    const key = combArrItem.join(';');
    if (this.skuResult[key]) {
      const prevPrice = this.skuResult[key].price;
      const curPrice = [sku.price];
      this.skuResult[key] = {
        ...sku,
        price: prevPrice.concat(curPrice).sort(), // 
        stock: this.skuResult[key].stock + sku.stock, // 
      };
    } else {
      this.skuResult[key] = { ...sku, price: [sku.price] };
    }
  }

  /**
   * 
   * @param {Array} arr
   */
  static filterValidArr(arr) {
    return arr.filter((item) => item).map((item) => item.id);
  }

  /**
   * 
   * @param {Array} selectSpecList
   * @param {String} name
   * @param {Number} index
   */
  checkSpecAttrDisabled(selectSpecList, id, index) {
    // 
    // console.log('checkSpecAttrDisabled skudebug', id )

    if (!this.skuResult[id]) return true;
    // 
    const newSelectList = selectSpecList.map((item) => item && { id: item });
    // console.log('newSelectList', newSelectList)
    // id
    newSelectList[index] = {
      id: '',
      ...newSelectList[index],
    };
    // ** 
    if (Number(newSelectList[index].id) !== Number(id)) {
      newSelectList[index].id = id;
      //  name
      const hitAttrKey = SkuUtil.filterValidArr(newSelectList).join(';');
      return !this.skuResult[hitAttrKey];
    }
  }

  /**
   * active
   * @param {Array} selectSpecList
   * @param {String} name
   * @return {Boolean}
   */
  checkSpecAttrActive(selectSpecList, name) {
    const newSelectList = selectSpecList.map((item) => ({ id: item }));
    return SkuUtil.filterValidArr(newSelectList).indexOf(name) !== -1 || SkuUtil.filterValidArr(newSelectList).indexOf(Number(name)) !== -1;
  }

  /**
   * 
   * @param {Array} selectSpecList
   * @param {Object} item
   * @param {Number} index
   * @param {Boolean} disabled
   */
  getActionSpecList(selectSpecList, item, index) {
    // 
    if (selectSpecList[index] && selectSpecList[index] === item.id) {
      selectSpecList[index] = null;
    } else {
      selectSpecList[index] = item.id;
    }
    if (selectSpecList.length) {
      return selectSpecList.slice();
    }
    return [];
  }

  /**
   * 
   * @param {Array} selectSpecList
   */
  getPrice(selectSpecList) {
    const skukey = SkuUtil.filterValidArr(selectSpecList).join(';');
    const hitSpecObj = this.skuResult[skukey];
    if (!hitSpecObj) return null;
    const priceArr = hitSpecObj.price;
    const maxPrice = Math.max(...priceArr);
    const minPrice = Math.min(...priceArr);
    return {
      minPrice,
      maxPrice,
    };
  }

  /**
   *  
   */
  getStock(selectSpecList) {
    const skukey = SkuUtil.filterValidArr(selectSpecList).join(';');
    const hitSpecObj = this.skuResult[skukey];
    if (!hitSpecObj) return null;
    return hitSpecObj.stock;
  }
}
