import navigateCheckout from './navigate-checkout';
import getCartId from './get-cart-id';
import renderPaypal from './render-paypal';
import navigateCart from './navigate-cart';
import addToCart from './add-to-cart';
import updateCheckoutDetail from './update-checkout-detail';
import apiLogger from '../../utils/api-logger';
import useCart from './use-cart';

const logger = apiLogger('register');
// eslint-disable-next-line prettier/prettier
const events = [navigateCheckout, getCartId, renderPaypal, navigateCart, addToCart, useCart, updateCheckoutDetail];

/**
 * ### Useage
 *```javascript
 * import register from '@yy/sl-theme-shared/events/trade/developer-api'
 *
 * register("Cart::NavigateCart")
 *
 * register("Cart::NavigateCart", "Checkout::NavigateCheckout")
 * ```
 */
export default (...activateApiNames) => {
  const executedEvents = [];
  activateApiNames.forEach((activateApiName) => {
    events.forEach((event) => {
      if (event?.apiName === activateApiName) {
        executedEvents.push(activateApiName);
        event?.();
      }
    });
  });
  logger.info('executed events', executedEvents);
  return executedEvents;
};
