import Swiper, { Autoplay } from 'swiper';
import 'swiper/swiper-bundle.css';
import './header-search';
import './header-mobile-menu';
import './mobile-locale-currency-drawer';
import { throttle, debounce } from 'lodash';
import request from '@yy/sl-theme-shared/utils/request';
import { StickyElementManager } from '@yy/sl-theme-shared/utils/stickyElementManager';
import Base from '../../shared/base/BaseClass';
import { OPEN_MINI_CART, CLOSE_MINI_CART } from '../../shared/cart/globalEvent';
import TopDrawer, { DRAWER_EVENT_NAME, DRAWER_OPERATORS } from '../../shared/components/topDrawer';
import './desktop-site-nav';

Swiper.use(Autoplay);
let menuDrawer = null;
class Header extends Base {
  config = {
    namespace: 'satge:header',
    wrapperOverlayed: false,
    stickyEnabled: false,
    stickyActive: false,
    stickyHeader: false,
    lastScroll: 0,
    forceStopSticky: false,
    stickyHeaderWrapper: 'StickyHeaderWrap',
    swiperBreakpoint: 750,
    lastStickOffsetTop: 0,
  };

  classes = {
    overlayedClass: 'header--is-light',
    stickyClass: 'header__main--sticky',
    openTransitionClass: 'header__main--opening',
    activateSwiperClass: 'swiper-wrapper',
    activateSwiperContainer: 'swiper-container',
    activeCartClass: 'header__cart--active',
    outerWrapperSticky: 'is-sticky',
    wrapperSticky: 'header-wrapper--sticky',

    activeNavClass: 'show-nav',
    headerBtnLink: 'header__btn--link',
    headerBtnActive: 'header__btn--on',
    headerBtnWrapperActive: 'header__btn--active',
  };

  selectors = {
    wrapper: '#stage-headerWrapper',
    header: '#stage-header',
    drawerBtn: '.j-header-drawer-btn',
    announcementSlider: '#AnnouncementSlider',
    announcementSliderWrapper: '.stage-announcement-slider',
    miniCart: '.header__cart',
    cartBtn: '#stage-header-cart',
    cartPoint: '.header__cart-point',
    megamenu: '.megamenu',
    logoImage: '.header__logo--link img',
    outerWrapper: '.header-sticky-wrapper',
    logoImag: '.header__logo--link img',
    notOverlayId: '#CollectionHeaderSection',

    navBtn: '.j-header-nav-btn',
    headerBtn: '.header__btn',
    headerBtnWrapper: '.header__item--buttons',
    menuBtn: '.header__btn--menu',
    layoutContainer: '.header__layout-container',
    sectionOuterHeaderWrapper: '#shopline-section-header',
  };

  jq = {
    header: $(),
    wrapper: $(),
    stickyHeaderWrapper: $(),
  };

  cacheAnnouncementSliderHTML = '';

  cacheAnnouncementSliderEl = null;

  swiperInstance = null;

  activeProductItem = null; // PC

  activeProductItemTimerId = null;

  activeSiteNavMegaMenu = null; // pc

  constructor() {
    super();
    this.$setNamespace(this.config.namespace);
    this.jq.header = $(this.selectors.header);
    this.jq.wrapper = $(this.selectors.wrapper);

    this.stickyElementManager = new StickyElementManager();

    this.initMenuDrawer();
    this.initMobileToolkit();

    if ($(this.selectors.announcementSlider).length) {
      this.bindAnnouncement();
    }

    this.bindCartEvent();
    this.initAfterLoaded();
    this.bindHeaderNav();
    const $img = $(this.selectors.logoImag);
    let waitForImg = null;
    if ($img.length) {
      waitForImg = $img?.toArray().find((img) => img.offsetParent !== null);
    }
    if (waitForImg) {
      if (waitForImg.complete && waitForImg.naturalHeight !== 0) {
        this.initHeader();
      } else {
        waitForImg.onload = () => {
          this.initHeader();
        };
      }
    } else {
      $(() => {
        this.initHeader();
      });
    }
  }

  initAfterLoaded() {
    if (document.readyState !== 'loading') {
      this.bindCartCount();
    } else {
      document.addEventListener('DOMContentLoaded', () => {
        this.bindCartCount();
      });
    }
  }

  initMenuDrawer() {
    if (!menuDrawer) {
      menuDrawer = new TopDrawer('header-mobile-menu-drawer');
    }

    const footerBody = $('#stage-footer .footer__body').clone();
    menuDrawer.$root.find('.header-menu__footer').html(footerBody);

    this.$on('click', this.selectors.menuBtn, (e) => {
      const $btn = $(e.currentTarget);
      if (menuDrawer.isOpen) {
        menuDrawer.close();
        this.toggleHeaderBtn($btn, false);
      } else {
        const winHeight = window.innerHeight;
        const offsetTop = menuDrawer.$root[0].getBoundingClientRect().y;
        const drawerMaxHeight = winHeight - offsetTop;

        menuDrawer.setMaxHeight(drawerMaxHeight);
        menuDrawer.open({ disablePageScroll: true });
        this.toggleHeaderBtn($btn, true);
      }
    });
  }

  initMobileToolkit() {
    const $items = $('.j-locale-currency-flag');
    const $root = $('#stage-headerWrapper');
    const $localeBtn = $root.find('.j-locale-drawer-btn');
    const $currencyBtn = $root.find('.j-currency-drawer-btn');

    for (let i = 0; i < $items.length; i++) {
      const $item = $($items[i]);
      const tmp_currency = $item.data('currency');
      const tmp_locale = $item.data('locale');
      const tmp_currency_symbol = $item.data('currency-symbol');
      if (tmp_currency) {
        $currencyBtn.addClass('show');
      }
      if (tmp_locale) {
        $localeBtn.addClass('show');
      }
      if (tmp_currency_symbol) {
        $currencyBtn.find('.currency-symbol').addClass('show');
      }
    }
  }

  initHeader() {
    this.config.stickyEnabled = this.jq.header.data('sticky');
    if (this.config.stickyEnabled) {
      const notOverlay = $(this.selectors.notOverlayId);
      if (notOverlay.length) {
        this.config.wrapperOverlayed = false;
        this.jq.wrapper.removeClass(this.classes.overlayedClass);
        this.jq.wrapper.removeClass(this.classes.wrapperSticky);
      } else {
        this.config.wrapperOverlayed = this.jq.wrapper.data('overlay');
      }
      this.stickyHeaderCheck();
    }
    window.SL_EventBus.on('force-header-intoView', () => {
      if (window.scrollY < 250 && window.scrollY > 0) {
        $(this.selectors.header)[0].scrollIntoView();
      }
    });

    window.SL_EventBus.on(DRAWER_EVENT_NAME, (res) => {
      const { id, operator } = res;
      if (!['header-mobile-menu-drawer', 'cart-select'].includes(id)) {
        return;
      }

      const $btn = $(this.selectors.cartBtn);
      if (operator === DRAWER_OPERATORS.OPEN) {
        this.toggleHeaderBtn($btn, true);
      } else {
        this.toggleHeaderBtn($btn, false);
        this.toggleHeaderBtn($(this.selectors.menuBtn), false);
      }
    });

    window.SL_EventBus.on('stage:locale:change', () => {
      if (this?.jq?.stickyHeaderWrapper[0]?.style?.height) {
        this.jq.stickyHeaderWrapper[0].style.height = 'auto';
        this.stickyHeaderHeight();
      }
    });
  }

  stickyHeaderCheck() {
    this.config.stickyHeader = true;
    if (this.config.stickyHeader) {
      this.config.forceStopSticky = false;
      this.stickyHeader();
    } else {
      this.config.forceStopSticky = true;
    }
  }

  stickyHeader() {
    this.config.lastScroll = 0;
    const wrapWith = document.createElement('div');
    wrapWith.id = this.config.stickyHeaderWrapper;
    this.jq.header.wrap(wrapWith);
    this.jq.stickyHeaderWrapper = $(`#${wrapWith.id}`);
    this.stickyHeaderHeight();
    this.stickyHeaderScroll();
    // 
    setTimeout(() => {
      requestAnimationFrame(this.scrollWorker.bind(this));
    }, 2500);
    this.$onWin('resize', debounce(this.stickyHeaderHeight.bind(this), 50));
    this.$onWin('scroll', throttle(this.stickyHeaderScroll.bind(this), 20));

    if (window.Shopline.designMode) {
      // this.jq.stickyHeaderWrapper[0].style.height = 'auto';
      const $logoImage = $(this.selectors.logoImage);
      const onLoadHandler = () => {
        setTimeout(() => {
          this.stickyHeaderHeight();
        }, 1000);
        $logoImage.off('load');
      };

      $logoImage.on('load', onLoadHandler);
    }
  }

  stickyHeaderScroll() {
    if (!this.config.stickyEnabled) {
      return;
    }
    if (this.config.forceStopSticky) {
      return;
    }
    requestAnimationFrame(this.scrollWorker.bind(this));
    this.config.lastScroll = window.scrollY;
  }

  scrollWorker() {
    if (window.scrollY > 250) {
      this.doSticky();
    } else {
      this.undoSticky();
    }
  }

  emitSticky(stickyActive) {
    const headerHeight = $(this.selectors.header).height();
    const stickOffsetTop = this.getStickHeaderOffsetTop();
    this.config.lastStickOffsetTop = stickOffsetTop;
    window.SL_EventBus.emit('stage:header:sticky', {
      stickyActive,
      headerHeight,
    });
    window.SL_EventBus.emit('stage:header:stickOffsetTopUpdate', {
      stickOffsetTop,
      stickyActive,
    });
  }

  undoSticky() {
    if (this.config.stickyActive === false) {
      return;
    }
    this.config.stickyActive = false;
    this.jq.header.removeClass([this.classes.openTransitionClass, this.classes.stickyClass]);
    $(this.selectors.outerWrapper).removeClass(this.classes.outerWrapperSticky);
    this.jq.header.css({ top: 0 });
    if (this.config.wrapperOverlayed) {
      this.jq.wrapper.addClass(this.classes.overlayedClass);
    }
    this.emitSticky(false);
  }

  doSticky() {
    const stickOffsetTop = this.getStickHeaderOffsetTop();
    const isNeedRerender = !this.config.stickyActive || this.config.lastStickOffsetTop !== stickOffsetTop;
    if (!isNeedRerender) {
      return;
    }

    this.config.stickyActive = true;
    this.jq.header.addClass(this.classes.stickyClass);
    if (this.config.wrapperOverlayed) {
      this.jq.wrapper.removeClass(this.classes.overlayedClass);
    }
    setTimeout(() => {
      this.jq.header.addClass(this.classes.openTransitionClass);
      $(this.selectors.outerWrapper).addClass(this.classes.outerWrapperSticky);
      this.emitSticky(true);
      this.jq.header.css({ top: stickOffsetTop });
    }, 100);
  }

  stickyHeaderHeight() {
    if (!this.config.stickyEnabled) {
      return;
    }
    const isMobile = window.innerWidth <= this.config.swiperBreakpoint;

    const headerContentHeight = $('.header__layout').css('height') || 0;
    const headerSearchHeight = $('.header__search-mobile').css('height') || 0;
    const headerNavHeight = $('.header__nav-container').css('height') || 0;

    const pcHeight = parseFloat(headerContentHeight) + parseFloat(headerNavHeight);
    const mobileHeight = parseFloat(headerContentHeight) + parseFloat(headerSearchHeight);
    const h = isMobile ? mobileHeight : pcHeight;
    this.jq.stickyHeaderWrapper[0].style.height = this.config.wrapperOverlayed ? 'auto' : `${h}px`;
  }

  // 
  getStickHeaderOffsetTop() {
    let top = 0;
    $(this.selectors.sectionOuterHeaderWrapper)
      .prevAll()
      .each((_, el) => {
        const $el = $(el);
        if ($el.css('position') === 'sticky') {
          top += $el.height();
        }
      });
    return top;
  }

  bindCartEvent() {
    this.$on('click', this.selectors.miniCart, (e) => {
      const $btn = $(e.currentTarget);
      if ($btn.hasClass('header__btn--on')) {
        window.SL_EventBus.emit(CLOSE_MINI_CART);
      } else {
        window.SL_EventBus.emit(OPEN_MINI_CART);
      }
    });
  }

  bindHeaderNav() {
    this.$on('click', this.selectors.navBtn, () => {
      $(this.selectors.layoutContainer).toggleClass(this.classes.activeNavClass);
    });
  }

  bindAnnouncement() {
    this.cacheAnnouncementSliderHTML = $(this.selectors.announcementSlider).parent()[0].innerHTML;

    this.activateSwiper();
    this.$win.on(
      'resize',
      debounce(() => {
        this.swiperResizeHandler();
      }, 50),
    );
  }

  swiperResizeHandler() {
    this.disableSwiper();
    this.activateSwiper();
    // const width = window.innerWidth
    // if (width < this.config.swiperBreakpoint) {
    //   this.activateSwiper();
    // } else {
    //   this.disableSwiper();
    // }
  }

  activateSwiper() {
    const $wrapper = $(this.selectors.announcementSlider).find(this.selectors.announcementSliderWrapper);
    if ($wrapper.hasClass(this.classes.activateSwiperClass)) {
      return;
    }
    $wrapper.parents('.container').eq(0).addClass(this.classes.activateSwiperContainer);
    $wrapper.addClass(this.classes.activateSwiperClass);
    const count = $wrapper.data('block-count');
    this.initSwiperHeight();

    this.swiperInstance = new Swiper(this.selectors.announcementSlider, {
      init: true,
      loop: count > 1,
      direction: 'vertical',
      autoplay: {
        delay: 5000,
        speed: 7000,
      },
    });
  }

  initSwiperHeight() {
    const height = $(this.selectors.announcementSlider).height();
    $(this.selectors.announcementSliderWrapper).css('height', height);
  }

  disableSwiper() {
    if (!this.swiperInstance) {
      return;
    }
    const $wrapper = $(this.selectors.announcementSlider).find(this.selectors.announcementSliderWrapper);
    if (!$wrapper.hasClass(this.classes.activateSwiperClass)) {
      return;
    }
    $wrapper.removeClass(this.classes.activateSwiperClass);
    $wrapper.parents('.container').eq(0).removeClass(this.classes.activateSwiperContainer);
    $(this.selectors.announcementSlider).parent().html(this.cacheAnnouncementSliderHTML);
  }

  fetchCartInfo() {
    return request({
      url: 'carts/cart/count',
      method: 'GET',
    }).then((res) => {
      if (res.success) {
        return Promise.resolve(res.data);
      }
    });
  }

  bindCartCount() {
    window.SL_State.on('cartInfo', () => {
      this.activeCart();
    });
    this.fetchCartInfo().then((num) => {
      this.activeCart(num);
    });
  }

  toggleHeaderBtn($btn, active) {
    const isToggle = $btn.data('type') === 'toggle';
    if (!isToggle) {
      return;
    }
    const $btnWrapper = $(this.selectors.headerBtnWrapper);
    const cls = this.classes.headerBtnActive;
    const wrapperCls = this.classes.headerBtnWrapperActive;

    if (active) {
      $btn.addClass(cls);
      $btnWrapper.addClass(wrapperCls);
    } else {
      $btn.removeClass(cls);
      $btnWrapper.removeClass(wrapperCls);
    }
  }

  activeCart(num = 0) {
    const cartBtn = $(this.selectors.cartBtn);
    if (!cartBtn.length) {
      return;
    }
    const cartNum = window.SL_State.get('cartInfo.count') || num;
    if (cartNum) {
      cartBtn.removeClass(this.classes.activeCartClass).addClass(this.classes.activeCartClass);
      $(this.selectors.cartPoint).html(cartNum);
    } else {
      cartBtn.removeClass(this.classes.activeCartClass);
    }
  }

  offEventBus() {
    window.SL_EventBus.off('force-header-intoView');
  }

  off() {
    this.$offAll();
    this.stickyElementManager.offEvent();
    this.offEventBus();
  }
}

// eslint-disable-next-line no-new
let instance = new Header();

export const getHeaderSectionId = () => {
  return $(instance.selectors.wrapper).data('section=id');
};

$(document).on('shopline:section:load', () => {
  instance.off();
  instance = null;
  instance = new Header();
  // instance.initHeader();
});
