import request from '@yy/sl-theme-shared/utils/request';
import { t } from '@yy/sl-theme-shared/utils/i18n';
import utils from '../../shared/utils';
import Base from '../../shared/base/BaseClass';
import checkEmail from '../../shared/utils/checkEmail';
import debounce from '../../shared/utils/debounce';
import Toast from '../../shared/components/toast';

const { listenPlatform } = utils.helper;

class Footer extends Base {
  config = {
    namespace: 'satge:footer',
  };

  constructor() {
    super();
    this.$setNamespace(this.config.namespace);
    this.init();
    listenPlatform(() => {
      this.reset();
    });
  }

  init() {
    this.bindEvent();
    this.initMobileToolkit();
  }

  initMobileToolkit() {
    const $item = $('#stage-footer .j-locale-currency-flag');
    const $root = $('#stage-footer');
    const $localeBtn = $root.find('.j-locale-drawer-btn');
    const $currencyBtn = $root.find('.j-currency-drawer-btn');

    const currency_selector = $item.data('currency');
    const locale_selector = $item.data('locale');
    const show_currency_symbol = $item.data('currency-symbol');

    if (locale_selector) {
      $localeBtn.addClass('show');
    }

    if (currency_selector) {
      $currencyBtn.addClass('show');
      if (show_currency_symbol) {
        $currencyBtn.find('.currency-symbol').addClass('show');
      }
    }
  }

  reset() {
    this.offEvent();
    this.init();
  }

  offEvent() {
    this.$off('click');
  }

  bindEvent() {
    this.bindSubscription();
  }

  bindSubscription() {
    const post = debounce(300, (val) => {
      const params = {
        subscribeAccountType: 'email',
        subscribeAccount: val,
      };
      const referralCode = window?.SLMemberPlugin?.memberReferralCode?.value || null;
      if (referralCode) {
        params.referralCode = referralCode;
      }

      request
        .post('/user/front/users/footersub', params)
        .then((res) => {
          if (res.success) {
            Toast.init({ content: t('footer.subscribeSuccess') });
          } else {
            Toast.init({ content: t('footer.subscribeRegExpError') });
          }
        })
        .catch((err) => {
          console.log('subscribe error', err);
          Toast.init({ content: t('footer.subscribeRegExpError') });
        });
    });

    this.$on('click', '.footer__newsletter-btn', (e) => {
      const $input = $(e.currentTarget).siblings('input');
      const value = $input.val();
      if (checkEmail(value) !== true) {
        Toast.init({ content: t('footer.subscribeRegExpError') });
        return;
      }
      post(value);
    });
  }
}

// export default new Footer();

const instance = new Footer();

$(document).on('shopline:section:load', () => {
  instance.reset();
});
