import '../styles/_platform.scss';

let d = $('i[data-platform]');

if (!d.get(0)) {
  d = $(`<i data-platform></i>`);
  $(document.body).append(d);
}

export default function isMobile() {
  let dom = $('i[data-platform]');

  if (!dom.get(0)) {
    dom = $(`<i data-platform></i>`);
    $(document.body).append(dom);
  }

  return dom.css('display') === 'block';
}
