import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import utils from '../../../../shared/utils';

const getNeedSticky = (containerSelector) => {
  if (containerSelector !== '.miniCart__stick_container') {
    return true;
  }
  const maxHeight = $('#cart-select .trade_cart_not_empty_wrapper').css('max-height');
  // 280px
  return parseInt(maxHeight, 10) >= 280;
};

export const setStickyContAnimate = ({ viewportSelector, containerSelector }) => {
  const inInput = SL_State.get('cartInInputMode');
  const needSticky = getNeedSticky(containerSelector);
  if (needSticky && !inInput && !utils.helper.isUnderViewport($(viewportSelector).get(0))) {
    // 
    $(containerSelector).slideDown(300).attr('isOpen', true);
  } else {
    // 
    $(containerSelector).slideUp(200).removeAttr('isOpen');
  }
};

export const setStickyContainerHeight = (selector) => {
  if (!$(selector).length || $(selector).prop('hadSet') === true) return;
  const height = $(selector).innerHeight();
  if (height) {
    $(selector).height(height).prop('hadSet', true);
  }
};

export const setFixedContentStyle = (contentSelector, height) => {
  const contentEl = $(contentSelector);
  if (!contentEl.length || contentEl.prop('hadSet')) return;
  if (contentEl) {
    contentEl.css('paddingBottom', height).prop('hadSet', true);
  }
};

export const listenElementMutation = (target, callback, options = { childList: true }) => {
  const observer = new MutationObserver(callback);
  observer.observe(target, options);
  return observer;
};

export const listenElementIntersection = (target, callback) => {
  const intersectionObserver = new IntersectionObserver(
    function (entries) {
      // If intersectionRatio is 0, the target is out of view
      const status = entries[0].intersectionRatio > 0.5;
      callback(status);
    },
    {
      threshold: 0.5,
    },
  );
  // start observing
  intersectionObserver.observe(target);
  return intersectionObserver;
};
