import utils from './index';
import { SL_State } from '../state-selector';

/**
 * 
 * @param value
 * @param from
 * @param to
 */
export const covertCalc = (value, from, to, dataSource = {}) => {
  if (from === to) {
    return value;
  }
  return (value * (dataSource?.[to] ?? 1)) / (dataSource?.[from] ?? 1);
};

/**
 * 
 * @param value
 * @param options
 */
export const convertFormat = (value, options = {}) => {
  const fromDefault = SL_State.get('storeInfo.currency');
  const toDefault = SL_State.get('currencyCode');
  const locale = SL_State.get('request.locale');

  const { from = fromDefault, to = toDefault, lang = locale } = options;
  const data = SL_State.get('currencyRates');
  const rst = covertCalc(value, from, to, data);
  return utils.format(rst, {
    code: to,
    lang,
  });
};
