import * as Cookies from 'js-cookie';
import { PageType, ClickType } from '../report/const';

const clickFbData = (type, params) => {
  let value = null;
  let event = null;
  let evid = null;
  let res = [];
  switch (type) {
    case ClickType.AddToCart:
      value = {
        content_type: 'product_group',
        content_category: params?.category,
        content_ids: params?.skuId,
        content_name: params?.name,
        value: params?.price,
        currency: params?.currency ?? Cookies.get('currency_code'),
      };
      event = 'AddToCart';
      evid = {
        eventID: params?.eventId,
      };
      res.push(['track', event, value, evid]);
      break;
    default:
      res = [];
  }
  return res;
};

const loadFbData = (page, params) => {
  let res = [];
  switch (page) {
    case PageType.OrderConfirm:
      res.push([
        'track',
        'Purchase',
        {
          content_type: 'product_group',
          content_ids: params?.skuIds,
          value: params?.amount,
          quantity: params?.quantity,
          currency: params?.currency ?? Cookies.get('currency_code'),
        },
        {
          eventID: params?.eventId,
        },
      ]);
      break;
    default:
      res = [];
      break;
  }
  return res;
};

export { clickFbData, loadFbData };
