import { t } from '@yy/sl-theme-shared/utils/i18n';
import { processPrice } from '../../../shared/utils/convertPrice';

let uniqueId = '';

const priceWrap = '.product-info-price_'; // 
const priceSellWrap = '.product-price-sales_'; // 
const priceOriginWrap = '.product-price-origin_'; // 
const priceVipWrap = '.product-price-vip_'; // vip
const priceDiscountWrap = '.product-price-discount_'; // 

/**
 * @description: 
 * @param {*} showDiscount 
 * @param {*} sku
 */
function getDiscount(showDiscount, sku) {
  const $el = $(`${priceDiscountWrap}${uniqueId}`);
  const discountSettingStyle = $el?.attr('product_discount_style'); // number: ratio
  const ratioCalc = Math.round(100 * (1 - sku.price / sku.originPrice));
  const discountText = discountSettingStyle === 'number' ? sku.originPrice - sku.price : ratioCalc;
  let isHidden = true;

  if (showDiscount && discountText > 0) {
    if (discountSettingStyle === 'number') {
      const discount = sku.originPrice - sku.price;

      if (discount > 0) {
        const { get: getPriceContent } = processPrice($el, discount, { isSavePrice: true });
        isHidden = false;

        $el.html(t('productList.container.listItem.saveByJs', { priceDom: getPriceContent() }));
      } else {
        isHidden = true;
      }
    } else {
      isHidden = false;
      $el.html(t('productList.container.listItem.saveRatio', { price: ratioCalc }));
    }
  } else {
    isHidden = true;
  }

  $el.toggleClass('hide', isHidden);
}

/**
 * @description: sku
 * @param {*} spuSoldout spu
 * @param {*} activeSku sku
 * @param {*} originPrice sku
 * @param {*} price sku
 * @param {*} discount 10&&spu
 */
const setSkuPrice = (spuSoldout, activeSku = {}) => {
  const discountSetting = $(`${priceWrap}${uniqueId}`)?.attr('product_discount'); // 
  const { originPrice: oriPrice, price, showMemberMark } = activeSku;
  const originPrice = oriPrice > price ? oriPrice : '';
  const showDiscount = discountSetting && !spuSoldout;
  processPrice($(`${priceSellWrap}${uniqueId}`), price).render();

  if (showMemberMark) {
    $(`${priceVipWrap}${uniqueId}`).removeClass('hide');
  } else {
    $(`${priceVipWrap}${uniqueId}`).addClass('hide');
  }

  if (originPrice) {
    processPrice($(`${priceOriginWrap}${uniqueId}`), originPrice).render();
    $(`${priceOriginWrap}${uniqueId}`).removeClass('hide');
  } else {
    $(`${priceOriginWrap}${uniqueId}`).addClass('hide');
  }
  getDiscount(showDiscount, activeSku);
};

const getHighOriginPrice = (min, item) => {
  if (min.price === item.price) {
    // skusku
    // eslint-disable-next-line no-nested-ternary
    return min.originPrice > item.originPrice ? min : item;
  }
  return min.price > item.price ? item : min;
};

const checkActive = (item) => {
  return item?.saleActivityResponseList?.some((activity) => activity.promotionType === 1 && activity.promotionSubType === 1);
};

/**
 * @description: skusku
 * @param {*} soldOut spu
 * @param {*} skuList sku
 */
const getMinPrice = (soldOut, skuList) => {
  return skuList?.reduce((min, item) => {
    // if (!item.available && !soldOut) {
    //   return min;
    // }
    if (min === null) {
      return item;
    }

    // 
    if (checkActive(min)) {
      if (checkActive(item)) {
        return getHighOriginPrice(min, item);
      }
      return min;
    }

    if (checkActive(item)) {
      return item;
    }

    // 
    if (min?.showMemberMark) {
      if (item?.showMemberMark) {
        return getHighOriginPrice(min, item);
      }

      return min;
    }

    if (item?.showMemberMark) {
      return item;
    }

    return getHighOriginPrice(min, item);
  }, null);
};

const setMinPrice = (soldOut, skuList) => {
  const minSku = getMinPrice(soldOut, skuList);
  setSkuPrice(soldOut, minSku);
};

const setProductPrice = (id, statePath, activeSku) => {
  uniqueId = id;
  const { skuList } = window.SL_State.get(`${statePath}.sku`);
  const { soldOut } = window.SL_State.get(`${statePath}.spu`);
  if (activeSku) {
    setSkuPrice(soldOut, activeSku);
  } else {
    setMinPrice(soldOut, skuList);
  }
};

export default setProductPrice;
