import { find, findIndex, round } from 'lodash';
import { convertFormat, covertCalc } from './CurrencyConvert';
import { HARD_CODE_CONFIG, SYMBOL_HARD_CODE_CONFIG, CURRENCY_DISPLAY_HARDCODE } from './constants';
import { SL_State } from '../state-selector';

const storeCurrency = SL_State.get('storeInfo.currency');
const storeLang = SL_State.get('request.locale');
const currencyRates = SL_State.get('currencyRates');

//  CNY
const defaultCurrency = 'CNY';
//  10**2=1000
export const defaultCurrencyDigit = 2;
//  10**2=100
const defaultPresentDigit = 2;
// 
const defaultLang = 'zh-hans-cn';
// 
const digitsMap = new Map();
//  map 
const formatUtilMap = new Map();
// 
const symbolsMap = new Map();
// 
const hardcoreConfigs = HARD_CODE_CONFIG;

// hardcore 
const hardcodeDigit = (code) => {
  const hardcoreConfig = hardcoreConfigs.find((config) => config.code === code);
  return {
    minimumFractionDigits: hardcoreConfig?.digit ?? void 0,
    maximumFractionDigits: hardcoreConfig?.digit ?? void 0,
  };
};

const hardCodeCurrencyDisplay = (code) => {
  return CURRENCY_DISPLAY_HARDCODE[code] ?? {};
};

/** hard code  */
const hardCodeSymbol = (code, lang) => {
  return SYMBOL_HARD_CODE_CONFIG[code]?.[lang] ?? lang;
};

// 
export const formatGenerator = (code, lang) => {
  const realLang = hardCodeSymbol(code, lang);
  return new Intl.NumberFormat(realLang, {
    style: 'currency',
    currency: code,
    ...hardCodeCurrencyDisplay(code),
    ...hardcodeDigit(code),
  });
};

// key
const cacheKeyGenerator = ({ code, lang }) => {
  const countryCode = code?.toUpperCase();
  const language = lang?.toUpperCase();
  if (countryCode && language) {
    return `${countryCode}-${language}`;
  }
  if (countryCode) {
    return countryCode;
  }
  if (language) {
    return language;
  }
};

/**
 * 
 * @param value
 * @param options
 * @returns string
 */
const format = (value, options = {}) => {
  const decimalDigits = defaultCurrencyDigit;
  const code = options?.code || storeCurrency || defaultCurrency;
  const lang = options?.lang || storeLang || defaultLang;
  const digits = 10 ** decimalDigits;
  let f = null;
  if (formatUtilMap.get(cacheKeyGenerator({ code, lang }))) {
    f = formatUtilMap.get(cacheKeyGenerator({ code, lang }));
  } else {
    f = formatGenerator(code, lang);
    // formatmap 
    formatUtilMap.set(cacheKeyGenerator({ code, lang }), f);
    // 
    digitsMap.set(code, f.resolvedOptions().maximumFractionDigits);
  }
  // value 1000
  return f.format(value / digits);
};

/**
 * code
 * @param code
 */
const getDigitsByCode = (code) => {
  if (typeof digitsMap.get(cacheKeyGenerator({ code })) === 'number') {
    return digitsMap.get(cacheKeyGenerator({ code }));
  }
  //  
  const digit = formatGenerator(code, 'zh-cn').resolvedOptions().maximumFractionDigits;
  digitsMap.set(cacheKeyGenerator({ code }), digit);
  return digit;
};

/**
 * code
 * @param code
 */
const getSymbolByCode = (code, lang = 'zh-cn') => {
  if (symbolsMap.get(cacheKeyGenerator({ code, lang }))) {
    return symbolsMap.get(cacheKeyGenerator({ code, lang }));
  }
  let symbol = '';
  const format = formatGenerator(code, lang);
  if (format.formatToParts) {
    symbol = find(format.formatToParts(), ['type', 'currency'])?.value;
  } else {
    const realLang = hardCodeSymbol(code, lang);
    // ie  hack
    const intl = new Intl.NumberFormat(realLang, {
      style: 'currency',
      currency: code,
      ...hardCodeCurrencyDisplay(code),
      // replace
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    });
    symbol = intl.format(0).replace('0', '')?.trim();
  }
  symbolsMap.set(cacheKeyGenerator({ code, lang }), symbol);
  return symbol;
};

/**
 * code, 
 * @param code
 */
const getSymbolOrderByCode = (code, lang = 'zh-cn') => {
  let order = 0;
  const format = formatGenerator(code, lang);
  if (format.formatToParts) {
    order = findIndex(format.formatToParts(), ['type', 'currency']);
  }
  // ie , 0 ieprefix
  return order > 0 ? 'suffix' : 'prefix';
};
/**
 * 
 * eg: 10.55 -> 1055
 * @param value
 * @param decimalDigits = 2
 * @returns number
 */
const unformatNumber = (value, decimalDigits = defaultCurrencyDigit) => {
  const v = round((typeof value !== 'number' ? Number(value) : value) * 10 ** decimalDigits, 0);
  return v;
};

/**
 * 
 * eg: 10.55 -> 1055
 * @param value
 * @returns number
 */
const unformatCurrency = (value) => {
  return unformatNumber(value, defaultCurrencyDigit);
};

/**
 * 
 * eg: 10.55 -> 1055
 * @param value
 * @returns number
 */
const unformatPercent = (value) => {
  return unformatNumber(value, defaultPresentDigit);
};

/**
 * 
 * eg: 1055 -> 10.55
 * @param value
 * @param decimalDigits = 2
 * @returns number
 */
const formatNumber = (value, decimalDigits = defaultCurrencyDigit) => {
  const v = typeof value !== 'number' ? Number(value) : value;
  return v / 10 ** decimalDigits;
};

/**
 * 
 * eg: 1055 -> 10.55
 * @param value
 * @returns number
 */
const formatCurrency = (value) => {
  return formatNumber(value, defaultCurrencyDigit);
};

/**
 * 
 * 10.51 -> 1051
 * @param value
 * @returns number
 */
const formatPercent = (value) => {
  return formatNumber(value, defaultPresentDigit);
};

/**
 * code
 * @param code
 */
const getDecimalSymbolByCode = (code, lang) => {
  let decimal = '';
  const format = formatGenerator(code, lang);
  if (format.formatToParts) {
    decimal = find(format.formatToParts(1.0), ['type', 'decimal'])?.value;
  } else {
    // ie ,hack
    const realLang = hardCodeSymbol(code, lang);
    const intl = new Intl.NumberFormat(realLang, {
      currency: code,
      ...hardCodeCurrencyDisplay(code),
      // replace
      useGrouping: false,
    });
    // 
    decimal = intl.format(0.1).replace(/[0-9]*/g, '');
  }
  return decimal;
};

/**
 * code
 * @param code
 */
const getGroupSymbolByCode = (code, lang) => {
  let group = '';
  const format = formatGenerator(code, lang);
  if (format.formatToParts) {
    group = find(format.formatToParts(10000.0), ['type', 'group'])?.value;
  } else {
    const realLang = hardCodeSymbol(code, lang);
    // ie ,hack
    const decimal = getDecimalSymbolByCode(code, realLang);
    group = decimal === '.' ? ',' : '.';
  }
  return group;
};

/**
 * parts
 * eg: [
  { type: "integer",  value: "3"   },
  { type: "group",    value: "."   },
  { type: "integer",  value: "500" },
  { type: "decimal",  value: ","   },
  { type: "fraction", value: "00"  },
  { type: "literal",  value: " "   },
  { type: "currency", value: ""   }
]
 */
const getFormatParts = (value, options) => {
  const { code, lang } = options;
  const format = formatGenerator(code, lang);
  if (format.formatToParts) {
    return format.formatToParts(value);
  }
  // ie ,hack
  const formatStr = format.format(value);
  const group = getGroupSymbolByCode(code, lang);
  const decimal = getDecimalSymbolByCode(code, lang);
  const symbolOrder = getSymbolOrderByCode(code, lang);
  const symbol = getSymbolByCode(code, lang);
  const rst = [];
  const [integerValue, fraction] = formatStr.replace(symbol, '').split(decimal);
  integerValue.split(group).forEach((item, index) => {
    rst.push({ type: 'integer', value: item });
    if (index !== integerValue.length - 1) rst.push({ type: 'group', value: group });
  });
  rst.push({ type: 'decimal', value: decimal });
  rst.push({ type: 'fraction', value: fraction.trim() });
  if (symbolOrder === 'prefix') {
    rst.unshift({ type: 'currency', value: symbol });
  } else {
    rst.push({ type: 'currency', value: symbol });
  }
  return rst;
};

const getConvertPrice = (money, options) => {
  const fromCurrencyCode = storeCurrency;
  const toCurrencyCode = options?.code;
  const lang = options?.lang;
  const covertMoneyByRate = covertCalc(money, fromCurrencyCode, toCurrencyCode, currencyRates);
  const covertMoney = formatCurrency(covertMoneyByRate);

  const formatPartsResult = getFormatParts(covertMoney, { code: toCurrencyCode, lang });
  const convertResult = {
    group: '',
    integer: '',
    decimal: '',
    fraction: '',
    symbolOrder: '',
    currencySymbol: '',
  };
  // 
  convertResult.symbolOrder = getSymbolOrderByCode(toCurrencyCode, lang);
  formatPartsResult.forEach((item) => {
    if (item?.type === 'currency') {
      convertResult.currencySymbol = item.value;
    }
    if (item?.type === 'integer') {
      if (convertResult.integer) {
        convertResult.integer = `${convertResult.integer}${convertResult.group || ''}${item.value}`;
      } else {
        convertResult.integer = item.value;
      }
    }
    if (item?.type === 'group') {
      convertResult.group = item.value;
    }
    if (item?.type === 'decimal') {
      convertResult.decimal = item.value;
    }
    if (item?.type === 'fraction') {
      convertResult.fraction = item.value;
    }
  });

  return convertResult;
};

export default {
  format,
  unformatNumber,
  formatNumber,
  unformatCurrency,
  unformatPercent,
  formatCurrency,
  formatPercent,
  getDigitsByCode,
  getSymbolByCode,
  getSymbolOrderByCode,
  getDecimalSymbolByCode,
  getGroupSymbolByCode,
  getFormatParts,
  getConvertPrice,

  // 
  convertFormat,
  covertCalc,
};
