import responseCode from '../../constant/responseCode';

function isOk(vo) {
  if (!vo) return false;
  return vo.code === responseCode.SUCCESS;
}

function is(vo, code) {
  if (!vo || !code) return false;
  return vo.code === code;
}

function batchIs(vo, code) {
  if (!vo || !code) return false;
  try {
    return code.split(',').includes(vo.code);
  } catch {
    return false;
  }
}
export default {
  is,
  isOk,
  batchIs,
};

/**
 * interface IResponseCode {
 *   code: string;
 * }
 * */
