import logger from '@funnyecho/logger';
import loggerSentry from '@funnyecho/logger-sentry';
import { withShoplineTrackingTransport, withStdoutTransport, withSentryTransport } from './transports';

const loggerGetter = (function () {
  let _logger;
  return function getLogger() {
    if (_logger) return _logger;

    _logger = logger.withLogger(
      null,
      logger.pipeTransport(...withShoplineTrackingTransport(), ...withSentryTransport(), ...withStdoutTransport()),
    );

    logger.withGlobalLogger(_logger);

    return _logger;
  };
})();

export default {
  get logger() {
    return loggerGetter();
  },

  pipeOwner(data) {
    //  object
    if (typeof data !== 'object') {
      console.warn('[logger.pipeOwner]object');
      return;
    }

    const { owner, base = loggerGetter(), onTag } = data;

    //  sentry tag
    if ((!process.env.USE_SENTRY || process.env.USE_SENTRY === 'SENTRY') && onTag) {
      return logger.withLogger(
        base,
        logger.pipeOwner(owner),
        logger.pipeTransport(loggerSentry.withSentryOnTag(onTag)),
      );
    }

    return logger.withLogger(base, logger.pipeOwner(owner));
  },
};

export { loggerSentry };
