import './hdReport';
/* eslint-disable */
import HiidoReport from './@hiido';
import { composedPath } from './utils';
import reportHeadless from './report-headless';
import { autoReport } from './auto-report';

const CLICK_CLASSNAME = '__sl-track_click';
const EXPOSE_CLASSNAME = '__sl-track_expose';
const COLLECT_CLICK_CLASSNAME = '__sl-collect_click';
const COLLECT_EXPOSE_CLASSNAME = '__sl-collect_expose';

// sdk
if (!window.SL_Report?.loaded) {
  window.SL_Report = window.SL_Report || {};
  initReportEvent();
}

if (!window.SL_Report.HdObserverSet) {
  window.SL_Report.HdObserverSet = new WeakSet();
}

if (!window.SL_Report.HdObserver) {
  window.SL_Report.HdObserver = new IntersectionObserver(
    (entries) => {
      entries.forEach((entrie) => {
        if (entrie.isIntersecting) {
          const repeat = entrie.target?.dataset['track_repeat'] || entrie.target?.dataset['collect_repeat'];
          // obsever
          if (
            (entrie.target.classList?.contains(EXPOSE_CLASSNAME) ||
              entrie.target.classList?.contains(COLLECT_EXPOSE_CLASSNAME)) &&
            (repeat === 'true' || (repeat !== 'true' && !window.SL_Report.HdObserverSet.has(entrie.target)))
          ) {
            let collectObj = {};
            sendCollect(entrie.target, collectObj, () => {
              if (repeat !== 'true') {
                window.SL_Report.HdObserverSet.add(entrie.target);
              }
            });
          } else {
            if (!window.SL_Report.HdObserverSet.has(entrie.target)) {
              window.SL_EventBus.emit('global:hdReport:expose', entrie.target);
              window.SL_Report.HdObserverSet.add(entrie.target);
            }
          }
        }
      });
    },
    {
      threshold: 0,
    },
  );
}

/**
 * 
 * 1. 
 * 2. 
 * 3. 
 */
function initReportEvent() {
  window.SL_Report.loaded = true;
  /**
   * 
   */
  window.SL_EventBus.on('global:thirdPartReport', (data) => {
    try {
      Object.keys(data).forEach((dataKey) => {
        let eventKey = dataKey;
        if (dataKey === 'GAR') {
          eventKey = 'GARemarketing';
        }

        if (window.__PRELOAD_STATE__.eventTrace?.enabled[eventKey]) {
          const configs = window.__PRELOAD_STATE__.eventTrace.enabled[eventKey];
          let payloads = data[dataKey];
          switch (dataKey) {
            case 'GA':
            case 'GAAds':
            // 
            case 'GARemarketing':
            // 
            case 'GAR':
              configs.forEach((config) => {
                if (dataKey === 'GA' && config.enableEnhancedEcom && data.GAE) {
                  payloads = data[dataKey].concat(data.GAE);
                }

                payloads.forEach(([track, event, data = {}, scope, ...rest]) => {
                  data = data || {};
                  const { useLegacyCode, traceType } = config;

                  // 
                  if (parseInt(traceType, 10) === 0) return;
                  // useLegacyCode
                  if (useLegacyCode === undefined && dataKey === 'GAR') return;
                  // useLegacyCode=0
                  if (parseInt(useLegacyCode, 10) === 0 && dataKey === 'GARemarketing') return;
                  // useLegacyCode=1
                  if (parseInt(useLegacyCode, 10) === 1 && dataKey === 'GAR') return;
                  // scopescope
                  // scopescope
                  // case: 
                  if ((config.scope || scope) && scope !== config.scope) return;

                  const isDataObj = Object.prototype.toString.call(data) === '[object Object]';

                  if (['GA', 'GARemarketing', 'GAR'].indexOf(dataKey) !== -1 && isDataObj) {
                    data.send_to = `${config.id}`;
                  }

                  if (dataKey === 'GAAds' && isDataObj) {
                    data.send_to = `${config.id}/${config.tag}`;
                  }

                  // 
                  window.gtag(track, event, data, ...rest);
                });
              });
              break;

            case 'FBPixel':
              payloads.forEach((payload) => {
                const [action, eventName, customData = {}, extData = {}, ...rest] = payload;
                //   if (eventName === 'PageView' && !extData.eventId) {
                //     extData.eventID = window.__PRELOAD_STATE__?.serverEventId;
                //   }
                window.fbq(action, eventName, customData, extData, ...rest);
              });
              break;
            default:
              break;
          }
        }
      });
      if (data.FBPixel?.[0]) {
        HiidoReport.report(data.FBPixel[0][1], data.FBPixel[0][2], data.FBPixel[0][3], data.FBPixel[0][4]);
      }
    } catch (err) {
      // eslint-disable-next-line no-console
      console.error('global:thirdPartReport err:', err);
    }
  });

  /**
   *  - 
   * data data=[eventId, exitParams]
   * data data={}
   */
  let beforeunloadCallback;
  let getDestPathCallback;
  let sendLock = false;
  window.SL_EventBus.on('global:hdReport:exit', (data) => {
    if (beforeunloadCallback) {
      sendLock = false;
      window.removeEventListener('beforeunload', beforeunloadCallback);
      document.removeEventListener('click', getDestPathCallback);
    }

    function report(data, page_dest) {
      if (sendLock) return;
      sendLock = true;

      // eventId
      if (Array.isArray(data)) {
        const [eventId, params] = data;
        window.HdSdk?.shopTracker.report(eventId, {
          page_dest,
          event_name: '999',
          ...params,
        });
      }
      // data
      if (Object.prototype.toString.call(data) === '[object Object]') {
        window.HdSdk?.shopTracker.collect?.({
          action_type: '999',
          page_dest_url: page_dest,
          ...data,
        });
      }
    }

    beforeunloadCallback = () => {
      report(data, '');
    };

    getDestPathCallback = (event) => {
      const path = composedPath(event);
      for (let i = path.length; i--; ) {
        const element = path[i];
        if (element && element.nodeType === 1 && element.nodeName.toLowerCase() === 'a') {
          if (/^https?:\/\//.test(element.href)) {
            report(data, element.href);
            break;
          }
        }
      }
    };
    window.addEventListener('beforeunload', beforeunloadCallback);
    document.addEventListener('click', getDestPathCallback);
  });

  /**
   *  - 
   */
  window.SL_EventBus.on('global:hdReport:pageview', (...data) => {
    const [eventIdOrData, ...rest] = data;
    // eventId
    if (typeof eventIdOrData == 'string') {
      window.HdSdk?.shopTracker.report(eventIdOrData, ...rest);
    }
    // data
    if (Object.prototype.toString.call(eventIdOrData) === '[object Object]') {
      window.HdSdk?.shopTracker.collect?.(eventIdOrData);
    }
  });

  /**
   *  - FBGApageviewGAAdsconversion
   * userREuser
   */
  const isTradePage = location.pathname.includes('/trade/');
  if (!location.pathname.startsWith('/user/')) {
    window.SL_EventBus.emit('global:thirdPartReport', {
      FBPixel: [
        [
          'track',
          'PageView',
          {},
          {
            eventID: window.__PRELOAD_STATE__?.serverEventId,
          },
        ],
      ],
      GAAds: isTradePage ? [] : [['event', 'conversion', null]],
      GA: [
        [
          'event',
          'page_view',
          {
            page_title: document.title,
            page_location: window.location.href,
            page_path: window.location.pathname + window.location.search,
          },
        ],
      ],
    });
  }

  //  - 
  autoReport();

  //  - 
  reportHeadless();

  // 
  startObserver();

  // 
  clickCollect();
}

/**
 *  - 
 * @param {*} el
 * @param {track: object, collect: object} collectObj
 * @param {*} callback
 * @returns
 */
function sendCollect(el, collectObj, callback) {
  while (el) {
    const { dataset } = el;
    collectReportProps(dataset, collectObj);
    el = el.parentNode;
  }
  if (!Object.keys(collectObj).length) return;
  const { track, collect } = collectObj;
  // 
  if (track && Object.keys(track).length) {
    const { eventid, ...params } = track;
    if (eventid) {
      window.HdSdk?.shopTracker.report(eventid, params);
    }
  }
  // 
  if (collect && Object.keys(collect).length) {
    window.HdSdk?.shopTracker.collect?.(collect);
  }
  callback && callback();
}

/**
 *  - 
 * @param {*} dataset
 * @param {*} collectObj
 * @returns
 */
function collectReportProps(dataset, collectObj = {}) {
  if (!dataset) return;
  Object.keys(dataset).forEach((sKey) => {
    ['track', 'collect'].forEach((collectKey) => {
      const value = dataset[sKey];
      if (sKey.indexOf(collectKey) === 0) {
        collectObj[collectKey] = collectObj[collectKey] || {};
        let key = sKey.replace(collectKey, '');
        if (key.startsWith('_')) return;
        key = key.replace(/[A-Z]/g, (letter, index) => {
          return `${index === 0 ? '' : '_'}${letter.toLowerCase()}`;
        });
        if (!collectObj[collectKey].hasOwnProperty(key)) {
          collectObj[collectKey][key] = value;
        }
      }
    });
  });
}

/**
 *  - 
 * @param {*} options
 */
export function collectObserver(options) {
  [].forEach.call(document.querySelectorAll(options.selector), (el) => {
    window.SL_Report?.HdObserver?.observe(el);
  });
}

/**
 *  - 
 * @param {*} options
 */
export function startObserver(options) {
  options = Object.assign(
    {
      selector: `.${EXPOSE_CLASSNAME}, .${COLLECT_EXPOSE_CLASSNAME}`,
    },
    options,
  );
  if (options.reset) {
    window.SL_Report.HdObserverSet = new WeakSet();
  }
  window.SL_Report?.HdObserver?.disconnect();
  if (document.readyState === 'complete') {
    collectObserver(options);
  } else {
    document.addEventListener('DOMContentLoaded', () => {
      collectObserver(options);
    });
  }
}

/**
 *  - 
 */
export function clickCollect() {
  if (!window.SL_Report?.__clickCollectCallback) {
    window.SL_Report = window.SL_Report || {};
    window.SL_Report.__clickCollectCallback = (ev) => {
      if (ev.target.classList?.contains(CLICK_CLASSNAME) || ev.target.classList?.contains(COLLECT_CLICK_CLASSNAME)) {
        let collectObj = {};
        sendCollect(ev.target, collectObj);
        window.SL_EventBus.emit('global:hdReport:click', ev.target);
      }
    };
  }
  const options = {
    capture: true,
  };
  document.removeEventListener('click', window.SL_Report.__clickCollectCallback, options);
  document.addEventListener('click', window.SL_Report.__clickCollectCallback, options);
}
