import getEnv from '@yy/sl-theme-shared/utils/get-env';
import { t } from '@yy/sl-theme-shared/utils/i18n';
import { SL_State as state } from '../../../utils/state-selector';

export const isProd = ['product', 'preview'].includes(getEnv().APP_ENV);

export const conversion = (error) => {
  let content;
  switch (true) {
    case /0401/.test(error.code): {
      content = t('paypal.error-expired');
      break;
    }
    case /0402/.test(error.code): {
      content = t('paypal.error-expired');
      break;
    }
    case /0403/.test(error.code): {
      content = t('paypal.error-sold-out');
      break;
    }
    case /0404/.test(error.code): {
      content = t('paypal.error-product-limit');
      break;
    }
    case /0005/.test(error.code): {
      content = t('paypal.error-network-error');
      break;
    }
    case /0703/.test(error.code): {
      content = t('paypal.error-shop-not-exit');
      break;
    }
    default: {
      content = error?.message || t('paypal.error-network-error');
    }
  }

  return content;
};

/**
 * PayPalnacos
 * @returns "{disableFunding:string,}" | "{Array}"
 */
export const getPaypalNacosConfig = () => {
  return window.__ENV__?.PAYPAL_CONFIG_V2;
};

export const getPayPalServerConfig = () => {
  const { enterAccountConfig = {}, ...rest } = state.get('tradeCollectionConfig') || {};
  const intent = enterAccountConfig.manualEnterAccount ? 'authorize' : 'capture';
  const manualCapture = enterAccountConfig.manualEnterAccount;

  return {
    ...rest,
    intent,
    manualCapture,
  };
};

export const getConfigCommit = (config) => {
  return !!config.commit;
};

const paypalConfigKey = ['paypalMerchantId', 'clientId'];
/**
 * getPaypalConfig paypalMerchantIdclientId
 * @returns {Boolean}
 */
export const canTrustedPaypalConfig = (paypalConfig) => {
  const servicesConfig = paypalConfigKey.map((key) => paypalConfig[key]);
  /**
   * paypalConfigKeynull ==false
   * paypalConfigKeynull ==false
   * paypalConfigKeynullnull ==true
   */
  if (servicesConfig.some(Boolean) && !servicesConfig.every(Boolean)) {
    return true;
  }
  return false;
};

export const getQueryParams = (config, { isContinueMode, paypalConfig }) => {
  const { intent } = getPayPalServerConfig();
  const commit = getConfigCommit(config);
  const nacosConfig = getPaypalNacosConfig();

  let queryParams = `intent=${intent}&commit=${commit}`;
  if (config.queryParams) {
    queryParams += `&${config.queryParams}`;
  }

  // paypalConfigmerchant-id
  // https://developer.paypal.com/sdk/js/configuration/#merchant-id
  if (canTrustedPaypalConfig(paypalConfig) && paypalConfig.paypalMerchantId) {
    queryParams += `&merchant-id=${paypalConfig.paypalMerchantId}`;
  }

  // nacosConfig object
  //  https://shopline.yuque.com/hhxcgv/eb6sot/kcklku
  // scope: all-continue-payNow-
  if (Array.isArray(nacosConfig)) {
    /**
     * 
     * @param {{ key: String; value: String; isCoverAll: Boolean; whitelist: Array; blacklist: Array }} item
     */
    const setQueryParams = (item) => {
      const { key, value, isCoverAll, whitelist, blacklist } = item;
      const { storeId } = window.Shopline;

      if (isCoverAll) {
        //  blacklist
        if (Array.isArray(blacklist) && !blacklist.includes(storeId)) {
          queryParams += `&${key}=${value}`;
        }
      } else if (Array.isArray(whitelist) && whitelist.includes(storeId)) {
        //  whitelist
        queryParams += `&${key}=${value}`;
      }
    };

    nacosConfig.forEach((item) => {
      if (item.scope === 'all') {
        setQueryParams(item);
      } else {
        if (isContinueMode && item.scope === 'continue') {
          setQueryParams(item);
        }
        if (!isContinueMode && item.scope === 'payNow') {
          setQueryParams(item);
        }
      }
    });
  } else {
    // todo isv
    const { disableFunding } = window.__ENV__?.PAYPAL_CONFIG || {};
    // eslint-disable-next-line no-lonely-if
    if (isContinueMode) {
      // -object

      if (disableFunding) {
        queryParams += `&disable-funding=${disableFunding}`;
      }
    }
  }

  return queryParams;
};
