import http from '@yy/sl-theme-shared/utils/request';
import SvcAdapter from '../domain/adapter/svc';
import StorageAdapter from '../domain/adapter/storage';
import CartService from './cart';

let initialized = false;

function init() {
  if (initialized) return;
  initialized = true;

  SvcAdapter.withSvc({
    ai: http,
  });
  CartService.withCartService(SvcAdapter.takeSvc(), StorageAdapter.takeStorage());
}

export default {
  init,
};
