import { t } from '@yy/sl-theme-shared/utils/i18n';
import Toast from '../../../shared/components/toast';
import '../../../shared/components/toast/index.scss';
import '../style/sku-stepper.scss';

class SkuStepper {
  constructor({ root, value, min, max, domReady, disabled, onChange }) {
    this.root = root;
    this.data = {
      min,
      max,
      disabled,
      value,
    };
    this.onChange = onChange;
    this.init(domReady);
  }

  init(domReady) {
    if (domReady) {
      this.$stepper = $(this.root);
      this.initEvent();
    } else {
      this.createAndInitDom();
    }
    this.toast = new Toast();
  }

  initEvent() {
    if (this.data.disabled) return;

    this.$stepper.children('.stepper-before').on('click', () => {
      if (this.data.min < this.data.value) {
        this.data.value -= 1;
        this.render();
        window.SL_EventBus.emit('product:quantity:minus', [this.data.value, this.root]);
      }
    });

    this.$stepper.children('.stepper-after').on('click', () => {
      if (this.data.value < this.data.max) {
        this.data.value += 1;
        this.render();
        window.SL_EventBus.emit('product:quantity:add', [this.data.value, this.root]);
      }
    });

    this.$stepper.children('.stepper-input').on('input', (e) => {
      const filerValue = e.target.value.replace(/[^\d]/g, '');
      const value = filerValue ? Number(filerValue) : filerValue;
      this.data.value = value;
      this.render();
    });

    this.$stepper.children('.stepper-input').on('blur', (e) => {
      const value = Number(e.target.value);
      let overStockLimit = 0;
      let isReset = 1;

      if (value > this.data.max) {
        this.data.value = this.data.max;
        if (!this.data.disabled) {
          overStockLimit = 1;
          this.toast.open(t('common.stock-limit', { stock: this.data.max }), 1000);
        }
      } else if (this.data.min > value) {
        this.data.value = this.data.min;
      } else  {
        isReset = 0;
      }
      window.SL_EventBus.emit('product:quantity:modify', [this.data.value, overStockLimit, this.root]);

      // 
      if (isReset) {
        this.render();
      }
    });
  }

  createAndInitDom() {
    $(this.root).html(`<div>stepper</div>`);
  }

  setSingleDisabled(position, disabled) {
    if (disabled) {
      this.$stepper.children(`.stepper-${position}`).addClass('disabled');
    } else {
      this.$stepper.children(`.stepper-${position}`).removeClass('disabled');
    }
  }

  setStepperDisabled() {
    if (this.data.disabled) {
      this.$stepper.addClass('disabled');
    } else {
      this.$stepper.removeClass('disabled');
    }
  }

  setStepperData(obj) {
    this.data = { ...this.data, ...obj };
    this.render();
  }

  render() {
    // 
    if (this.data.value) {
      this.setSingleDisabled('before', this.data.min >= this.data.value);
      this.setSingleDisabled('after', this.data.max <= this.data.value);
    }
    this.$stepper.children('.stepper-input').val(this.data.value);
    this.onChange(this.data.value);
  }
}

export default SkuStepper;
