/**
 * @fileoverview toast
 * toast
 * 1
 * 2sku
 */
import Toast from '../shared/components/toast';
import { t } from '../../../utils/i18n';

const EVENT_BUS = {
  /** + */
  QUANTITY_ADD_EVENT: 'product:quantity:add',
  /** - */
  QUANTITY_MINUS_EVENT: 'product:quantity:minus',
  /**  */
  QUANTITY_MODIFY_EVENT: 'product:quantity:modify',
  /**  */
  SKU_INIT_EVENT: 'product:sku:init',
  /** sku tab */
  SKU_CHANGE_EVENT: 'product:sku:change',
};

const TOAST_TYPE = {
  // 
  ACTIVE_PURCHASE_LIMIITED: 1,
  // 
  PRODUCT_PURCHASE_LIMIITED: 2,
  // 
  ACTIVE_NOSTOCK: -1,
};

const template = (saleBuyLimitConfig) => {
  switch (saleBuyLimitConfig?.userLimitedType) {
    case TOAST_TYPE.ACTIVE_PURCHASE_LIMIITED:
      return t('productDetail.activityToast.product-limit', {
        stock: saleBuyLimitConfig?.acquirePerUserLimit > 0 ? saleBuyLimitConfig?.acquirePerUserLimit : '0',
      });
    case TOAST_TYPE.PRODUCT_PURCHASE_LIMIITED:
      return t('productDetail.activityToast.price-limit', {
        num: saleBuyLimitConfig?.acquirePerUserLimit > 0 ? saleBuyLimitConfig?.acquirePerUserLimit : '0',
      });
    case TOAST_TYPE.ACTIVE_NOSTOCK:
      return t('productDetail.activityToast.title-limit');
    default:
      return '';
  }
};

const defaultOption = {
  // skukey
  id: '',
  // 
  productInfo: {},
};
class FlashSale {
  constructor(option = {}) {
    this.option = { ...defaultOption, ...option };
    this.option.productInfo[this.option.id] = {};
    this.init();
  }

  init() {
    this.toast = new Toast();
    this.bindEventListener();
  }

  bindEventListener() {
    window.SL_EventBus.on(EVENT_BUS.QUANTITY_ADD_EVENT, ([value, selector]) => {
      if (this.checkData({ selector })) {
        this.getProductNum(value);
        this.showTips(value);
      }
    });

    window.SL_EventBus.on(EVENT_BUS.QUANTITY_MINUS_EVENT, ([value, selector]) => {
      if (this.checkData({ selector })) {
        this.getProductNum(value);
        this.showTips(value);
      }
    });

    window.SL_EventBus.on(EVENT_BUS.QUANTITY_MODIFY_EVENT, ([value, overStockLimit, selector]) => {
      if (this.checkData({ selector })) {
        this.getProductNum(value);
        if (!overStockLimit) {
          this.showTips(value);
        }
      }
    });

    window.SL_EventBus.on(EVENT_BUS.SKU_INIT_EVENT, ([sku, id]) => {
      if (this.checkData({ id })) {
        this.dataProcess(sku);
      }
    });

    window.SL_EventBus.on(EVENT_BUS.SKU_CHANGE_EVENT, ([sku, id]) => {
      if (this.checkData({ id })) {
        this.dataProcess(sku);
        this.showTips(this.option.productInfo[this.option.id].productNum);
        this.compareStock(sku);
      }
    });
  }

  /**
   * @des 
   */
  compareStock(sku) {
    if (sku?.stock <= this.option.productInfo[this.option.id].productNum) {
      this.option.productInfo[this.option.id].productNum = sku.stock;
    }
  }

  /**
   * @des id
   * @param data {Object}
   */
  checkData(data) {
    if (data?.id === this.option.id || data?.selector?.attr('id')?.indexOf(this.option.id) > -1) {
      return true;
    }

    return false;
  }

  /**
   * @des 
   * @param value {Number} 
   */
  getProductNum(value) {
    this.option.productInfo[this.option.id].productNum = value;
  }

  /**
   * @des sku
   * @param sku {Object} sku
   */
  dataProcess(sku) {
    const prdInfo = this.option.productInfo[this.option.id];

    if (!sku?.saleActivityResponseList) {
      this.option.productInfo[this.option.id] = {};
      return;
    }

    sku?.saleActivityResponseList?.forEach((item) => {
      if (item?.promotionType === 1 && item?.promotionSubType === 1) {
        // item?.saleBuyLimitConfig?.allowBuyOverLimit
        this.option.productInfo[this.option.id].activeTip = !item?.saleBuyLimitConfig?.allowBuyOverLimit
          ? template(item?.saleBuyLimitConfig)
          : '';
        prdInfo.promotionRemainStock = item?.skuPromotionProduct?.promotionRemainStock;
        prdInfo.userRemainBuyCount = item?.skuPromotionProduct?.userRemainBuyCount;
      }
    });
    prdInfo.stock = sku?.stock;
  }

  /**
   * @des 
   * @param data {Object} 
   */
  showTips(value) {
    const prdInfo = this.option.productInfo[this.option.id];

    // 
    if (prdInfo?.activeTip && prdInfo?.promotionRemainStock !== -1 && prdInfo?.promotionRemainStock < value) {
      this.toast.open(template({ userLimitedType: -1 }));
    } else if (
      prdInfo?.activeTip &&
      // 
      prdInfo?.userRemainBuyCount < value
    ) {
      this.toast.open(prdInfo?.activeTip);
    }
  }
}

export default FlashSale;
