import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import { bem, visibleClassName, animationClassMap, disablePageScroll, enablePageScroll, HIDDEN, VISIBLE, DEFAULT_MODAL_ID_PRE, maskClosableClass } from './common';

export default class Modal {
  constructor({ modalId = '' } = {}) {
    this.modalId = `${DEFAULT_MODAL_ID_PRE}${modalId}`;
    this.$modal = $(`#${this.modalId}`);
    this.$modalBody = this.$modal.find(`.${bem('body')}`);
    this.$modalContainer = this.$modal.find(`.${bem('container')}`);
    this.isMobile = SL_State.get('request.is_mobile');
    this.maskClosable = this.$modal.data('maskclosable');
    this.visibleState = HIDDEN;
    this.eventsBinded = false;
    this.init();
  }

  init() {
    if (!this.eventsBinded) {
      this.bindEvents();
      this.eventsBinded = true;
    }
  }

  show() {
    this.visibleState = VISIBLE;
    disablePageScroll(this.$modalBody.get(0));
    this.$modal.addClass([visibleClassName, animationClassMap.visible]).removeClass(animationClassMap.hidden);
    this.toggleMaskClassName();
  }

  hide(force) {
    this.visibleState = HIDDEN;
    enablePageScroll(this.$modalBody.get(0));
    this.toggleMaskClassName();
    this.$modal.addClass(animationClassMap.hidden).removeClass(animationClassMap.visible);
    if (force) {
      this.afterAnimation();
    }
  }

  toggleMaskClassName() {
    if (this.maskClosable) {
      this.$modal.find(`.${bem('mask')}`).toggleClass(maskClosableClass, this.visibleState === VISIBLE);
    }
  }

  afterAnimation() {
    this.$modal.toggleClass(visibleClassName, this.visibleState === VISIBLE);
  }

  bindEvents() {
    // close with close button
    this.$modal.on('click', `.${bem('close')}`, this.hide.bind(this, false));

    if (this.isMobile) {
      this.$modal.on('touchstart', `.${bem('close')}`, this.hide.bind(this, false));
    }

    // close with mask layer
    if (this.maskClosable) {
      this.$modal.on('click', `.${bem('mask')}`, this.hide.bind(this, false));
    }

    this.$modalContainer.on('animationend', this.afterAnimation.bind(this));
  }
}
