import { OPEN_MINI_CART } from '../../interior-event';
import apiLogger from '../../../utils/api-logger';

/**
 * 
 * 
 * @param 
 * @param {
 *    initiator: 'POD'
 *  },
 * @param {
 *    onSuccess?: () => void,
 *    onError?: (resError) => void,
 *    onComplete?: () => void,
 *  }
 *
 * @type resError {
 *  message: string,
 * }
 *
 */
const EVENT_NAME = 'Cart::NavigateCart';

const logger = apiLogger(EVENT_NAME);

const interior = window?.SL_EventBus;
const external = window?.Shopline.event;
const navigateCartHandler = (argument) => {
  const noop = () => {};
  const data = argument?.data || {};
  const onSuccess = argument?.onSuccess || noop;
  const onError = argument?.onError || noop;
  try {
    interior.emit(OPEN_MINI_CART, { data, onSuccess });
    logger.log(data);
    onSuccess?.();
  } catch (error) {
    onError?.(error);
  }
};

const navigateCart = () => external?.on(EVENT_NAME, navigateCartHandler);
navigateCart.apiName = EVENT_NAME;
export default navigateCart;
