import apiLogger from '../../utils/api-logger';
import { ADD_TO_CART } from '../enum';

const logger = apiLogger(`${ADD_TO_CART} - EMIT`);

const external = window?.Shopline.event;

const addToCart = (data) => {
  logger.info(data);
  return external.emit(ADD_TO_CART, {
    data,
    interior: ADD_TO_CART,
    onSuccess: (result) => {
      logger.info('success', result);
    },
    onError: (error) => {
      logger.error(error);
    },
  });
};

addToCart.apiName = ADD_TO_CART;

export default addToCart;
