import querystring from 'querystring';
import { getUrlAllQuery } from '../../../shared/utils/url';

const onSortChange = () => {
  $('#collectionsAjaxInner').delegate('#collection-sort', 'change', function () {
    const newQueryObj = { ...getUrlAllQuery(), sort_type: $(this).val() };
    const newQueryStr = querystring.stringify(newQueryObj);
    // eslint-disable-next-line no-restricted-globals
    setTimeout(() => {
      window.location.href = `${window.location.origin + window.location.pathname}?${newQueryStr}`;
    }, 0);
  });
};

onSortChange();
