import { isBoolean } from 'lodash';
import apiLogger from '../../../utils/api-logger';
import * as externalEvent from '../../enum';
import * as interiorEvent from '../../interior-event';

const logger = apiLogger(externalEvent.USE_CART);

const interior = window?.SL_EventBus;
const external = window?.Shopline.event;

const sideBarStatusEnum = ['open', 'close', 'none'];

// * check './README.md' plz
const useCart = () =>
  external?.on(externalEvent.USE_CART, async (argument) => {
    const options = argument?.options || null;
    const onSuccess = argument?.onSuccess || (() => {});
    const onError = argument?.onError || (() => {});
    try {
      logger.info(`[emit]`, interiorEvent.USE_CART);
      //  
      if (
        options &&
        sideBarStatusEnum.includes(options.switchSideBar) &&
        isBoolean(options.updateState) &&
        isBoolean(options.rerenderDom) &&
        isBoolean(options.cartDetail)
      ) {
        interior.emit(interiorEvent.USE_CART, { options, success: onSuccess, error: onError });
        return;
      }

      // * 
      console.warn('USE_CART EVENT WARN: params error, check it please');
    } catch (error) {
      onError?.(error);
    }
  });

useCart.apiName = externalEvent.USE_CART;
export default useCart;
