import axios from 'axios';
import { t } from '@yy/sl-theme-shared/utils/i18n';
import http from '@yy/sl-theme-shared/utils/request';
import { getEventID } from '@yy/sl-theme-shared/utils/report/tool';
import pageMapping from '@yy/sl-theme-shared/utils/report/pageMapping';
import currency from '@yy/sl-theme-shared/utils/currency';
import dataReportViewContent from '@yy/sl-theme-shared/events/data-report/view-content';
import productSkuChange from '@yy/sl-theme-shared/events/product/sku-change';
import quickViewClick from '@yy/sl-theme-shared/events/product/quickView-click';
import { ModalWithHtml } from '../../../shared/components/modal';
import Toast, { Loading } from '../../../shared/components/toast';
import { getUrlQuery } from '../../../shared/utils/url';
import ButtonEvent from '../../detail/js/product-button';
import { addToCartThirdReport, addToCartHdReport } from '../../detail/js/product-button-report';
import SkuQuality from '../../detail/js/product-quantity';
import initSku from '../../detail/js/sku-trade';
import setProductPrice from './product-info';
import { ADD_TO_CART } from '../../../shared/cart/globalEvent';
import '../style/quick-add-modal.scss';

const { formatCurrency } = currency;

const emitProductSkuChange = (data) => {
  try {
    productSkuChange({ ...data, currency: window?.SL_State?.get('storeInfo.currency') });
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error(e);
  }
};
const emitViewContent = (data) => {
  try {
    dataReportViewContent(data);
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error(e);
  }
};

const modalInstanceMap = new Map();
// dom
const previewInstanceMap = new Map();

const quickAddLoadingClassName = 'product-item__btn--loading';

function modalExpose(page) {
  window.HdSdk?.shopTracker.report('60006263', {
    event_name: 'view',
    page,
  });
}

const getSkuChangeData = (skuInfo = {}) => {
  const { spuSeq, discount, skuSeq, price, originPrice, stock, weight, weightUnit, available, shelves, skuAttributeIds, imageList, soldOut, allowOversold, imageBeanList } =
    skuInfo;
  return {
    spuSeq,
    discount,
    skuSeq,
    price: formatCurrency(price || 0),
    originPrice: formatCurrency(originPrice || 0),
    stock,
    weight,
    weightUnit,
    available,
    shelves,
    skuAttributeIds,
    imageList,
    soldOut,
    allowOversold,
    imageBeanList,
  };
};

export default async function quickAddModal(data) {
  const { spuSeq, uniqueKey, $button } = data;
  let modalPrefix = 'product_quick_add_';
  let queryObj = {};
  const query = $button.data('query');

  try {
    queryObj = { ...query };

    modalPrefix = queryObj.modalPrefix ? `${queryObj.modalPrefix}_product_quick_add_` : 'product_quick_add_';
  } catch (e) {
    console.log(e);
  }

  const page = modalPrefix.startsWith('productRecommendModal') ? '123' : pageMapping[window.SL_State.get('templateAlias')];

  function toggleAddLoading(isLoading) {
    $button.toggleClass(quickAddLoadingClassName, isLoading);
  }

  if ($button.hasClass(quickAddLoadingClassName)) {
    return;
  }

  try {
    toggleAddLoading(true);
    // 
    const res = await getProductDetail(spuSeq);

    if (res.code === 'SUCCESS') {
      const productInfo = res.data;
      const skuList = res.data?.sku?.skuList ?? [];
      const isSoldOut = res.data?.spu?.soldOut;
      const isSingleSku = Array.isArray(skuList) && skuList.length === 1;

      if (isSoldOut) {
        new Toast().open(t('common.soldOut'), 3e3);
        return;
      }

      modalExpose(page);

      if (isSingleSku) {
        // 
        const skuInfo = skuList[0];
        addToCart({ sku: skuInfo, spu: productInfo?.spu, toggleAddLoading, hdReportPage: page });
        // sku
        emitProductSkuChange({
          type: 'init',
          quantity: 1,
          ...getSkuChangeData(skuInfo),
        });
        emitViewContent({
          content_spu_id: productInfo?.spu?.spuSeq,
          content_sku_id: skuInfo?.skuSeq,
          content_category: '',
          currency: window?.SL_State?.get('storeInfo.currency'),
          value: formatCurrency(skuInfo?.price || 0),
          quantity: 1,
          price: formatCurrency(skuInfo?.price || 0),
        });
      } else {
        showModal({ spuSeq, uniqueKey, modalPrefix });
      }
    } else {
      new Toast().open(t('common.productError'), 3e3);
    }
  } catch (err) {
    new Toast().open(t('common.productError'), 3e3);
  } finally {
    toggleAddLoading(false);
  }
}

async function showModal({ spuSeq, uniqueKey, modalPrefix }) {
  window.lozadObserver.observe();

  if (modalInstanceMap.has(spuSeq)) {
    modalInstanceMap.get(spuSeq)?.show();
    previewInstanceMap.get(spuSeq)?.emitEvent();
    quickViewClick({
      type: 'change',
      eventName: 'OPEN_QUICKVIEW_ADDTOCART',
      spuSeq,
      modal: modalInstanceMap.get(spuSeq),
      preview: {
        skuTrade: previewInstanceMap.get(spuSeq).skuTrade,
        quantityStepper: previewInstanceMap.get(spuSeq).quantityStepper,
      },
      $el: document.getElementById(modalInstanceMap.get(spuSeq)?.modalId),
    });
  } else {
    const children = $('<div class="quick-add-modal__outerWrapper"></div>');
    const modal = new ModalWithHtml({
      children,
      containerClassName: 'quick-add-modal__container',
      zIndex: 128,
      closeCallback: () => {
        quickViewClick({
          eventName: 'CLOSE_QUICKVIEW_ADDTOCART',
          spuSeq,
          $el: document.getElementById(modalInstanceMap.get(spuSeq)?.modalId),
        });
      },
    });

    modal.show();

    const loading = new Loading({
      target: modal.$modal.find('.mp-modal__body'),
      loadingColor: 'currentColor',
      duration: -1,
    });

    loading.open();

    try {
      // 
      const res = await fetchModalContent(uniqueKey, modalPrefix);
      // 
      children.empty().append(res.data);
      // 
      initQuickAddModal(`${modalPrefix}${spuSeq}`, children, modal, spuSeq);

      modalInstanceMap.set(spuSeq, modal);
      quickViewClick({
        type: 'init',
        eventName: 'OPEN_QUICKVIEW_ADDTOCART',
        spuSeq,
        modal: modalInstanceMap.get(spuSeq),
        preview: {
          skuTrade: previewInstanceMap.get(spuSeq).skuTrade,
          quantityStepper: previewInstanceMap.get(spuSeq).quantityStepper,
        },
        $el: document.getElementById(modalInstanceMap.get(spuSeq)?.modalId),
      });
    } catch (err) {
      new Toast().open(t('common.productError'), 3e3);
      modal.hide();
    } finally {
      loading.close();
      window.SL_EventBus.emit('global.activeIcon.show', {
        type: 'vip',
      });
    }
  }
}

// 
function initQuickAddModal(id, el, modal, spuSeq) {
  const sku = window.SL_State.get(`${id}.sku`);
  const spu = window.SL_State.get(`${id}.spu`);

  initWidgets({ id, sku, spu }, el, modal, spuSeq);
}

function initWidgets({ id, sku, spu }, el, modal, spuSeq) {
  let activeSkuCache = {};
  // 
  const ButtonGroup = new ButtonEvent({
    id,
    cartRoot: `.pdp_add_to_cart_${id}`,
    buyNowRoot: `.pdp_buy_now_${id}`,
    payPayId: `pdp_paypal_${id}`,
    soldOutRoot: `.pdp_sold_out_${id}`,
    spu,
    sku,
    onAddSuccess: () => {
      modal.hide();
    },
  });

  // 
  const quantityStepper = new SkuQuality({
    id,
    sku,
    spu,
    onChange: (num) => {
      ButtonGroup.setActiveSkuNum(num);

      window.SL_EventBus.emit('product:count:change', [num, id]);
    },
  });
  //  sku
  const skuTrade = initSku({
    id,
    sku,
    spu,
    mixins: window.skuMixins,
    onInit: (trade, activeSku) => {
      activeSkuCache = activeSku;
      // skuChangeshared
      let content_sku_id = '';
      let price = null;

      if (activeSku) {
        changeActiveSku(activeSku);
        content_sku_id = activeSku?.skuSeq;
        price = formatCurrency(activeSkuCache?.price || 0);
        emitProductSkuChange({
          type: 'init',
          quantity: quantityStepper?.skuStepper?.data?.value || 1,
          ...getSkuChangeData(activeSku),
        });
      }
      emitViewContent({
        content_spu_id: spu.spuSeq,
        content_sku_id,
        content_category: '',
        currency: window?.SL_State?.get('storeInfo.currency'),
        value: price,
        quantity: 1,
        price,
      });
    },
    onChange: (activeSku) => {
      activeSkuCache = activeSku;
      // skuChangeshared
      // activeskuactiveSku
      if (activeSku) {
        emitProductSkuChange({
          type: 'change',
          quantity: quantityStepper?.skuStepper?.data?.value || 1,
          ...getSkuChangeData(activeSku),
        });
      }

      // sku
      if (!activeSku && !quantityStepper.activeSku) return;
      setProductPrice(id, id, activeSku);
      changeActiveSku(activeSku);
    },
  });
  previewInstanceMap.set(spuSeq, {
    skuTrade,
    quantityStepper,
    emitEvent: () => {
      let content_sku_id = '';
      let price = null;
      if (activeSkuCache) {
        content_sku_id = activeSkuCache?.skuSeq;
        price = formatCurrency(activeSkuCache?.price || 0);
        emitProductSkuChange({
          type: 'init',
          quantity: quantityStepper?.skuStepper?.data?.value || 1,
          ...getSkuChangeData(activeSkuCache),
        });
      }
      emitViewContent({
        content_spu_id: spu.spuSeq,
        content_sku_id,
        content_category: '',
        currency: window?.SL_State?.get('storeInfo.currency'),
        value: price,
        quantity: 1,
        price,
      });
    },
  });

  listenCurrencyCodeChange(id, id, quantityStepper);

  function changeActiveSku(activeSku) {
    ButtonGroup.setActiveSku(activeSku);
    quantityStepper.setActiveSku(activeSku);

    const avaliableButtons = el.find('.product-button-list button:not(.hide), .product-button-list .paypay-button');
    const classNameMap = {
      2: 'two-buttons',
      3: 'three-buttons',
    };
    const matchedClassName = classNameMap[avaliableButtons.length];

    if (matchedClassName) {
      el.find('.quick-add-modal__wrapper').addClass(matchedClassName);
    }
  }
}

// 
function addToCart({ sku, spu, toggleAddLoading, hdReportPage }) {
  const activeSku = sku
    ? {
        ...sku,
        num: 1,
        name: spu.title,
      }
    : null;
  // 
  if (isPreview()) {
    new Toast().open(t('productDetail.previewLinkToast'));
    return;
  }
  if (!activeSku) {
    new Toast().open(t('productList.container.tradePopup.error4'));
    return;
  }
  toggleAddLoading(true);
  const { spuSeq: spuId, skuSeq: skuId, num, name, price } = activeSku;
  const eventID = getEventID();
  const hdReportData = {
    page: hdReportPage,
    spuId,
    skuId,
    name,
    price,
    num,
  };
  window.SL_EventBus.emit(ADD_TO_CART, {
    spuId,
    skuId,
    num,
    price,
    name,
    eventID: `addToCart${eventID}`,
    error: () => {
      addToCartHdReport({
        ...hdReportData,
        event_status: 0,
      });
    },
    success: () => {
      addToCartHdReport({
        ...hdReportData,
        event_status: 1,
      });
      // 
      addToCartThirdReport({
        spuId,
        name,
        price,
        num,
        skuId,
        eventID,
      });
      // this.onAddSuccess?.();
    },
    complete: () => {
      toggleAddLoading(false);
    },
  });
}

function listenCurrencyCodeChange(id, statePath, quantityStepper) {
  window.SL_EventBus.on('global:currency:format', () => {
    setProductPrice(id, statePath, quantityStepper.activeSku);
  });
}

function isPreview() {
  const currentUrl = window.location.pathname;
  return /^[/]preview[/].*/.test(currentUrl);
}

// 
function fetchModalContent(uniqueKey, modalPrefix) {
  let recommendQuery = {};
  if (modalPrefix.startsWith('productRecommendModal')) {
    recommendQuery = { modalPrefix: 'productRecommendModal' };
  }
  return axios.get(`/products/${uniqueKey}`, {
    params: {
      view: 'quick-add-modal',
      preview: getUrlQuery('preview'),
      themeId: getUrlQuery('themeId'),
      ignoreRedirect: getUrlQuery('ignoreRedirect'),
      engineType: getUrlQuery('engineType'),
      sourcePage: window.SL_State.get('templateAlias'),
      ...recommendQuery,
    },
  });
}

// 
function getProductDetail(spuSeq) {
  return http.get(`/product/detail/query`, {
    params: {
      spuSeq,
    },
  });
}
