import constant from './internal/constant';

async function getCart(svc, abandonedOrderMark, abandonedOrderSeq) {
  return svc.request.get(constant.endpointCart, {
    params: {
      abandonedOrderMark,
      abandonedOrderSeq,
    },
  });
}

async function deleteCartItemList(svc, skuList) {
  return svc.request.delete(constant.endpointCartItemRemove, {
    data: skuList,
  });
}

async function putCartItem(svc, skuInfo) {
  return svc.request.put(constant.endpointCart, skuInfo);
}

async function addCartItem(svc, spuId, skuId, num, dataReportReq) {
  return svc.request.post(constant.endpointCart, {
    item: {
      spuId,
      skuId,
      num,
    },
    dataReportReq,
  });
}

async function verifyCartItemList(svc, itemList) {
  return svc.request.post(constant.endpointCartVerify, itemList || []);
}

async function memberPoint(svc, use) {
  return svc.request.get(constant.memberPoint, {
    params: {
      use,
    },
  });
}

export default {
  getCart,
  deleteCartItemList,
  verifyCartItemList,
  putCartItem,
  addCartItem,
  memberPoint,
};
