import { getEventID } from '@yy/sl-theme-shared/utils/report/tool';
import currency from '@yy/sl-theme-shared/utils/currency';
import dataReportAddToCart from '@yy/sl-theme-shared/events/data-report/add-to-cart';

const { formatCurrency } = currency;

export function reportAddToCartEvent(data) {
  try {
    dataReportAddToCart(data);
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error(e);
  }
}
// addtocart
export function addToCartThirdReport({ spuId, name, price, skuId, num, eventID = getEventID(), variant }) {
  window.SL_EventBus.emit('global:thirdPartReport', {
    GA: [
      [
        'event',
        'add_to_cart',
        {
          items: [
            {
              id: skuId,
              name,
              price: formatCurrency(price),
              quantity: num,
              variant,
            },
          ],
        },
      ],
    ],
    GAAds: [
      [
        'event',
        'conversion',
        {
          value: formatCurrency(price * (num || 1)),
          currency: window.SL_State.get('storeInfo.currency'),
        },
        'ADD-TO-CART', // scope
      ],
    ],
    FBPixel: [
      [
        'track',
        'AddToCart',
        {
          content_ids: spuId,
          content_name: name || '',
          content_category: '',
          content_type: 'product_group',
          currency: window.SL_State.get('storeInfo.currency'),
          value: formatCurrency(price),
        },
        {
          eventID: `addToCart${eventID}`,
        },
      ],
    ],
    GAR: [
      [
        'event',
        'add_to_cart',
        {
          value: formatCurrency(price * (num || 1)),
          items: [{ id: skuId, google_business_vertical: 'retail' }],
        },
      ],
    ],
    GARemarketing: [
      [
        'event',
        'add_to_cart',
        {
          ecomm_prodid: skuId,
          ecomm_pagetype: 'cart',
          ecomm_totalvalue: formatCurrency(price * (num || 1)),
        },
      ],
    ],
  });
  reportAddToCartEvent({
    content_spu_id: spuId,
    content_sku_id: skuId,
    content_category: '',
    content_name: name,
    currency: window?.SL_State?.get('storeInfo.currency'),
    price: formatCurrency(price || 0),
    value: formatCurrency(price || 0),
    quantity: num,
  });

  return eventID;
}

// addtocart
export function addToCartHdReport({ spuId, skuId, num, price, name, page, event_status }) {
  window.HdSdk?.shopTracker.report('60006263', {
    page,
    event_name: 'additem',
    event_category: 'cart',
    product_type: 'product',
    product_id: spuId,
    variantion_id: skuId,
    quantity: num,
    price: formatCurrency(price),
    product_name: name,
    event_status,
  });
}

