import BaseSkuTrade from './base-sku-trade';
import '../../style/sku-trade/sku-trade-flatten.scss';

class SkuTrade extends BaseSkuTrade {
  // 
  getAttrValueName(index) {
    return this.getAttrValue(index)?.name;
  }

  initDom() {
    this.root.children('.spec-box').each((_, el) => {
      const box = $(el);
      const index = box.data('index');

      box
        .children('.attr-box')
        .children('.attr-value')
        .each((__, valueEl) => {
          const valueJQ = $(valueEl);
          const i = valueJQ.data('index');

          valueJQ.on('click', () => {
            if (!valueJQ.prop('disabled')) {
              this.clickAttr(index, i);
            }
          });

          const specItem = this.dataPool.attrArray[index];

          if (specItem?.onlyShowAttrImg) {
            valueJQ.tooltip({
              title: specItem.specAttrList[i]?.name,
            });
          }
        });
    });
  }

  createAndInitDom() {
    this.clearRoot();

    const root = this.root.addClass('product-sku-trade');
    this.dataPool.attrArray.forEach((spec, index) => {
      if (!spec.hidden) {
        const specBox = $('<div class="spec-box"></div>');
        specBox.data('index', index);
        specBox.append(`<div class="spec-name body6 ls-30p text-uppercase">${spec.specName}</div>`);

        const attrBox = $('<div class="attr-box body3"></div>');
        specBox.append(attrBox);

        spec.specAttrList.forEach((value, i) => {
          const { imgUrl } = value;
          let valueJQ;
          if (imgUrl) {
            valueJQ = $(`<div class="attr-value with-img"><img class="value-img" src="${imgUrl}"></div>`);

            if (!spec.onlyShowAttrImg) {
              valueJQ.append(`<span class="value-text">${value.name}</span>`);
            } else {
              valueJQ.tooltip({
                title: value.name,
              });
            }
          } else {
            valueJQ = $(`<div class="attr-value"><span>${value.name}</span></div>`);
          }

          valueJQ.data('index', i);
          attrBox.append(valueJQ);

          valueJQ.on('click', () => {
            if (!valueJQ.prop('disabled')) {
              this.clickAttr(index, i);
            }
          });
        });

        root.append(specBox);
      }
    });
  }

  isPreview() {
    const currentUrl = window.location.pathname;
    return /^[/]preview[/].*/.test(currentUrl);
  }

  render() {
    super.beforeUpdate();

    this.root.children('.spec-box').each((_, el) => {
      const boxEl = $(el);
      const valueEls = boxEl.children('.attr-box').children('.attr-value');
      const index = boxEl.data('index');
      const nameItem = this.dataPool.attrArray[index];

      if (nameItem.onlyShowAttrImg) {
        const attrValue = this.getAttrValueName(index);
        if (attrValue) {
          boxEl.find('.spec-name').text(`${nameItem.specName}${attrValue}`);
        } else {
          boxEl.find('.spec-name').text(nameItem.specName);
        }
      }

      valueEls.each((__, el_) => {
        const valueEl = $(el_);
        const i = valueEl.data('index');
        const valueItem = nameItem.specAttrList[i];
        // previewfalse
        const disabled = !this.isPreview() ? this.dataPool.skuUtil.checkSpecAttrDisabled(this.dataPool.currentSpecList, valueItem.id, index) : false;
        const active = this.dataPool.skuUtil.checkSpecAttrActive(this.dataPool.currentSpecList, valueItem.id);
        if (disabled) {
          valueEl.addClass('disabled').prop('disabled', true);
        } else {
          valueEl.removeClass('disabled').prop('disabled', false);
        }
        if (active) {
          valueEl.addClass('active');
        } else {
          valueEl.removeClass('active');
        }
      });
    });

    super.afterUpdate();
  }
}

export default SkuTrade;
