import { t } from '@yy/sl-theme-shared/utils/i18n';
import pageMapping from '@yy/sl-theme-shared/utils/report/pageMapping';
import axios from 'axios';
import quickViewClick from '@yy/sl-theme-shared/events/product/quickView-click';
import { ModalWithHtml } from '../../../../shared/components/modal';
import Toast, { Loading } from '../../../../shared/components/toast';
import initPreview from '../../../detail/js/product-preview';
import { getUrlQuery } from '../../../../shared/utils/url';

import '../../style/preview-modal.scss';

function modalExpose(modalPrefix) {
  window.HdSdk?.shopTracker.report('60006263', {
    event_name: 'view',
    page: modalPrefix === 'productRecommendModal' ? '123' : pageMapping[window.SL_State.get('templateAlias')],
  });
}

function fetchDetail(uniqueKey, params) {
  return axios.get(`/products/${uniqueKey}`, {
    params: {
      ...params,
      view: 'modal',
      preview: getUrlQuery('preview'),
      themeId: getUrlQuery('themeId'),
      ignoreRedirect: getUrlQuery('ignoreRedirect'),
      engineType: getUrlQuery('engineType'),
      sourcePage: window.SL_State.get('templateAlias'),
    },
  });
}

function createContent() {
  return $('<div class="product-preview-modal-content"></div>');
}

const modalMap = {};
const previewMap = {};

function previewModal({ spuSeq, uniqueKey, query }) {
  let modalPrefix = 'productModal_';
  let queryObj = {};

  try {
    queryObj = { ...query };

    modalPrefix = queryObj.modalPrefix || 'productModal_';
  } catch (e) {
    console.log(e);
  }

  if (modalMap[spuSeq]) {
    modalMap[spuSeq]?.show();
    previewMap[spuSeq]?.productEventRepeat();
    modalExpose(modalPrefix);
    quickViewClick({
      type: 'change',
      eventName: 'OPEN_QUICKVIEW_EVENT',
      spuSeq,
      modal: modalMap[spuSeq],
      preview: {
        skuTrade: previewMap[spuSeq].skuTrade,
        quantityStepper: previewMap[spuSeq].quantityStepper,
      },
      $el: document.getElementById(modalMap[spuSeq].modalId),
    });
  } else {
    const children = createContent();

    const modal = new ModalWithHtml({
      children,
      containerClassName: 'product-preview-modal-container',
      bodyClassName: 'product-preview-modal-body',
      zIndex: 128,
      closeCallback: () => {
        quickViewClick({
          eventName: 'CLOSE_QUICKVIEW_EVENT',
          spuSeq,
          $el: document.getElementById(modalMap[spuSeq].modalId),
        });
      },
    });

    modal.show();

    new Loading({
      target: children,
      loadingColor: 'currentColor',
      duration: -1,
    }).open();

    fetchDetail(uniqueKey, queryObj)
      .then((res) => {
        children.html('<div class="product-preview-modal-top-space"></div>').append(res.data);

        modalExpose(modalPrefix);
        window.SL_EventBus.emit('global.activeIcon.show', {
          type: 'vip',
        });

        try {
          const preview = initPreview({
            id: `${modalPrefix}${spuSeq}`,
            statePath: `${modalPrefix}${spuSeq}`,
            offsetTop: 48,
            container: modal.$modal.find('.product-preview-modal-body'),
            onAddSuccess: () => {
              modal.hide();
            },
          });
          modalMap[spuSeq] = modal;
          previewMap[spuSeq] = preview;
          quickViewClick({
            type: 'init',
            eventName: 'OPEN_QUICKVIEW_EVENT',
            spuSeq,
            preview: {
              skuTrade: preview.skuTrade,
              quantityStepper: preview.quantityStepper,
            },
            modal,
            $el: document.getElementById(modalMap[spuSeq].modalId),
          });
        } catch (e) {
          setTimeout(() => {
            throw e;
          });
        }
      })
      .catch(() => {
        new Toast().open(t('common.productError'), 3000);
        modal.hide();
        modal.destroy();
      });
  }

  return modalMap[spuSeq];
}

export default previewModal;
