import PayPal from '../../../../components/paypal';
import apiLogger from '../../../utils/api-logger';

/**
 * paypal
 * 
 * @param 
 * @param {
 *    initiator: 'POD';
 *    pageType?: any;
 *    domId: string;
 *    height?: number;
 *    beforeCreateOrder: () => void;
 *  },
 * @param {
 *    onSuccess?: (resSuccess) => void,
 *    onError?: (resError) => void,
 *    onComplete?: () => void,
 *  }
 *
 * @type resSuccess {
 *  paypal
 * }
 *
 * @type resError {
 *  message: string,
 * }
 *
 */
const EVENT_NAME = 'Paypal::Spb::Render';

const logger = apiLogger(EVENT_NAME);

const external = window?.Shopline.event;
const paypalRenderHandler = async (argument) => {
  const { data = {}, onSuccess, onError } = argument;
  try {
    const paypal = new PayPal(data);
    logger.info(paypal);
    onSuccess?.(paypal);
  } catch (error) {
    logger.error(error);
    onError?.(error);
  }
};

const renderPayPal = () => external?.on(EVENT_NAME, paypalRenderHandler);
renderPayPal.apiName = EVENT_NAME;
export default renderPayPal;
