import stockType from '../../constant/stockType';

function isProductOnSale(vo) {
  if (!vo) return false;
  if (vo.stock > 0) return true;
  return vo.stockType === stockType.OVERSOLD || vo.stockType === stockType.UNLIMITED;
}

function isStockNotLimited(vo) {
  if (!vo) return false;

  return vo.stockType === stockType.OVERSOLD || vo.stockType === stockType.UNLIMITED;
}

export default {
  isProductOnSale,
  isStockNotLimited,
};

