/**  */
import { hidooRp } from './tradehReport';
import { SL_EventBus } from '../event-bus';
import currencyUtil from '../currency';
import { pageMap, cartPage } from './const';

// paypal 
const reportPaypal = (data, cid, ext = {}) => {
  const products = data?.map((item) => ({
    product_id: item.productSeq,
    variantion_id: item.productSku,
    quantity: item.productNum,
    price: currencyUtil.formatNumber(Number(item?.productPrice) || 0).toString(),
    product_name: item.productName,
  }));
  const page = 'cart';
  hidooRp.event({ event_name: 'quick_payment', page, products, ...ext }, cid);
};

// 
const proceedToCheckout = ({ cid, event_status }) => {
  hidooRp.event(
    {
      event_name: 'click_component',
      custom_component: ['proceed_to_checkout'],
    },
    cid,
  );
  hidooRp.event(
    {
      event_name: 'proceed_to_checkout',
      event_status,
    },
    cid,
  );
};

const reportBuyNow = () => {
  SL_EventBus.on('trade:goToCheckout:report', ({ data }) => {
    const { event_status, isCart, stage } = data;
    if (isCart) {
      const cid = pageMap[stage];
      if (cid) {
        proceedToCheckout({ cid, event_status });
      }
    }
  });
};

const quickPayment = () => {
  SL_EventBus.on('trade:spb:report', ({ data }) => {
    const { event_status, product, stage } = data;
    if (cartPage[stage]) {
      const cid = pageMap[stage];
      reportPaypal(product, cid, { event_status });
    }
  });
};

// 
const listenCartReport = () => {
  reportBuyNow();
  quickPayment();
};

export { listenCartReport };
