/**
 * banner 
 * */
import { debounce } from 'lodash';

export default class Banner {
  constructor() {
    this.$container = document.querySelector('.product-list-banner');
    this.$banner = $('#productListBanner-parallax');
    this.$bannerTop = this.$banner?.offset()?.top || 0;
    this.speed = 2;
    this.isInit = false;
    this.isVisible = false;
    this.isParallax = false;
  }

  init() {
    if (this.isInit) {
      this.destory();
    }
    this.isInit = true;

    if (!this.$banner) {
      return;
    }

    this.setSizes();
    this.scrollHandler();
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          this.isVisible = entry.isIntersecting;
          if (this.isVisible) {
            $(window).on('scroll', this.onScroll.bind(this));
          } else {
            $(window).off('scroll', this.onScroll.bind(this));
          }
        });
      },
      { rootMargin: '0px 0px 0px 0px' },
    );

    observer.observe(this.$container);

    $(window).on('resize', this.debounceSetSizes.bind(this));
  }

  debounceSetSizes() {
    return debounce(this.setSizes.bind(this), 250);
  }

  onScroll() {
    if (!this.isVisible) {
      return;
    }
    const isParallax = $('#productListBanner-parallax').attr('data-parallax-scroll');
    if (isParallax !== 'true') {
      return;
    }
    requestAnimationFrame(this.scrollHandler.bind(this));
  }

  scrollHandler() {
    const viewPortHeight = $(window).height();
    const { top, height } = this.$container?.getBoundingClientRect();
    if (!top && !height) return false;
    const movableDistance = viewPortHeight + height;
    const currentDistance = viewPortHeight - top;
    const percent = ((currentDistance / movableDistance) * this.speed * 38).toFixed(2);
    const num = 38 - Number(percent);
    $('#productListBanner-parallax').css('transform', `translate3d(0 , ${-num}% , 0)`);
    // const shiftDistance = (window.scrollY - this.$elTop) * this.speed;
    // $('#productListBanner-parallax').css('transform', `translate3d(0, ${shiftDistance}px, 0)`);
  }

  setSizes() {
    const domTop = document.querySelector('#productListBanner-parallax')?.getBoundingClientRect()?.top || 0;
    this.$elTop = domTop + window.scrollY;
  }

  destory() {
    this.$banner.css('transform', 'none');
    $(window).off('scroll', this.onScroll.bind(this));
    $(window).off('resize', this.debounceSetSizes.bind(this));
    this.isInit = false;
  }
}
