// 
export function formatTime(originTime) {
  const days = originTime / (24 * 3600 * 1000) >= 10 ? Math.floor(originTime / (24 * 3600 * 1000)).toString() : `0${Math.floor(originTime / (24 * 3600 * 1000)).toString()}`; // 
  const leavel = originTime % (24 * 3600 * 1000); // 
  const hours = leavel / (3600 * 1000) >= 10 ? Math.floor(leavel / (3600 * 1000)).toString() : `0${Math.floor(leavel / (3600 * 1000)).toString()}`; // 
  const leavel2 = leavel % (3600 * 1000); // 
  const minutes = leavel2 / (60 * 1000) >= 10 ? Math.floor(leavel2 / (60 * 1000)).toString() : `0${Math.floor(leavel2 / (60 * 1000)).toString()}`; // 
  const leftSeconds = (leavel2 % (60 * 1000)) / 1000; // 
  const seconds = leftSeconds >= 10 ? leftSeconds.toFixed(1) : 0 + leftSeconds.toFixed(1); // 
  return {
    days,
    hours,
    minutes,
    seconds,
  };
}
