import checkout from '../../../../utils/checkout';
import apiLogger from '../../../utils/api-logger';

/**
 * 
 * 
 * @param 
 * @param {
 *    products: Array<{
 *      productSku: string, //sku
 *      productSeq: string, //
 *      productNum: number //
 *    }>;
 *    abandonedOrderSeq?: string;
 *    associateCart?: boolean;
 *    associatePayPal?: boolean;
 *    query?: object;
 *    needReport?: () => void;
 *    onBeforeJump?: () => void;
 *  },
 * @param {
 *    onSuccess?: (resSuccess) => void,
 *    onError?: (resError) => void,
 *  }
 */

const EVENT_NAME = 'Checkout::NavigateCheckout';

const logger = apiLogger(EVENT_NAME);

const external = window?.Shopline.event;
const navigateCheckoutHandler = async (arg) => {
  logger.info(JSON.stringify(arg));
  const { data, onSuccess, onError } = arg;
  const { products, ...rest } = data;
  try {
    const result = await checkout.save(products, rest);
    logger.info(JSON.stringify(result, null, 4));
    onSuccess?.(result);
  } catch (error) {
    logger.info(error);
    onError?.(error);
  }
};

const navigateCheckout = () => external?.on(EVENT_NAME, navigateCheckoutHandler);
navigateCheckout.apiName = EVENT_NAME;
export default navigateCheckout;
