import logger from '@funnyecho/logger';
// eslint-disable-next-line import/no-unresolved
import transports from '@funnyecho/logger/transports';
import sentryTransport from '@funnyecho/logger-sentry';
import envService from './env';

export function withShoplineTrackingTransport() {
  const breadCrumbs = [];
  const addBreadCrumbs = (payload) => {
    breadCrumbs.push(payload);
  };

  const getBreadCrumbs = (_breadCrumbs) => {
    return _breadCrumbs.map((bs) => {
      const { category: type, message, eventId } = bs;
      return {
        type,
        message,
        eventId,
      };
    });
  };

  function bubble() {
    return {
      bubble: (ctx, entry) => {
        const { owner, level, fields } = entry;
        const message = `${entry.message}`;

        const extra = logger.mapFieldList(fields);

        let data = {};

        switch (level) {
          case logger.LevelEnum.error:
          case logger.LevelEnum.fatal:
            data = {
              message,
              breadCrumbs: getBreadCrumbs(breadCrumbs),
              owner,
              extra,
            };

            window.__SL_Track__.error(owner, data);
            breadCrumbs.length = 0;
            break;
          case logger.LevelEnum.info:
            data = {
              message,
              breadCrumbs: getBreadCrumbs(breadCrumbs),
              owner,
              extra,
            };

            window.__SL_Track__.info(owner, data);
            breadCrumbs.length = 0;
            break;
          case logger.LevelEnum.debug:
          default:
            addBreadCrumbs({
              message: `${message} ${Object.keys(extra).length ? JSON.stringify(extra) : ''}`,
              category: 'debug',
              eventId: Date.now(),
            });
            break;
        }
      },
    };
  }
  if (process.env.USE_SENTRY === 'TRACK') {
    return [bubble()];
  }

  return [];
}

export function withStdoutTransport() {
  if (!(envService.appEnvInDevelop() || envService.appEnvInStaging())) return [];

  return [transports.withStdout()];
}

export function withSentryTransport() {
  if (!process.env.USE_SENTRY || process.env.USE_SENTRY === 'SENTRY') {
    if (envService.appEnvInDevelop()) return [];

    return [sentryTransport.withSentryConfig(), sentryTransport.withSentryPort()];
  }

  return [];
}
