import './index.scss';
import isMobile from '../../utils/isMobile';

const listHeightPlusNumMap = {
  Normal: 0,
  SlipIn: -40,
};

const attrs = {
  animationType: 'animation-type',
  relatedId: 'dropdown-related-id',
  platform: 'platform',
  disabled: 'disabled',
};

const defaultOptions = {
  containerSelecter: '[data-dropdown]',
  triggerSelecter: '[data-dropdown-trigger]',
  listSelecter: '[data-dropdown-list]',
  opendClass: 'is-open',
  closedClass: 'is-close',
};

class DropdownList {
  constructor(id) {
    this.id = id;
    this.options = { ...defaultOptions };

    this._initSelector();
  }

  get status() {
    return this._doActionWithCheck(() => {
      return this.container.hasClass(this.options.opendClass);
    }, false);
  }

  get disabled() {
    return this.container.attr(`data-${attrs.disabled}`) === 'true';
  }

  set disabled(disabled) {
    this.container.attr(`data-${attrs.disabled}`, String(!!disabled));
  }

  _initSelector() {
    const { id, options } = this;

    Object.assign(this, getTargetElement($(`#${id}`), options));
  }

  _doActionWithCheck(action, errorReturnValue) {
    if (this.container.length && this.trigger.length && this.dropdownList.length) {
      return action();
    }

    return errorReturnValue ?? this;
  }

  toggle(flag, effect = true) {
    return this._doActionWithCheck(() => {
      const { dropdownList, container, trigger, options } = this;

      const nowOptions = {
        ...options,
        trigger,
        container,
        dropdownList,
      };

      if (effect) closeOtherReleased(nowOptions);
      toggle(nowOptions, flag);
    });
  }
}

function getTargetElement(container, options) {
  const { triggerSelecter, listSelecter } = options;

  const trigger = container.find(triggerSelecter).first();
  const dropdownList = container.find(listSelecter).first();

  return {
    container,
    trigger,
    dropdownList,
  };
}

function toggle(options, flag) {
  const { container, dropdownList, opendClass, closedClass } = options;
  const isDisabled = container.attr(`data-${attrs.disabled}`) === 'true';
  const mustMobile = container.attr(`data-${attrs.platform}`) === 'mobile';
  const isOpen = flag == null ? !container.hasClass(opendClass) : flag;

  if (isDisabled) return;
  if (mustMobile && !isMobile()) return;

  const listAnimationType = dropdownList.data(attrs.animationType);
  const listHeight = dropdownList.prop('scrollHeight') + (isOpen ? listHeightPlusNumMap[listAnimationType || 'Normal'] : 0);
  dropdownList.css('height', listHeight);

  if (!isOpen) {
    setTimeout(() => {
      dropdownList.css('height', '');
    }, 0);
  }
  dropdownList.one('transitionend', (event) => {
    $(event.target).css('height', '');
  });

  if (isOpen) {
    container.addClass(opendClass).removeClass(closedClass);
  } else {
    container.addClass(closedClass).removeClass(opendClass);
  }
}

function closeOtherReleased(options) {
  const { container } = options;
  const attr = attrs.relatedId;
  const relatedId = container.data(attr);

  if (relatedId) {
    $(`[data-${attr}=${relatedId}]`).each((_idx, element) => {
      if (element === container[0]) return;
      toggle(
        {
          ...options,
          ...getTargetElement($(element), options),
        },
        false,
      );
    });
  }
}

// 
$(function () {
  function openHandler(event) {
    const trigger = $(event.currentTarget);
    const container = trigger.parents(defaultOptions.containerSelecter).first();
    const dropdownList = container.find(defaultOptions.listSelecter).first();

    const options = {
      ...defaultOptions,
      relatedId: container.data(attrs.relatedId),
      container,
      trigger,
      dropdownList,
    };

    closeOtherReleased(options);
    toggle(options);
  }

  $('body').on('click', `${defaultOptions.containerSelecter} ${defaultOptions.triggerSelecter}`, openHandler);
});

// 
export default DropdownList;
