// eslint-disable-next-line import/no-unresolved
import Swiper, { EffectFade, Lazy } from 'swiper';
import PhotoSwipe from 'photoswipe/dist/photoswipe.min';
import photoSwipeUiDefault from 'photoswipe/dist/photoswipe-ui-default.min';
import YTPlayer from 'yt-player';
import imgSize from '../../../../utils/img-size';
import photoSwipeHtmlString from './product-photoSwipeHtml';
import imgUrl from '../../shared/utils/imgUrl';
// eslint-disable-next-line import/no-unresolved
import 'swiper/swiper-bundle.css';
import '../style/product-swiper.scss';
import getYouTubeCover from '../../shared/utils/getYouTubeCover';

Swiper.use([EffectFade, Lazy]);

const COLUMN = 'column';

const ROW = 'row';
const WRAP_PC_ID = '.product_pc_productImageContainer';
const WRAP_M_ID = '.product_mobile_productImageContainer';

function handleVideoPlayPause(videoPlayer, type) {
  if (type === 'play') {
    videoPlayer?.play();
  }
  if (type === 'pause') {
    videoPlayer?.pause();
  }
}

function imgOnload(src, cb, failCb) {
  if (!src) return;
  const img = new Image();
  img.onload = () => {
    cb(img);
  };
  img.onerror = () => {
    console.warn('imgOnError...', img);
    failCb(img);
  };
  img.src = src;
}

export default class ProductImages {
  static pcSelectorPrefix = 'execute_productImages_pc';

  static mobileSelectorPrefix = 'execute_productImages_mobile';

  constructor(options) {
    const { selectorId, heightOnChange, swiperConfig } = options || {};
    this.swiperConfig = swiperConfig || {};
    // 
    this.heightChangedCount = 0;
    this.heightOnChange = heightOnChange || null;
    this.slideItems = [];

    if (!selectorId) {
      console.error(`productImagesselectorIdid`);
      $(WRAP_PC_ID).hide();
      $(WRAP_M_ID).hide();
      return;
    }
    const pcWrapperSelector = `.${ProductImages.pcSelectorPrefix}_${selectorId}`;
    const mobileWrapperSelector = `.${ProductImages.mobileSelectorPrefix}_${selectorId}`;
    if ($(`${pcWrapperSelector}`).length === 0 || $(`${mobileWrapperSelector}`).length === 0) {
      console.error(`productImagesselectorIdhtml`);
      $(pcWrapperSelector).hide();
      $(mobileWrapperSelector).hide();
      return;
    }
    const pcWrapper = $(`${pcWrapperSelector}`);
    this.thumbsDirection = pcWrapper.data('thumbs-direction') === 'aside' ? COLUMN : ROW;
    this.showThumbsArrow = String(pcWrapper.data('show-thumbnail-arrow')) === 'true';
    this.productImageScale = String($(`.product_productImageScale_${selectorId}`).val()) === 'true';
    this.productVideoMute = String($(`.product_productVideoMute_${selectorId}`).val()) === 'true';
    this.productVideoLoop = String($(`.product_productVideoLoop_${selectorId}`).val()) === 'true';
    this.mobileWidthRatio = $(mobileWrapperSelector).hasClass('middleWidth') ? 0.75 : 1;
    this.productImageLength = $('.product_productImageLength').val();
    this.id = pcWrapperSelector;
    this.mobileId = mobileWrapperSelector;
    this.mobileSwiper = this.initMobileProductImages(true);
    this.swiper = this.initPcProductImages(true);
    this.swiper?.init();
    this.videoPcPlayer = this.initPcVideo();
    this.videoMobilePlayer = this.initMobileVideo();
    this.videoMobilePlayerStatus = 'pause';
  }

  initPcVideo() {
    const videoPcSelector = `${this.id} .product_youTubeVideoBox`;
    const videoDom = $(videoPcSelector);

    if (videoDom.length > 0) {
      const videoPlayer = new YTPlayer(videoPcSelector, {
        modestBranding: true,
        controls: false,
      });
      const videoId = videoDom.data('video-id');
      videoPlayer.load(videoId);

      // 
      window.SL_EventBus.on('global:popup:close', () => {
        handleVideoPlayPause(videoPlayer, 'pause');
      });
      videoPlayer.on('playing', () => {
        window.SL_EventBus.emit('product:product-play-video');
      });
      if (this.productVideoMute) {
        videoPlayer.mute();
      }
      if (this.productVideoLoop) {
        videoPlayer.on('ended', () => {
          videoPlayer.play();
        });
      }
      return videoPlayer;
    }

    return null;
  }

  initMobileVideo() {
    const videoMDom = $(`${this.mobileId} .swiper-slide`)
      .not('.swiper-slide-duplicate')
      .find('.product_youTubeVideoBox')
      .addClass('product_youTubeMobileVideoBox');
    const videoCoverDom = $(`${this.mobileId} .videoItem`)
      .not('.swiper-slide-duplicate')
      .find('.product_photoSwipe_image');
    if (videoMDom.length > 0) {
      const videoPlayer = new YTPlayer('.product_youTubeMobileVideoBox', {
        modestBranding: true,
        controls: false,
      });
      const videoId = videoMDom.data('video-id');
      videoPlayer.load(videoId);
      videoPlayer.on('playing', () => {
        window.SL_EventBus.emit('product:product-play-video');
      });
      if (this.productVideoMute) {
        videoPlayer.mute();
      }
      if (this.productVideoLoop) {
        videoPlayer.on('ended', () => {
          videoPlayer.play();
        });
      }
      videoCoverDom.on('click', () => {
        if (this.videoMobilePlayerStatus === 'pause') {
          handleVideoPlayPause(videoPlayer, 'play');
          this.videoMobilePlayerStatus = 'play';
        } else {
          handleVideoPlayPause(videoPlayer, 'pause');
          this.videoMobilePlayerStatus = 'pause';
        }
      });
      return videoPlayer;
    }
    return null;
  }

  initPhotoSwipe(slidesWrapID, platform) {
    // 
    if (Number(this.productImageLength) === 0) return;
    const slidesSelector = `${slidesWrapID} .product_productImages`;
    const triggerDom = $(slidesSelector);
    const eventDom = platform === 'mobile' ? '.scaleImageIcon' : '.swiper-slide';
    const self = this;

    self.updatePhotoSwipeItems(slidesWrapID);

    triggerDom.on('click', eventDom, function () {
      if ($(this).hasClass('swiper-slide-duplicate') || !self.slideItems?.length) {
        return;
      }

      const triggerThis = platform === 'mobile' ? $(this).closest('.imageItem') : $(this);
      // mobilesku 
      const isMobileHasActiveSkuImage = triggerThis.find('.product_m_skuImage').length > 0;
      const isVideoSlide = triggerThis.hasClass('videoItem');
      if (isMobileHasActiveSkuImage || isVideoSlide) return;
      const { activeIndex } = platform === 'mobile' ? self.mobileSwiper : self.swiper;
      const mobileIndexByLoop = self.getSwiperIsLoop() ? triggerThis.data('swiper-slide-index') : activeIndex;
      const index = platform === 'mobile' ? mobileIndexByLoop : activeIndex;

      self.handlePhotoSwiper(self.slideItems, index);
    });
  }

  initPcSkuPhotoSwiper() {
    const self = this;
    $(`${this.id} .product_pc_skuImage`).on('click', function () {
      const items = [
        {
          src: $(this).find('.product_photoSwipe_image').attr('data-photoswipe-src'),
          w: 0,
          h: 0,
          el: $(this)[0],
        },
      ];
      self.handlePhotoSwiper(items, 0, false);
    });
  }

  handlePhotoSwiper(items, index, cacheNaturalSize) {
    let pswpElement = document.querySelectorAll('.pswp')[0];
    if (!pswpElement) {
      $('body').append(photoSwipeHtmlString);
      // eslint-disable-next-line
      pswpElement = document.querySelectorAll('.pswp')[0];
    }
    this.openPhotoSwipe(pswpElement, items, index, cacheNaturalSize);
  }

  initMobileSkuPhotoSwiper() {
    const self = this;
    $(`${this.mobileId} .product_m_skuImageBox .scaleSkuImageIcon`).on('click', function () {
      const items = [
        {
          src: $(this).parent().find('.product_photoSwipe_image').attr('data-photoswipe-src'),
          w: 0,
          h: 0,
          el: $(this).parent()[0],
        },
      ];
      self.handlePhotoSwiper(items, 0, false);
    });
  }

  updatePhotoSwipeItems(slidesWrapID) {
    const slidesDom = $(`${slidesWrapID} .product_productImages`)
      .find('.swiper-slide')
      .not('.swiper-slide-duplicate')
      .filter(function () {
        return !$(this).children('.product-detail-empty-image').length;
      });

    const items = [];
    slidesDom.each((index, item) => {
      const imgEl = $(item).find('.product_photoSwipe_image');
      const size = $(item).attr('data-natural-size');
      const transSize = size ? size.split('x') : null;
      items.push({
        src: imgEl.attr('data-photoswipe-src'),
        // msrc: imgEl.attr('data-photoswipe-src'),
        w: transSize ? parseInt(transSize[0], 10) : imgEl.innerWidth(),
        h: transSize ? parseInt(transSize[1], 10) : imgEl.innerHeight(),
        el: item,
      });
    });

    this.slideItems = items;

    // return { items };
  }

  openPhotoSwipe(pswpElement, items, index = 0, cacheNaturalSize = true) {
    const self = this;
    if (items?.length > 1) {
      $('.pswp__button--arrow--left, .pswp__button--arrow--right').show();
    } else {
      $('.pswp__button--arrow--left, .pswp__button--arrow--right').hide();
    }
    const photoSwipeOptions = {
      allowPanToNext: false,
      captionEl: false,
      closeOnScroll: false,
      counterEl: false,
      history: false,
      index,
      pinchToClose: false,
      preloaderEl: false,
      shareEl: false,
      tapToToggleControls: false,
      barsSize: { top: 20, bottom: 20 },
      getThumbBoundsFn(_index) {
        let thumbnailParent;
        if (items[_index].el?.className.indexOf('swiper-slide-duplicate') !== -1) {
          thumbnailParent = $(`${self.mobileId} .swiper-slide`).eq(self.mobileSwiper.activeIndex).get(0);
        } else {
          thumbnailParent = items[_index].el;
        }
        const thumbnail = thumbnailParent.getElementsByClassName('product_photoSwipe_image')[0];
        const pageYScroll = window.pageYOffset || document.documentElement.scrollTop;
        const rect = thumbnail.getBoundingClientRect();
        return {
          x: rect.left,
          y: rect.top + pageYScroll,
          w: rect.width,
        };
      },
    };

    const gallery = new PhotoSwipe(pswpElement, photoSwipeUiDefault, items, photoSwipeOptions);
    gallery.listen('gettingData', function (_index, item) {
      const imgWrapEl = item.el;
      if (!imgWrapEl.getAttribute('data-natural-size')) {
        const img = new Image();
        img.onload = () => {
          const hasWH = imgSize(img.src)?.ratio;
          if (cacheNaturalSize) {
            // 1:1
            const naturalSize = hasWH ? `${img.naturalWidth}x${img.naturalHeight}` : `100x100`;
            imgWrapEl.setAttribute('data-natural-size', naturalSize);
          }
          /* eslint-disable */
          item.w = img.naturalWidth;
          item.h = img.naturalHeight;
          /* eslint-disable */
          // gallery.invalidateCurrItems(); //afterChange
          gallery.updateSize(true);
        };
        img.src = item.src;
      }
    });

    gallery.init();
    gallery.listen('afterChange', () => {
      this.galleryAfterChange(gallery);
    });
  }

  galleryAfterChange(gallery) {
    const currentIndex = gallery.getCurrentIndex();
    this.swiper?.slideTo(currentIndex);
    this.mobileSwiper?.slideToLoop(currentIndex);
  }

  heightOnChangeCb(height) {
    setTimeout(() => {
      this.heightOnChange?.(height);
    }, 200);
  }

  /**
   * slides
   * @param {string} swiperSelector swiper selector dom
   * @param {number} activeIndex swiper activeIndex
   * @param {1 | 0.75} ratio slidepercent
   * @param {string} targetImgSrc img src
   * @returns Promise
   */
  handleProductImagesHeight(swiperSelector, activeIndex, ratio = 1, targetImgSrc) {
    const self = this;
    const selector = swiperSelector;
    this.heightChangedCount++;
    const count = this.heightChangedCount;

    const promise = new Promise((resolve, reject) => {
      if (count !== this.heightChangedCount) {
        resolve();
        return;
      }

      if (activeIndex !== undefined) {
        const currentSlide = $(`${selector} .swiper-slide`).eq(activeIndex);
        //  
        if (!targetImgSrc && currentSlide.find('.product-detail-empty-image').length > 0) {
          const mobileWidthRatio = $(`${self.mobileId}`).css('display') === 'block' ? self.mobileWidthRatio : 1;
          // load error fallback
          const boxWidth = parseInt($(`${selector}`).width() * mobileWidthRatio, 10);
          $(`${selector}`).css('height', boxWidth);
          self.heightOnChangeCb(boxWidth);
          resolve(boxWidth);
          return;
        }
        // tip:  targetImgSrcsku
        if (currentSlide.hasClass('videoItem') && !targetImgSrc) {
          const videoBoxHeight = parseInt(currentSlide.children().innerHeight(), 10);
          $(`${selector}`).css('height', videoBoxHeight);
          self.heightOnChangeCb(videoBoxHeight);
          window.SL_EventBus.emit('product:expose-product-video', currentSlide);
          resolve(videoBoxHeight);
        } else if (currentSlide.hasClass('imageItem') || targetImgSrc) {
          const currentSlideImgNaturalSize = currentSlide.attr('data-natural-size');
          //  img onload
          if (currentSlideImgNaturalSize && !targetImgSrc) {
            const [imgNaturalWidth, imgNaturalHeight] = currentSlideImgNaturalSize.split('x');
            const wrapperHeight = parseInt(
              String(($(`${selector}`)[0].offsetWidth * ratio * imgNaturalHeight) / imgNaturalWidth),
              10,
            );
            $(`${selector}`).css('height', wrapperHeight);
            self.heightOnChangeCb(wrapperHeight);
            resolve(wrapperHeight);
            return;
          }

          // dom swiperslides swiperslides
          const currentSlideImg = currentSlide.find('img');
          const imgSrc = targetImgSrc || currentSlideImg.attr('src') || currentSlideImg.attr('data-src');

          if (!targetImgSrc && $(`${self.id}`).css('display') !== 'none') {
            const slideHeight = parseInt(currentSlide.innerHeight(), 10);
            $(`${selector}`).css('height', slideHeight);
            self.heightOnChangeCb(slideHeight);
            resolve(slideHeight);
            return;
          }

          imgOnload(
            imgSrc,
            (img) => {
              if (count !== this.heightChangedCount) {
                resolve();
                return;
              }

              if (!img) {
                reject(new Error('img'));
                return;
              }
              const height = parseInt(String(($(`${selector}`)[0].offsetWidth * ratio * img.height) / img.width), 10);
              $(`${selector}`).css('height', height);
              self.heightOnChangeCb(height);
              resolve(height);
            },
            () => {
              if (count !== this.heightChangedCount) {
                resolve();
                return;
              }
              const mobileWidthRatio = $(`${self.mobileId}`).css('display') === 'block' ? self.mobileWidthRatio : 1;
              // load error fallback
              const boxWidth = parseInt($(`${selector}`).width() * mobileWidthRatio, 10);
              $(`${selector}`).css('height', boxWidth);
              if (targetImgSrc && selector.indexOf(ProductImages.pcSelectorPrefix) !== -1) {
                // pcsku
                $(`${this.id} .product_pc_skuImage`).addClass('imageItemError');
              } else if (targetImgSrc && selector.indexOf(ProductImages.mobileSelectorPrefix) !== -1) {
                // msku
                const currentSlideDom = self.getMobileCurrentSlideDom();
                currentSlideDom.find('.product_m_skuImageBox').addClass('imageItemError');
              } else {
                // 
                currentSlide.addClass('imageItemError');
              }
              self.heightOnChangeCb(boxWidth);
              resolve(boxWidth);
            },
          );
        } else {
          reject(new Error(`slide,currentSlide:${currentSlide},activeIndex:${activeIndex}`));
        }
      } else {
        reject(new Error(`activeIndex${activeIndex}`));
      }
    });
    return promise;
  }

  /**
   * thumbs swiper
   * @param {number} height
   */
  setColumnThumbsSwiperHeight(height) {
    $(`${this.id} .product_thumbsColumnContainer .productImageThumbs`).css({ maxHeight: `${height}px` });
  }

  /**
   * thumbs swiper  thumbs swiper
   * @param {number} activeIndex swiperactiveIndex
   * @param {number} slideLen slides
   * @param {number} wrapperHeight swiper
   */
  handleThumbsArrow(activeIndex, slideLen, wrapperHeight) {
    const selectorPrefix = this.id;
    const { thumbsDirection: direction, showThumbsArrow } = this;
    if (!showThumbsArrow) {
      this.setColumnThumbsSwiperHeight(wrapperHeight);
      return;
    }
    $(`${selectorPrefix} .thumbsArrowTop,${selectorPrefix} .thumbsArrowBottom`).show();
    if (activeIndex === 0) {
      $(`${selectorPrefix} .thumbsArrowTop`).hide();
      if (direction === COLUMN) {
        this.setColumnThumbsSwiperHeight(`${wrapperHeight - 25}`);
      }
    }
    if (activeIndex + 1 === slideLen) {
      $(`${selectorPrefix} .thumbsArrowBottom`).hide();
      if (direction === COLUMN) {
        this.setColumnThumbsSwiperHeight(`${wrapperHeight - 25}`);
      }
    } else {
      if (direction === COLUMN) {
        this.setColumnThumbsSwiperHeight(`${wrapperHeight - 50}`);
      }
    }
  }

  /**
   * product main swiperthumbs swiper
   * @param {url} targetImageUrl url sku image
   */
  async handleEffectSwiperHeight(targetImageUrl) {
    const { swiper } = this;
    const { slides, activeIndex } = swiper || {};
    const height = await this.handleProductImagesHeight(
      `${this.id} .product_productImages`,
      activeIndex,
      1,
      targetImageUrl,
    );
    // columnthumbs swiper
    if (height) {
      this.handleThumbsArrow(activeIndex, slides.length, height);
    }
  }

  /**
   * pcsku
   * @param {boolean} isShow 
   * @param {string} skuImageUrl  skuurl
   */
  togglePcSkuImage(isShow, skuImageUrl) {
    const skuImageDom = $(`${this.id} .product_pc_skuImage`);
    const currentIndex = $(`${this.id}`).attr('data-index');
    if (skuImageDom.hasClass('imageItemError')) {
      skuImageDom.removeClass('imageItemError');
    }
    if (isShow && skuImageUrl) {
      skuImageDom.show().html(
        `<img class="product_photoSwipe_image" data-photoswipe-src=${imgUrl(skuImageUrl, {
          width: 1800,
        })} src=${skuImageUrl} />`,
      );
      $(`${this.id} .productImageThumbsWrapper .thumbsImageItem`).eq(currentIndex).removeClass('active');
    } else if (!isShow) {
      skuImageDom.hide().empty();
      $(`${this.id} .productImageThumbsWrapper .thumbsImageItem`).eq(currentIndex).addClass('active');
    }
  }

  /**
   * pcsku
   * @param {*} isShow 
   * @param {*} skuImage sku image
   */
  handlePcSkuImage(isShow, skuImage) {
    if (!this.swiper) return;
    if (isShow) {
      this.handleEffectSwiperHeight(skuImage);
      this.togglePcSkuImage(true, skuImage);
    } else {
      this.togglePcSkuImage(false);
      this.handleEffectSwiperHeight();
    }
  }

  /**
   * thumbs item
   * @param {scrollTop | scrollLeft} type 
   * @param {number} distance 
   */
  handleThumbsScroll(type, distance) {
    // tips 200ms transition (hack)
    if (type === 'scrollTop') {
      setTimeout(() => {
        $(`${this.id} .product_thumbsColumnContainer .productImageThumbs`).scrollTop(distance);
      }, 200);
    } else if (type === 'scrollLeft') {
      setTimeout(() => {
        $(`${this.id} .product_thumbsRowContainer .productImageThumbs`).scrollLeft(distance);
      }, 200);
    }
  }

  /**
   * thumbs item ( )
   * @param {top|left} type 
   * @param {number} index thumbs slideindex
   * @returns
   */
  getThumbsPosition(type, index) {
    const columnThumbsListDom = $(`${this.id} .product_thumbsColumnContainer .thumbsImageItem`);
    const rowThumbsListDom = $(`${this.id} .product_thumbsRowContainer .thumbsImageItem`);
    if (type === 'top') {
      const prevThumbsItem = columnThumbsListDom.eq(index > 0 ? index - 1 : 0);
      const prevThumbsItemHalfHeight = parseInt(prevThumbsItem.innerHeight() / 2, 10);
      return columnThumbsListDom.eq(index).position().top - prevThumbsItemHalfHeight - 20;
    }
    if (type === 'left') {
      const prevThumbsItem = rowThumbsListDom.eq(index > 0 ? index - 1 : 0);
      const prevThumbsItemHalfWidth = parseInt(prevThumbsItem.innerWidth() / 2, 10);
      return rowThumbsListDom.eq(index).position().left - prevThumbsItemHalfWidth - 20;
    }
  }

  /**
   * thumbs arrow 
   */
  initHandleProductImagesArrow() {
    if (!this.showThumbsArrow) return;
    const { thumbsDirection: direction } = this;
    $(`${this.id} .arrowTop`).on('click', () => {
      const { activeIndex } = this.swiper;
      this.swiper.slidePrev();
      if (direction === COLUMN) {
        const scrollTopDistance = this.getThumbsPosition('top', activeIndex - 1);
        this.handleThumbsScroll('scrollTop', scrollTopDistance);
      } else if (direction === ROW) {
        const scrollLeftDistance = this.getThumbsPosition('left', activeIndex - 1);
        this.handleThumbsScroll('scrollLeft', scrollLeftDistance);
      }
    });
    $(`${this.id} .arrowBottom`).on('click', () => {
      const { activeIndex } = this.swiper;
      this.swiper.slideNext();
      if (direction === COLUMN) {
        const scrollTopDistance = this.getThumbsPosition('top', activeIndex + 1);
        this.handleThumbsScroll('scrollTop', scrollTopDistance);
      } else if (direction === ROW) {
        const scrollLeftDistance = this.getThumbsPosition('left', activeIndex + 1);
        this.handleThumbsScroll('scrollLeft', scrollLeftDistance);
      }
    });
  }

  /**
   * pc thumbs item
   * @param {*} index
   */
  handlePcThumbsActive(index) {
    $(`${this.id} .productImageThumbsWrapper .thumbsImageItem`).removeClass('active').eq(index).addClass('active');
    $(`${this.id}`).attr('data-index', index); // thumbs activeindex
  }

  /**
   * thumbs swiper
   * @param {*} swiper
   * @param {column | row} direction thumbs 
   */
  async initThumbsSwiper(firstInit) {
    const { thumbsDirection } = this;
    const self = this;
    const bindClickDom = $(`${this.id} .product_thumbs${thumbsDirection === COLUMN ? 'Column' : 'Row'}Container `);
    bindClickDom.on('click', '.thumbsImageItem', function () {
      const index = $(this).index();
      self.swiper.slideTo(index);
      // thumb itemsku image (tips: slideChange thumbs slideChange)
      if ($(`${self.id} .product_pc_skuImage`).css('display') === 'block') {
        self.handlePcSkuImage(false);
        $(this).addClass('active');
      }
    });

    //  ssrsku image
    const skuImageDom = $(`${this.id} .product_pc_skuImage`).find('img');
    const skuImage = skuImageDom.attr('data-src') || skuImageDom.attr('src');
    if (!skuImage) {
      this.handlePcThumbsActive(self.swiper.activeIndex); // thumbs active
    }
    await this.handleEffectSwiperHeight(skuImage ? skuImage : null); // swiper
    // SSRsku
    if (skuImage && firstInit) {
      const firstActiveImg = $(`${this.id}`).find('.swiper-slide-active img');
      if (firstActiveImg?.length > 0) {
        const src = firstActiveImg.attr('src') || firstActiveImg.attr('data-src');
        const ratio = imgSize(src)?.ratio || '100%';
        firstActiveImg.parent().css('paddingBottom', ratio);
      }

      const emptyDom = $(`${this.id}`).find('.product-detail-empty-image');
      console.log('emptyDom', emptyDom.length);
      if (emptyDom?.length > 0) {
        emptyDom.css('paddingBottom', '100%');
      }
    }

    // SSRswiper swiper
    if (firstInit) {
      $(`${this.id}`).find('.swiper-slide').removeClass('firstInit');
    }

    $(`${this.id} .product_thumbsColumnContainer`).height('auto');
    $(`${this.id} .product_thumbsRowContainer`).height('auto');
    this.initHandleProductImagesArrow(); // thumbs arrow
  }

  /**
   * pc
   */
  initPcProductImages(firstInit) {
    const pcProductImagesDom = $(`${this.id}`);
    if (pcProductImagesDom.find('.product_productImages').length == 0 || pcProductImagesDom.css('display') === 'none')
      return null;
    const mainSwiper = new Swiper(`${this.id} .product_productImages`, {
      effect: 'fade',
      fadeEffect: {
        crossFade: true,
      },
      init: firstInit ? false : true,
      lazy: {
        loadOnTransitionStart: true,
      },
      allowTouchMove: false,
      on: {
        init: () => {
          if (firstInit) {
            this.initThumbsSwiper(firstInit);
            if (this.productImageScale) {
              this.initPhotoSwipe(this.id, 'pc');
              this.initPcSkuPhotoSwiper();
            }
          }
        },
        slideChange: () => {
          handleVideoPlayPause(this.videoPcPlayer, 'pause');
          const { activeIndex } = this.swiper;
          this.handlePcThumbsActive(activeIndex); // thumbs active
          this.handlePcSkuImage(false); // sku
          //  handlePcSkuImage func
          // this.handleEffectSwiperHeight();
          // slide thumbs auto scroll
          if (this.thumbsDirection === COLUMN) {
            const scrollTopDistance = this.getThumbsPosition('top', activeIndex);
            this.handleThumbsScroll('scrollTop', scrollTopDistance);
          } else {
            const scrollLeftDistance = this.getThumbsPosition('left', activeIndex);
            this.handleThumbsScroll('scrollLeft', scrollLeftDistance);
          }
        },
      },
    });
    return mainSwiper;
  }

  /**
   * thumbs swiperactive
   * @param {*} activeIndex
   */
  handleMActivePagination(activeIndex) {
    $(`${this.mobileId} .paginationList div`).removeClass('active').eq(activeIndex).addClass('active');
  }

  /**
   * msku
   * @param {boolean} isShow 
   * @param {String} skuImageUrl sku image url
   * @returns
   */
  handleMobileSkuImage(isShow, skuImageUrl) {
    const selector = `${this.mobileId} .product_productImages`;
    const mainSwiperDom = $(selector);
    if (!this.mobileSwiper) return;
    if (mainSwiperDom.length === 0) {
      console.error('mskuselectorIdhtml');
      return;
    }

    const { realIndex } = this.mobileSwiper;
    if (isShow) {
      this.toggleMSkuImage(realIndex, true, skuImageUrl);
    } else {
      this.toggleMSkuImage(realIndex, false);
    }
    // this.handleProductImagesHeight(`${this.mobileId} .product_productImages`, realIndex, widthRatio, skuImageUrl);
  }

  getSwiperIsLoop() {
    return $(`${this.mobileId} .product_productImages .swiper-slide-duplicate`).length > 0;
  }

  bindMobileSkuImageScaleDom() {
    const self = this;
    $('.product_m_skuImageBox .scaleSkuImageIcon').on('click', function () {
      const items = [
        {
          src: $(this).parent().find('.product_photoSwipe_image').attr('data-photoswipe-src'),
          w: 0,
          h: 0,
          el: $(this).parent()[0],
        },
      ];
      self.handlePhotoSwiper(items, 0, false);
    });
  }

  /**
   * mobileloopslidecopy 
   */
  getMobileCurrentSlideDom(index) {
    const { realIndex } = this.mobileSwiper || {};
    const _index = index || realIndex;
    const swiperIsLoop = this.getSwiperIsLoop();
    const currentSlideDom = swiperIsLoop
      ? $(`${this.mobileId} .product_productImages .swiper-slide[data-swiper-slide-index="${_index}"]`)
      : $(`${this.mobileId} .product_productImages .swiper-slide`).eq(_index);
    return currentSlideDom;
  }

  setCurrentSlidePB($dom, imageUrl) {
    const ratio = imgSize(imageUrl)?.ratio || '100%';
    $dom.css('paddingBottom', ratio).attr('data-sku-image-ratio', ratio);
  }

  /**
   * msku
   * @param {number} index swiper slide activeIndex
   * @param {boolean} isShow 
   * @param {string} skuImageUrl  skuurl
   */
  toggleMSkuImage(index, isShow, skuImageUrl) {
    const self = this;
    if (index === undefined || index === null) {
      console.error(`toggleMSkuImage: index${index}`);
      return;
    }

    const currentSlideDom = self.getMobileCurrentSlideDom(index);

    const currentSkuImageBox = currentSlideDom.find('.product_m_skuImageBox');

    const currentSlideBox = currentSlideDom.find('.swiper-slide-box');

    const currentSkuImageIsError = currentSkuImageBox.hasClass('imageItemError');
    if (currentSkuImageIsError) {
      currentSkuImageBox.removeClass('imageItemError');
    }

    const skuImageDom = `
    <div class="product_m_skuImageBox">
      <img onerror="this.onerror=null;this.parentElement.className+=' imageItemError';" class="product_m_skuImage product_photoSwipe_image" data-photoswipe-src=${imgUrl(
        skuImageUrl,
        { width: 1800 },
      )} src=${skuImageUrl} />
      ${
        self.productImageScale
          ? `<div class="scaleSkuImageIcon">
      <svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
      <circle cx="13" cy="12" r="7.5" stroke="black"/>
      <path d="M18.5 17.5L23 22.5" stroke="black" stroke-linecap="round"/>
      </svg>
      </div>`
          : ''
      }
    </div>`;
    if (isShow && index !== undefined && skuImageUrl) {
      currentSlideDom.find('img').hide();
      if (currentSkuImageBox.length === 0) {
        currentSlideDom.append(skuImageDom);
        self.setCurrentSlidePB(currentSlideBox, skuImageUrl);
        self.bindMobileSkuImageScaleDom();
      } else {
        currentSkuImageBox
          .find('.product_m_skuImage')
          .removeAttr('srcset data-srcset')
          .attr({
            src: skuImageUrl,
            'data-photoswipe-src': imgUrl(skuImageUrl, { width: 1800 }),
            onerror: "this.onerror=null;this.parentElement.className+=' imageItemError';",
          })
          .show();
        self.setCurrentSlidePB(currentSlideBox, skuImageUrl);
      }
      this.mobileSwiper.updateAutoHeight();
      $(`${this.mobileId} .product_productImages`).attr('sku-image-index', index);
    } else if (!isShow && index !== undefined && currentSkuImageBox.length > 0) {
      const slideImg = currentSlideDom.find('img');
      if (currentSlideBox.attr('data-image-ratio')) {
        currentSlideBox
          .css('paddingBottom', currentSlideBox.attr('data-image-ratio'))
          .removeAttr('data-sku-image-ratio');
      }
      currentSkuImageBox.remove();
      slideImg.show();
      this.mobileSwiper.updateAutoHeight();
      $(`${this.mobileId} .product_productImages`).attr('sku-image-index', null);
    }
  }

  handleMobileScaleImage() {
    $(`${this.mobileId} .paginationList div`).removeClass('active').eq(activeIndex).addClass('active');
  }

  /**
   * mobile
   */
  initMobileProductImages(firstInit) {
    const mobileProductImagesDom = $(`${this.mobileId}`);
    const selector = `${this.mobileId} .product_productImages`;
    const mainSwiperDom = $(selector);
    const slidesLength = mainSwiperDom.find('.swiper-slide').length;
    // 
    const videoDom = mainSwiperDom.find('.videoItem');
    const videoIndex = videoDom.data('index');
    //  loop
    const videoIsStartOrEndPos = videoIndex === 0 || videoIndex === videoDom.data('length') - 1 || slidesLength === 1;
    if (mainSwiperDom.length === 0 || mobileProductImagesDom.css('display') === 'none') return null;

    const mainSwiper = new Swiper(selector, {
      loop: videoIsStartOrEndPos ? false : true,
      slidesPerView: 'auto',
      centeredSlides: true,
      spaceBetween: 5,
      autoHeight: true,
      lazy: {
        loadOnTransitionStart: true,
        loadPrevNext: this.mobileWidthRatio === 0.75 ? true : false,
      },
      on: {
        init: (swiper) => {
          if (firstInit) {
            if (this.productImageScale) {
              this.initPhotoSwipe(this.mobileId, 'mobile');
              this.initMobileSkuPhotoSwiper();
            }
            this.handleMActivePagination(swiper.realIndex);
            //  ssrsku image
            $(`${this.mobileId} .product_productImages`).attr('sku-image-index', swiper.realIndex);
            if (videoIsStartOrEndPos) {
              const skuImageDom = $(`${this.mobileId} .product_productImages .swiper-slide`)
                .eq(swiper.realIndex)
                .find('.product_m_skuImage');
              const skuImage = skuImageDom.attr('src') || skuImageDom.attr('data-src');
              // this.handleProductImagesHeight(`${this.mobileId} .product_productImages`, swiper.realIndex, widthRatio, skuImage ? skuImage : null);
            }
            $(`${this.mobileId} .product_productImages .swiper-slide`).css('height', 'auto');
            swiper.updateAutoHeight();
            window.lozadObserver?.observe();
          }
        },
        slideChange: (swiper) => {
          handleVideoPlayPause(this.videoMobilePlayer, 'pause');
          this.videoMobilePlayerStatus = 'pause';
          this.handleMActivePagination(swiper.realIndex);
          const skuImageDom = $(`${this.mobileId} .product_productImages .swiper-slide`)
            .eq(swiper.realIndex)
            .find('.product_m_skuImage');
          const skuImage = skuImageDom.attr('src') || skuImageDom.attr('data-src');
          // this.handleProductImagesHeight(`${this.mobileId} .product_productImages`, swiper.activeIndex, widthRatio, skuImage ? skuImage : null);
        },
        slideChangeTransitionEnd: () => {
          //   sku
          const skuImageIndex = $(`${this.mobileId} .product_productImages`).attr('sku-image-index');
          if (skuImageIndex !== undefined) {
            this.toggleMSkuImage(skuImageIndex, false);
          }
        },
      },
      ...this.swiperConfig.mobile,
    });
    return mainSwiper;
  }

  updateSlides(list) {
    $(`${this.id} .product_productImages`)
      .children('.swiper-wrapper')
      .empty()
      .append(
        list?.length
          ? list.map((item, index) => {
              const imgRatio = imgSize(item.resource)?.ratio || '100%';
              if (item.type === 'VIDEO') {
                const { middle: cover, videoId } = getYouTubeCover(item.resource);

                return `<div class="swiper-slide videoItem">
<div class="product_youTubeVideoContainer">
<div class="product_youTubeVideoBox" data-video-id="${videoId}"></div>
</div>
<img onerror="this.onerror=null;this.parentElement.className+=' imageItemError';" class="product_photoSwipe_image swiper-lazy" data-photoswipe-src="${cover}" ${
                  index !== 0 ? 'data-' : ''
                }src="${cover}" alt="">
</div>`;
              }

              return `<div class="swiper-slide imageItem" style="height: 0; padding-bottom:${imgRatio}"><img onerror="this.onerror=null;this.parentElement.className+=' imageItemError';" data-photoswipe-src="${
                item.resource
              }" ${index !== 0 ? 'data-' : ''}src="${
                item.resource
              }" alt="" class="swiper-lazy product_photoSwipe_image"></div>`;
            })
          : `<div class="swiper-slide"><div class="product-detail-empty-image"></div></div>`,
      );

    const slidesLength = list.length;

    $(`${this.mobileId} .product_productImages`)
      .children('.swiper-wrapper')
      .empty()
      .append(
        list?.length
          ? list.map((item, index) => {
              if (item.type === 'VIDEO') {
                const { middle: cover, videoId } = getYouTubeCover(item.resource);

                return `<div class="swiper-slide videoItem" style="height: auto" data-index="${index}" data-length="${slidesLength}">
<div class="swiper-slide-box" data-image-ratio="56.25%" style="padding-bottom: 56.25%">
  <div class="product_youTubeVideoContainer">
    <div class="product_youTubeVideoBox" data-video-id="${videoId}"></div>
  </div>
  <img onerror="this.onerror=null;this.parentElement.className+=' imageItemError';" class="product_photoSwipe_image swiper-lazy" data-photoswipe-src="${cover}" ${
                  index !== 0 ? 'data-' : ''
                }src="${cover}" alt="">
</div>
</div>`;
              }
              const { ratio } = imgSize(item.resource);

              return `<div class="swiper-slide imageItem" style="height: auto">
<div class="swiper-slide-box" data-image-ratio="${ratio}" data-sku-image-ratio="100%" style="padding-bottom: ${ratio}">
<img onerror="this.onerror=null;this.parentElement.className+=' imageItemError';" data-photoswipe-src="${
                item.resource
              }" ${index !== 0 ? 'data-' : ''}src="${
                item.resource
              }" alt="" class="swiper-lazy product_photoSwipe_image">${
                this.productImageScale
                  ? `<div class="scaleImageIcon"><svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg"><circle cx="13" cy="12" r="7.5" /><path d="M18.5 17.5L23 22.5" stroke-linecap="round" /></svg></div>`
                  : ''
              }</div>
</div>`;
            })
          : `<div class="swiper-slide"><div class="swiper-slide-box" data-image-ratio="100%" style="padding-bottom: 100%"><div class="product-detail-empty-image product-noImages"></div></div></div>`,
      );
  }

  updateImageList(list, activeIndex) {
    handleVideoPlayPause(this.videoPcPlayer, 'pause');
    handleVideoPlayPause(this.videoMobilePlayer, 'pause');
    this.videoMobilePlayerStatus = 'pause';

    this.replaceThubsSwiper(list, activeIndex);

    this.swiper?.destroy();
    this.mobileSwiper?.destroy();

    this.updateSlides(list);

    if (this.swiper) {
      this.swiper = this.initPcProductImages();

      this.updatePhotoSwipeItems(this.id);

      this.handleEffectSwiperHeight();
    }

    if (this.mobileSwiper) {
      this.mobileSwiper = this.initMobileProductImages();

      this.updatePhotoSwipeItems(this.mobileId);
    }

    this.videoPcPlayer = this.initPcVideo();
    this.videoMobilePlayer = this.initMobileVideo();

    $(`${this.id}`).attr('data-index', activeIndex || 0); // thumbs activeindex
  }

  replaceThubsSwiper(list, activeIndex) {
    const wrapper = $(
      `${this.id} .product_thumbs${
        this.thumbsDirection === COLUMN ? 'Column' : 'Row'
      }Container .productImageThumbsWrapper`,
    );

    const mWrapper = $(`${this.mobileId} .paginationList`);

    wrapper.empty();
    mWrapper.empty();

    if (!list?.length || list.length <= 1) {
      $(`${this.id} .product_thumbs${this.thumbsDirection === COLUMN ? 'Column' : 'Row'}Container`).hide();
      mWrapper.hide();
    } else {
      $(`${this.id} .product_thumbs${this.thumbsDirection === COLUMN ? 'Column' : 'Row'}Container`).show();
      mWrapper.show();

      list.forEach((item, index) => {
        if (item.type === 'VIDEO') {
          wrapper.append(`<div class="swiper-slide thumbsImageItem ${
            activeIndex === index ? 'active' : ''
          }"><img onerror="this.onerror=null;this.parentElement.className+=' imageItemError';" src="${
            getYouTubeCover(item.resource).middle
          }" alt=""><svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
          <circle cx="10" cy="10" r="10" fill="black" fill-opacity="0.6"/>
          <path d="M13.6256 10.2496L8.46641 13.6891C8.26704 13.822 8 13.6791 8 13.4394V6.56056C8 6.32095 8.26704 6.17803 8.46641 6.31094L13.6256 9.75039C13.8037 9.86913 13.8037 10.1309 13.6256 10.2496Z" fill="white"/>
          </svg>
          </div>`);
        } else {
          wrapper.append(
            $(
              `<div class="swiper-slide thumbsImageItem ${
                activeIndex === index ? 'active' : ''
              }"><img onerror="this.onerror=null;this.parentElement.className+=' imageItemError';" src="${imgUrl(
                item.resource,
                { width: 152 },
              )}" alt=""></div>`,
            ),
          );
        }
        mWrapper.append(`<div class="${activeIndex === index ? 'active' : ''}" />`);
      });
    }
  }

  skuImageChange(img) {
    const { url } = img || {};
    if (url) {
      handleVideoPlayPause(this.videoPcPlayer, 'pause');
      handleVideoPlayPause(this.videoMobilePlayer, 'pause');
      this.videoMobilePlayerStatus = 'pause';
      this.handlePcSkuImage(true, url);
      this.handleMobileSkuImage(true, url);
    } else {
      this.handlePcSkuImage(false);
      this.handleMobileSkuImage(false);
    }
  }
}
