/**
 * 
 * eg: 
 * 
 const f1 = effectFc(function (p1) {
    console.log('effectFc:', p1);
    const { useEffect } = this;
    useEffect($(window), 'on,off', 'resize', (e) => {
      console.log('effectFc onresize:', e);
    });
    return (p2) => {
      console.log('effectFc unmount:', p2);
    };
  });
  const unmountFn = f1('p1'); // 
  unmountFn('p2');
 */
export default (fn) => {
  let offEvents = []; // 

  // 
  let useEffect = function (instance, method, eventName, eventFn) {
    if (typeof instance !== 'object' || typeof method !== 'string' || typeof eventName !== 'string' || typeof eventFn !== 'function') {
      return;
    }
    const arr = method?.split(/\s*,\s*/);
    const onName = arr?.[0];
    const offName = arr?.[1];
    if (onName) {
      instance[onName]?.(eventName, eventFn);
    }
    if (offName) {
      const offEvent = instance[offName]?.bind(instance, eventName, eventFn);
      offEvents.push(offEvent);
    }
  };

  return (...props) => {
    let unmount = fn?.call({ useEffect }, ...props);
    return (...args) => {
      if (offEvents?.length) {
        offEvents.forEach((offEvent) => {
          offEvent();
        });
      }

      if (typeof unmount === 'function') {
        unmount?.(...args);
      }
      offEvents = null;
      useEffect = null;
      unmount = null;
    };
  };
};
