import { getEventID } from '@yy/sl-theme-shared/utils/report/tool';
import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';

class AddToCartList {
  constructor(spu) {
    this.spu = spu;
  }

  // sku
  setActiveSku(sku) {
    this.activeSku = sku
      ? {
          ...sku,
          name: this.spu.title,
        }
      : null;
  }

  init() {
    const addToCart = this;
    $('#page-product-detail .addToCartList_content').on('click', '.addToCart', (ev) => {
      const { spuSeq: spuId, skuSeq: skuId, name, price } = addToCart.activeSku;
      const eventID = getEventID();
      window.Shopline.event.emit('Cart::AddToCart', {
        data: {
          spuId,
          skuId,
          num: parseInt(ev.target.dataset.count, 10),
          price,
          name,
          eventID: `addToCart${eventID}`,
        },
        onSuccess: () => {
          window.Shopline.event.emit('DataReport::AddToCart', {
            content_spu_id: spuId,
            content_sku_id: skuId,
            content_category: '',
            content_name: name,
            currency: SL_State.get('storeInfo').currency,
            value: price,
            quantity: parseInt(ev.target.dataset.count, 10),
            price,
          });
        },
        onError: (error) => {
          console.error(error);
        },
      });
    });
  }
}
export default AddToCartList;
