import currency from '@yy/sl-theme-shared/utils/currency';
import previewModal from './preview-modal';
import quickAddModal from './quick-add-modal';
import { processPrice } from '../../../shared/utils/convertPrice';
import '../style/pagination.scss';
import '../style/product-item.scss';
import '../style/product-process-image.scss';
import '@yy/sl-theme-shared/components/hbs/active/js';

const { formatCurrency } = currency;

$('body').delegate('.js-product-item-quick-view', 'click', function (e) {
  e.preventDefault();
  e.stopPropagation();
  const spuSeq = $(this).data('spu-seq');
  const uniqueKey = $(this).data('unique-key');
  const query = $(this).data('query');
  previewModal({ spuSeq, uniqueKey, query });
});

$('body').on('click', '.js-product-item-quick-add', (e) => {
  e.preventDefault();
  e.stopPropagation();

  const $current = $(e.currentTarget);
  const spuSeq = $current.data('spu-seq');
  const uniqueKey = $current.data('unique-key');
  quickAddModal({ spuSeq, uniqueKey, $button: $current });
});

window.SL_EventBus.on('global:currency:format', ({ currencyCode: code, lang }) => {
  const priceItems = $('[data-product-item-price]');
  // eslint-disable-next-line no-restricted-syntax
  for (const priceItem of priceItems) {
    const priceValue = $(priceItem).data('product-item-price');
    const isSavePrice = $(priceItem).data('product-item-save-price');

    processPrice($(priceItem), priceValue, { isSavePrice, code, lang }).render();
  }
});

function thirdPartReport({ id, name, price }) {
  window.SL_EventBus.emit('global:thirdPartReport', {
    GA: [
      [
        'event',
        'select_content',
        {
          content_type: 'product',
          items: [
            {
              id,
              name,
              price: formatCurrency(price),
            },
          ],
        },
      ],
    ],
  });
}

$(document.body).on('click', '.product-item', function () {
  // 
  thirdPartReport({ id: $(this).data('skuId'), name: $(this).data('name'), price: $(this).data('price') });
});
