import { t } from '@yy/sl-theme-shared/utils/i18n';
import PayPal from '@yy/sl-theme-shared/components/paypal';
import pageMapping from '@yy/sl-theme-shared/utils/report/pageMapping';
import currency from '@yy/sl-theme-shared/utils/currency';
import checkout from '@yy/sl-theme-shared/utils/checkout';
import { getEventID } from '@yy/sl-theme-shared/utils/report/tool';
import { checkoutEnd } from '@yy/sl-theme-shared/components/hbs/products/checkedEvent';
import { ADD_TO_CART } from '../../../shared/cart/globalEvent';
import Toast from '../../../shared/components/toast';
import debounce from '../../../shared/utils/debounce';
import { addToCartThirdReport, addToCartHdReport, buyNowHdReport, paypalHdReport } from './product-button-report';

const { formatCurrency } = currency;

export const getVariant = (skuAttributeIds, skuAttributeMap) => {
  return skuAttributeIds?.map((item) => skuAttributeMap?.[item?.id]?.skuAttributeValueMap?.[item?.valueId]?.defaultValue)?.toString();
};

class ButtonEvent {
  constructor({ id, cartRoot, buyNowRoot, payPayId, soldOutRoot, spu, onAddSuccess, onAddError, sku }) {
    this.addButton = cartRoot;
    this.buyButton = buyNowRoot;
    this.payPayId = payPayId;
    this.soldOutRoot = soldOutRoot;
    this.onAddSuccess = onAddSuccess;
    this.onAddError = onAddError;

    this.spu = spu;
    this.sku = sku;
    this.initPaypal(id);
    this.initEvent();
    this.toast = new Toast();
    this.initLoading();

    // page='123'
    this.page = String(id).startsWith('productRecommendModal') ? '123' : pageMapping[window.SL_State.get('templateAlias')];
  }

  initLoading() {
    this.addLoading = new Toast({ duration: 0, target: this.addButton, type: 'loading', className: 'product_add_loading' });
    this.addLoading.close();
    this.buyLoading = new Toast({ duration: 0, target: this.buyButton, type: 'loading', className: 'product_buy_loading' });
    this.buyLoading.close();
  }

  initPaypal() {
    if ($(`#${this.payPayId}`).length === 0) return;
    this.buttonConfig = window.SL_State.get('productSettleButtonConfig');
    // checkout  key 
    const stage = checkoutEnd.getUuidAndMonitorCheckoutEnd('trade:spb:report', (status) => {
      const { name, price, spuSeq: spuId, skuSeq: skuId, num } = this.activeSku;
      paypalHdReport({
        event_name: 'quick_payment',
        product_id: spuId,
        variantion_id: skuId,
        quantity: num,
        price: formatCurrency(price),
        product_name: name,
        page: this.page,
        event_status: status,
      });
    });
    this.PayPalButton = new PayPal({
      stage, // key
      needReport: () => {
        const { name, price, spuSeq: spuId, skuSeq: skuId, num } = this.activeSku;
        return addToCartThirdReport({
          spuId,
          name,
          skuId,
          num,
          price,
          variant: getVariant(this.activeSku?.skuAttributeIds, this.sku?.skuAttributeMap),
        });
      },
      beforeCreateOrder: () => {
        //  
        // const { name, price, spuSeq: spuId, skuSeq: skuId, num } = this.activeSku;
        // const reportData = {
        //   event_name: 'quick_payment',
        //   product_id: spuId,
        //   variantion_id: skuId,
        //   quantity: num,
        //   price: formatCurrency(price),
        //   product_name: name,
        // };
        // paypalHdReport({
        //   ...reportData,
        //   page: this.page,
        // });
        // if (id === 'productDetail') {
        // window.HdSdk?.shopTracker.report('60006253', {
        // ...reportData,
        // page: 'pdp',
        // });
        // }
      },
      domId: this.payPayId,
      height: $(`#${this.payPayId}`).height(),
      dynamic: this.buttonConfig?.originConfig?.system, // true/ false
      onToast: ({ message }) => this.toast.open(message),
      onDynamicNotify: () => {
        // paypalpaypalproductSettleButtonConfig.buyNowfalse
        if (!this.buttonConfig.buyNow) {
          this.extraBuyNow();
        }
        $(`#${this.payPayId}`).remove();
        $(this.buyButton).filter('.product-more-payment-button').remove();
      },
      afterRender: () => {
        // sku
        if (!this.activeSku) {
          this.setPaypalDisabled();
        }
      },
      onInit: () => {
        $(this.buyButton).filter('.product-more-payment-button').html(t('productDetail.morePaymentOptions'));
      },
    });

    this.PayPalButton.render();

    // 
    $(document).on('click', `#${this.payPayId} .product-button-paypal-preview-mask`, () => {
      this.toast.open(t('productDetail.previewLinkToast'));
    });
  }

  extraBuyNow() {
    const buyNow = `<button class="buy-now btn btn-primary btn-lg ${this.buyButton.substr(1)} __sl-custom-track-product-detail-buy-now paypalAddBuyNow">
        <span>${t('common.buy-now')}</span>
      </button>`;
    $(`#${this.payPayId}`).before(buyNow);
    this.bindBuyNow();
    this.setTradeButtonHide(this.activeSku ? !this.activeSku.available : this.spu.soldOut);
  }

  setLoading(button, loading) {
    let dom = this.addButton;
    if (button === 'buy') {
      dom = this.buyButton;
    }
    const loadingName = `${button}Loading`;
    if (loading) {
      $(dom).find('.pdp_button_text').addClass('loading');
      this[loadingName].open();

      $(`.product_${button}_loading`)
        .find('.mp-loading__circular path')
        .css({ stroke: $(dom).css('color') });
    } else {
      // loading openclose
      this[loadingName].close();
      $(dom).find('.pdp_button_text').removeClass('loading');
    }
  }

  initEvent() {
    // 
    const $this = this;
    $(this.addButton).on(
      'click',
      debounce(300, () => {
        // 
        if ($this.isPreview()) {
          this.toast.open(t('productDetail.previewLinkToast'));
          return;
        }
        if (!this.activeSku) {
          this.toast.open(t('productList.container.tradePopup.error4'));
          return;
        }
        this.setLoading('add', true);
        const { spuSeq: spuId, skuSeq: skuId, num, name, price } = this.activeSku;
        const eventID = getEventID();
        const hdReportData = {
          page: this.page,
          spuId,
          skuId,
          num,
          price,
          name,
        };
        window.SL_EventBus.emit(ADD_TO_CART, {
          spuId,
          skuId,
          num,
          price,
          name,
          eventID: `addToCart${eventID}`,
          error: (...e) => {
            addToCartHdReport({
              ...hdReportData,
              event_status: 0,
            });
            this.onAddError(...e);
          },
          success: () => {
            addToCartHdReport({
              ...hdReportData,
              event_status: 1,
            });
            addToCartThirdReport({
              spuId,
              name,
              price,
              skuId,
              num,
              eventID,
              variant: getVariant(this.activeSku?.skuAttributeIds, this.sku?.skuAttributeMap),
            });

            this.onAddSuccess?.();
          },
          complete: () => {
            this.setLoading('add', false);
          },
        });
      }),
    );

    $this.bindBuyNow();
  }

  bindBuyNow() {
    // checkout  key 
    const stage = checkoutEnd.getUuidAndMonitorCheckoutEnd('trade:goToCheckout:report', (status) => {
      const { name, price, spuSeq: spuId, skuSeq: skuId, num } = this.activeSku;
      const hdReportData = {
        page: this.page,
        spuId,
        skuId,
        num,
        price,
        name,
      };
      buyNowHdReport({
        ...hdReportData,
        event_status: status,
      });
    });
    const $this = this;
    // 
    $(this.buyButton).on(
      'click',
      debounce(300, () => {
        // 
        if ($this.isPreview()) {
          this.toast.open(t('productDetail.previewLinkToast'));
          return;
        }
        if (!this.activeSku) {
          this.toast.open(t('productList.container.tradePopup.error4'));
          return;
        }
        this.setLoading('buy', true);
        const product = [
          {
            productSeq: this.activeSku.spuSeq,
            productSku: this.activeSku.skuSeq,
            productNum: this.activeSku.num,
            productPrice: this.activeSku.price,
            productName: this.activeSku.name,
          },
        ];
        const { name, price, spuSeq: spuId, skuSeq: skuId, num } = this.activeSku;
        checkout
          .jump(product, {
            stage,
            needReport: () => {
              return addToCartThirdReport({
                spuId,
                name,
                skuId,
                num,
                price,
                variant: getVariant(this.activeSku?.skuAttributeIds, this.sku?.skuAttributeMap),
              });
            },
          })
          .catch(() => {
            this.setLoading('buy', false);
          });
      }),
    );
  }

  setActiveSku(sku) {
    this.activeSku = sku
      ? {
          ...sku,
          num: 1,
          name: this.spu.title,
        }
      : null;
    this.setPayPalProduct();
    this.setPaypalDisabled();
    if (sku) {
      this.setTradeButtonHide(sku.soldOut);
    }
  }

  setActiveSkuNum(num) {
    if (!this.activeSku) return;
    this.activeSku.num = num;
    this.setPayPalProduct();
  }

  setPaypalDisabled() {
    if (!this.activeSku) {
      this.PayPalButton?.setDisabled(true);
      return;
    }
    this.PayPalButton?.setDisabled(false);
  }

  setPayPalProduct() {
    if ($(`#${this.payPayId}`).length === 0 || !this.activeSku) return;
    const product = [
      {
        spuId: this.activeSku.spuSeq,
        skuId: this.activeSku.skuSeq,
        num: this.activeSku.num,
        name: this.spu.title,
        price: this.activeSku.price,
      },
    ];
    this.PayPalButton.setProducts(product);
  }

  setTradeButtonHide(show) {
    if (!show) {
      $(this.addButton).removeClass('hide');
      $(this.buyButton).removeClass('hide');
      $(`#${this.payPayId}`)?.removeClass('hide');
      $(this.buyButton).filter('.product-more-payment-button')?.removeClass('hide');
      $(this.soldOutRoot).addClass('hide');
      return;
    }
    $(this.addButton).addClass('hide');
    $(this.buyButton).addClass('hide');
    $(`#${this.payPayId}`)?.addClass('hide');
    $(this.buyButton).filter('.product-more-payment-button')?.addClass('hide');
    $(this.soldOutRoot).removeClass('hide');
  }

  isPreview() {
    const currentUrl = window.location.pathname;
    return /^[/]preview[/].*/.test(currentUrl);
  }
}

export default ButtonEvent;
