import * as qs from 'query-string';

export const queryStringConfig = {
  options: {
    skipNull: true,
    skipEmptyString: true,
  },
};

export const adaptor = (url, { query, fragmentIdentifier, fullQuery = true } = {}) => {
  const currentUrl = qs.parseUrl(window.location.href, { ...queryStringConfig.options, parseFragmentIdentifier: true });
  const allQuery = fullQuery
    ? {
        ...(currentUrl.query || {}),
        ...(query || {}),
      }
    : { ...query };
  const passUrl = qs.stringifyUrl(
    {
      url,
      query: allQuery,
      fragmentIdentifier: fragmentIdentifier || currentUrl.fragmentIdentifier,
    },
    queryStringConfig.options,
  );

  const wholeUrl = `${window.location.protocol}//${window.location.host}${passUrl}`;

  return {
    originUrl: url,
    ...currentUrl,
    // currentUrlquery
    query: allQuery,
    url: passUrl,
    wholeUrl,
  };
};
