import { ClickType, PageType } from '../report/const';

// addtocart

function addToCartHdReport({
  spuId,
  skuId,
  num,
  price,
  name,
  page,
  event_name,
  event_category,
  product_type,
  event_id,
  // ...rest
}) {
  window.HdSdk?.shopTracker.report(event_id, {
    page,
    event_name,
    event_category,
    product_type,
    product_id: spuId,
    variantion_id: skuId,
    // 0sdk false ''
    quantity: `${num}`,
    price: `${price}`,
    product_name: name,
    // ...rest,
  });
}

export default function reportHd(page, type, data) {
  switch (page) {
    case PageType.ProductDetail:
      switch (type) {
        case ClickType.AddToCart:
        case ClickType.BeginCheckout:
          addToCartHdReport(data);
          break;
        default:
          break;
      }
      break;
    default:
      break;
  }
}
