import createLogger from '@yy/sl-theme-shared/utils/createLogger';

export function composedPath(event) {
  if (event.path) {
    return event.path;
  }

  if (typeof event.composedPath === 'function') {
    return event.composedPath();
  }

  const path = [];
  let { target } = event;
  while (target.parentNode !== null) {
    path.push(target);
    target = target.parentNode;
  }

  path.push(document, window);
  return path;
}

const storageName = `orderSignList`;
const helpersConsole = createLogger('helpers', '[matchOrderSign]');

export function addOrderSign(seq) {
  if (typeof window !== 'undefined') {
    const signListJSON = window?.localStorage?.getItem(storageName) || JSON.stringify([]);
    const signList = JSON.parse(signListJSON);
    signList.push(seq);
    window?.localStorage?.setItem(storageName, JSON.stringify(signList));
    helpersConsole.log('signList', signList);
  }
}

export function matchOrderSign(seq) {
  if (typeof window !== 'undefined') {
    const signListJSON = window?.localStorage?.getItem(storageName) || JSON.stringify([]);
    const signList = JSON.parse(signListJSON);
    helpersConsole.log('match seq', seq);
    return signList.includes(seq);
  }
}

export function removeOrderSign(seq, options = {}) {
  if (typeof window !== 'undefined') {
    const signListJSON = window?.localStorage?.getItem(storageName) || JSON.stringify([]);
    const signList = JSON.parse(signListJSON);
    if (options?.removeAll) {
      helpersConsole.log('remove all', storageName);
      window?.localStorage?.removeItem(storageName);
      return;
    }

    const filterList = signList.filter((sign) => sign !== seq);
    helpersConsole.log('filter', filterList);
    window?.localStorage?.setItem(storageName, JSON.stringify(filterList));
  }
}

export function onDomReady(fn) {
  document.removeEventListener('DOMContentLoaded', fn);
  if (document.readyState !== 'loading') {
    setTimeout(fn, 1);
  } else {
    document.addEventListener('DOMContentLoaded', fn);
  }
}
