import * as Cookies from 'js-cookie';
import ga from './ga';
import { clickAdsData, loadAdsData } from './ads';
import { clickFbData, loadFbData } from './fb';
// import { EventNames } from './eventName';
import reportHd from './hd';
import { SL_EventBus } from '../event-bus';
import { ClickType, PageType } from '../report/const';
import { getEventID } from '../report/tool';
import apiLogger from '../../events/utils/api-logger';
import * as externalDtaReportEvents from '../../events/data-report/enum';
import dataReportAdapters from '../../events/data-report/adapters';

const excludedDataReportEvents = new Set(
  Object.keys(externalDtaReportEvents).map((key) => externalDtaReportEvents[key]),
);

const logger = apiLogger('DataReport:Instance - ON');
class DataReport {
  constructor() {
    this.eventBus = SL_EventBus;
    this.rpEvent = window.Shopline?.event;
  }

  load(data) {
    const { pageType, value } = data;
    const val = { ...value, ...{ currency: this.storeCurrency } };
    const gaParam = ga.load(pageType, val);
    const adsParams = loadAdsData(pageType, val);
    const fbParams = loadFbData(pageType, val);
    const params = {
      GAAds: adsParams,
      GA: gaParam,
      FBPixel: fbParams,
    };
    this.eventBus?.emit('global:thirdPartReport', params);
  }

  /**  */
  touch(data) {
    const { pageType, actionType, value } = data;
    const {
      content_spu_id,
      content_sku_id,
      content_category,
      content_name,
      currency,
      quantity,
      price,
      hdProducts,
      extra,
    } = value || {};
    const spu_ids = [content_spu_id];
    // addtocart pod,spuid 
    if (Array.isArray(hdProducts)) {
      hdProducts.forEach((item) => {
        if (item?.spuId) {
          spu_ids.push(item?.spuId);
        }
        reportHd(pageType, actionType, item);
      });
    }
    const eid = getEventID();
    const tpParams = {
      skuId: spu_ids,
      category: content_category,
      name: content_name,
      currency,
      quantity,
      price,
      eventId: `addToCart${eid}`,
    };

    // buynow cookie capi 
    if (extra?.abandonedOrderSeq && extra?.event_scenes === 'buy_now') {
      Cookies.set(`${extra?.abandonedOrderSeq}_fb_data`, {
        tp: 2,
        et: Date.now(),
        ed: eid,
      });
    }

    const hdParams = {
      spuId: content_spu_id,
      skuId: content_sku_id,
      name: content_name,
      num: quantity,
      price,
      ...extra,
    };
    // const val = { ...value, ...{ currency: this.storeCurrency } };
    const gaParam = ga.click(pageType, actionType, tpParams);
    const adsParams = clickAdsData(pageType, actionType, tpParams);
    const fbParams = clickFbData(actionType, tpParams);
    const params = {
      GAAds: adsParams,
      GA: gaParam,
      FBPixel: fbParams,
    };
    this.eventBus?.emit('global:thirdPartReport', params);
    if (extra?.event_name) {
      // event_id  event_name 
      reportHd(pageType, actionType, hdParams);
    }
  }

  //  addtocart addtocart
  listen(eventName) {
    this.rpEvent?.on(eventName, (data) => {
      logger.info(data);
      if (data.interior && excludedDataReportEvents.has(data.interior)) {
        logger.error('not in access events', {
          interior: data.interior,
          excludedDataReportEvents,
        });
        return;
      }
      const params = {
        actionType: ClickType.AddToCart,
        pageType: PageType.ProductDetail,
        value: data?.data,
      };
      this.touch(params);
    });
  }
}

const dataReport = new DataReport();

dataReportAdapters.on();

export default dataReport;
