import request from '@yy/sl-theme-shared/utils/request';
import createShadowDom from '../../commons/js/createShadowDom';

function whichTransitionEvent() {
  let t;
  const el = document.createElement('fakeElement');
  const transitions = {
    transition: 'transitionend',
    OTransition: 'oTransitionEnd',
    MozTransition: 'transitionend',
    WebkitTransition: 'webkitTransitionEnd',
    MsTransition: 'msTransitionEnd',
  };

  /* eslint-disable no-restricted-syntax */
  for (t in transitions) {
    if (el.style[t] !== undefined) {
      return transitions[t];
    }
  }
}

function openCollapseByHeight(element) {
  const initHeight = $(element).innerHeight();
  // element.style.transition = 'none'; // 2015-05-20mac Safariautotransition, none~
  element.style.height = 'auto';
  const targetHeight = $(element).innerHeight();
  element.style.height = `${initHeight}px`;
  $(element).css('color'); //  transition
  element.style.height = `${targetHeight}px`;
}

function closeCollapseByHeight(element) {
  const initHeight = $(element).innerHeight();
  // element.style.transition = 'none'; // 2015-05-20mac Safariautotransition, none~
  $(element).css('height', `${initHeight}px`);
  $(element).css('color'); //  transition
  element.style.height = `0px`;
}

const PAGE_ID = 'pageid';
const CUSTOM_PAGE_TYPE = 'customize';

/**
 * jsonsdk
 * 
 * @param originData
 * @returns
 */
export const isReJsonSdkData = (originData) => {
  try {
    return JSON.parse(originData);
  } catch (error) {
    return false;
  }
};

class Collapse {
  constructor({ lang = 'default', selector, cacheRequest = true }) {
    this.$container = $(selector);
    this.$collapseAsyncItems = this.$container.find('.base-collapse-item-async');
    this.$collapseSyncItems = this.$container.find('.base-collapse-item-sync');

    this.lang = lang;
    this.cacheRequest = cacheRequest;

    // 
    this.cacheData = {};

    //  item
    this.$activeItem = null;

    this.transitionEvent = whichTransitionEvent();

    this.init();
  }

  init() {
    const self = this;
    const ids = Array.from(this.$collapseAsyncItems)
      .map((item) => $(item).data(PAGE_ID))
      .filter((id) => !!id);

    this.$collapseAsyncItems.each((index, item) => {
      const $item = $(item);
      $item.find('.base-collapse-item__wrap').on(self.transitionEvent, function () {
        if ($(this).parent().hasClass('active')) {
          $(this).css('height', 'auto');
        }
      });
      if ($item.hasClass('active') && $item.data(PAGE_ID)) {
        this.requestCollapseContent($item.data(PAGE_ID)).then((res) => {
          this.setCollapseContent(res?.data, $item);
          // funTransitionHeight();
          // this.calcCollapseContentHeight($item);
        });
      }
    });

    this.$collapseSyncItems.each((index, item) => {
      const $item = $(item);
      $item.find('.base-collapse-item__wrap').on(self.transitionEvent, function () {
        if ($(this).parent().hasClass('active')) {
          $(this).css('height', 'auto');
        }
      });
      if (!$item.data('isInitShadowDom')) {
        const html = $item.find('.base-collapse-item__content').html();
        self.transContentByShadowDom($item, html);
        $item.data('isInitShadowDom', true);
      }
    });

    // const defaultFoldPageids = Array.from(this.$collapseAsyncItems).reduce((sum, item) => {
    //   const $item = $(item)
    //   if ($item.hasClass('active') && $item.data(PAGE_ID)) {
    //     return sum.concat($item.data(PAGE_ID))
    //   }

    //   return sum
    // }, [])

    // Promise.all(defaultFoldPageids.map(requestCollapseContent)).then(resArr => {
    //   resArr.map((r, index) => setCollapseContent(r?.data, this.$collapseAsyncItems[index]))
    // })

    this.requestCollapseTitle(ids);
    this.bindEvent();
  }

  requestCollapseTitle(ids) {
    const { lang } = this;

    return request({
      url: 'merchant/render/page/basic/infos',
      method: 'GET',
      params: {
        pageIds: ids.join(','),
      },
    }).then((res) => {
      if (res && Array.isArray(res.data)) {
        const data = res.data.reduce((fin, item) => {
          const name = item?.name ? item?.name[lang] : '';

          return {
            ...fin,
            [item?.id]: name,
          };
        }, {});
        this.setCollapseTitle(data);
      }
    });
  }

  setCollapseTitle(titleMap) {
    this.$collapseAsyncItems.each((index, item) => {
      const $item = $(item);
      const title = titleMap[$item.data(PAGE_ID)];

      if (title) {
        $item.find('.base-collapse-item__title').text(title);
      }
    });
  }

  bindEvent() {
    const self = this;
    // collapse
    this.$collapseAsyncItems.on('click', '.base-collapse-item__header', function () {
      const $item = $(this).closest('.base-collapse-item');
      const id = $item.data(PAGE_ID);

      const isOpen = $item.hasClass('active');

      if (isOpen) {
        self.close($item);
        return;
      }

      // requestCollapseContentitem id
      // todo: 
      self.requestCollapseContent(id).then((res) => {
        self.setCollapseContent(res?.data, $item);
        self.$activeItem = $item;
        self.open($item);
      });
    });

    // collapse
    this.$collapseSyncItems.on('click', '.base-collapse-item__header', function () {
      const $item = $(this).closest('.base-collapse-item');
      const isOpen = $item.hasClass('active');
      if (isOpen) {
        self.close($item);
        return;
      }
      //  shadow dom
      if (!$item.data('isInitShadowDom')) {
        const html = $item.find('.base-collapse-item__content').html();
        self.transContentByShadowDom($item, html);
        $item.data('isInitShadowDom', true);
      }
      self.open($item);
    });

    // 
    window.SL_EventBus.on('stage:locale:change', () => {
      //  item item 
      if (this.$activeItem) {
        this.calcCollapseContentHeight(this.$activeItem);
      } else {
        this.$collapseAsyncItems.each((index, item) => {
          const $item = $(item);

          if ($item.hasClass('active') && $item.data(PAGE_ID)) {
            this.calcCollapseContentHeight($item);
          }
        });
      }
    });
  }

  requestCollapseContent(id) {
    if (this.cacheRequest && this.cacheData[id]) {
      return Promise.resolve(this.cacheData[id]);
    }

    return request({
      url: `merchant/render/page/${CUSTOM_PAGE_TYPE}/${id}`,
      method: 'GET',
    })
      .then((res) => {
        if (this.cacheRequest) {
          this.cacheData[id] = res;
        }

        return res;
      })
      .catch(() => {
        if (this.cacheRequest) {
          this.cacheData[id] = {};
        }
        return {};
      });
  }

  getCustomPageContent(pageConfig) {
    const config = isReJsonSdkData(pageConfig ?? '');
    let html = '<div class="custom-page-render-container">';
    if (config) {
      const stage = config?.page;
      const grids = stage?.children;
      if (!grids || grids.length < 1) {
        return '';
      }
      grids.forEach((item) => {
        item?.children.forEach((it) => {
          const component = it?.children?.[0];

          if (component?.type === 'Text') {
            if (component?.props?.title) {
              html += `<h2>${component.props.title}</h2>`;
            }
            html += component.props.content;
          } else if (component?.type === 'CustomHtml') {
            html += `<div>${component.props.content}</div>`;
          } else if (component?.type === 'Image') {
            if (component?.props?.image?.url) {
              html += `<img data-src="${component.props.image.url}" style="max-width:100%"  data-srcset="${component.props.image.url}" alt="${component.props.image.alt}" class="lozad"  />`;
            }
          }
        });
      });
    } else {
      html += pageConfig;
    }

    html += '</div>';

    return html;
  }

  transContentByShadowDom($item, content) {
    const $content = $item.find('.base-collapse-item__content');
    const isAside = $content.hasClass('aside');
    $content.html(`
      <div style="overflow: hidden;" data-node="shadow-content">
        <div class="mce-content-body ${isAside ? 'aside' : 'bottom'}">
        <style>
        .mce-content-body.aside table{
          min-width: 100% !important;
        }
        .mce-content-body img {
          opacity: 0;
        }
        .mce-content-body img[srcset],
        .mce-content-body img[src] {
          opacity: 1;
        }
        @media (max-width: 749.98px){
          .mce-content-body table{
            min-width: 100% !important;
          }
        }
        .mce-content-body .table-wraper-fix{
          padding-bottom: 1px;
          padding-right: 1px;
        }
        
        @media (min-width: 749.98px){
          .mce-content-body .table-wraper-fix > table > tbody > tr td,
          .mce-content-body .table-wraper-fix > table > tbody > tr th{
            min-width:  80px !important;
            box-sizing: border-box !important;
          }
          .mce-content-body.bottom .table-wraper-fix > table > tbody > tr td,
          .mce-content-body.bottom .table-wraper-fix > table > tbody > tr th{
            min-width: 120px !important;
          }
        }
        .table-wraper-fix > table > tbody > tr td,
        .table-wraper-fix > table > tbody > tr th {
          padding: 8px;
        }
      </style>
          ${content}
        </div>
      </div>
      <div data-node="shadow-dom"></div>
    `);
    window.lozadObserver.observe();
    createShadowDom();
  }

  setCollapseContent(data, $item) {
    const content = this.getCustomPageContent(data?.htmlConfig);
    this.transContentByShadowDom($item, content);
  }

  calcCollapseContentHeight($item) {
    const $content = $item.find('.base-collapse-item__content');

    const images = Array.from($content.find('img')).map((item) => {
      return new Promise((resolve, reject) => {
        const image = new Image();
        image.src = item.src;
        image.onload = () => {
          resolve(image);
        };
        image.onerror = () => {
          reject(image);
        };
      });
    });

    // 
    Promise.allSettled(images)
      .then(() => {
        // fix safari 
        setTimeout(() => {
          const height = $content.outerHeight();
          $content.parent().css({ height });
        }, 0);
      })
      .catch(() => {
        $content.parent().css({ height: 'auto' });
      });
  }

  open($item) {
    // $item.find('.base-collapse-item__wrap').css({ height: 'auto' });
    // this.calcCollapseContentHeight($item);
    openCollapseByHeight($item.find('.base-collapse-item__wrap').get(0));
    $item.addClass('active');
  }

  close($item) {
    this.$activeItem = null;
    closeCollapseByHeight($item.find('.base-collapse-item__wrap').get(0));
    $item.removeClass('active');
    // $item.find('.base-collapse-item__wrap').css({ height: 0 });
  }
}

export default Collapse;
