import debounce from '../../../shared/utils/debounce';
import isMobile from '../../../shared/utils/isMobile';

function setPosition({ id, container = window, offsetTop = 0 }) {
  const box = $(`#product-detail-sticky_${id}`);

  if (!box.get(0)) {
    return;
  }

  const height = box.height();
  const wHeight = $(container).outerHeight();

  if (height + offsetTop > wHeight) {
    box.css('top', -(height - wHeight));
  } else {
    box.css('top', offsetTop);
  }
}

export function listenPosition({ id, container = window, offsetTop = 0 }) {
  setPosition({ id, container, offsetTop });

  const setPositionDebounce = debounce(300, () => {
    setPosition({ id, container, offsetTop });
  });

  $(window).on('resize', setPositionDebounce);

  if (!isMobile()) {
    $(container).one('scroll', setPositionDebounce);
  }

  return function removePostionListener() {
    $(window).off('resize', setPositionDebounce);
    $(container).off('scroll', setPositionDebounce);
  };
}

export default setPosition;
