// eslint-disable-next-line import/no-unresolved
import Form from '@yy/sl-theme-shared/utils/form';
import { t } from '@yy/sl-theme-shared/utils/i18n';
import http from '@yy/sl-theme-shared/utils/request';
import Base from '../../shared/base/BaseClass';
import BaseModal from '../../shared/components/modal';
import Toast from '../../shared/components/toast';
import Loading from '../../shared/components/toast/loading';
import '../commons/style/inquiry-price-modal.scss';

const emailRE = /^[A-Za-z0-9_./;+]+([A-Za-z0-9_./;+]+)*@([A-Za-z0-9]+\.)+[A-Za-z]{2,6}$/;
const toast = new Toast();

export default class InquiryPriceModal extends Base {
  constructor({ id, spu }) {
    super('product:inquiry:price:modal');
    this.$root = $(`#JS-inquiry-price-modal_${id}`);
    this.$setPortals(this.$root);
    this.buttonSelector = `#JS-inquiry-price-button_${id}`;
    this.spu = spu;
    this.activeSku = null;
    this.formInstance = null;
    this.modalInstance = null;
    this.init(id);
  }

  init(id) {
    if (this.spu?.inquiry) {
      this.initModal(id);
      this.initForm(id);
      //! NOTICE: when url has alias and alias equal with 'PreviewProductsDetail' do not execute below method
      this.bindEvents();
    }
  }

  initModal(id) {
    this.modalInstance = new BaseModal({ modalId: `inquiry-price_${id}` });
    this.modalInstance.init();
  }

  initForm(id) {
    this.formInstance = Form.takeForm(`JS-inquiry-modal-form_${id}`);
    this.formInstance.init();
    this.formInstance.setFields(InquiryPriceModal.getFieldsConfig());
  }

  setActiveSku(activeSku) {
    this.activeSku = activeSku;
  }

  bindEvents() {
    const eventHandlers = {
      submitClickHandler: async (e) => {
        try {
          // 
          if (this.isPreview()) {
            toast.open(t('productDetail.previewLinkToast'));
            return;
          }
          $(e.target).addClass('disabled');
          await this.validateForm();
          // fetch remote data
          await this.postForm();
        } catch (err) {
          // console.log('err: ', err);
        } finally {
          $(e.target).removeClass('disabled');
        }
      },
      cancelClickHandler: () => {
        this.hideModal();
      },
      buttonClickHandler: () => {
        this.showModal();
      },
    };

    this.$onPortals('click', '.JS-inquiry-modal-submit', eventHandlers.submitClickHandler);
    this.$onPortals('click', '.JS-inquiry-modal-cancel', eventHandlers.cancelClickHandler);
    $(this.buttonSelector).on('click', eventHandlers.buttonClickHandler);
  }

  unbindEvents() {
    this.$offAll();
    $(this.buttonSelector).off('click');
  }

  async postForm() {
    const inquiryInfo = this.formInstance.getFieldsValue();
    const { activeSku } = this;
    const spuInfo = this.spu;
    const finalInquiryInfo = {
      Email: inquiryInfo.email,
      Message: inquiryInfo.message,
      Name: inquiryInfo.name,
      Mobile: inquiryInfo.mobile,
      'Country/Region': inquiryInfo.region,
      Product: spuInfo?.title,
      ProductUrl: `${window.location.origin}/products/${spuInfo.spuSeq}${activeSku ? `?sku=${activeSku.skuSeq}` : ''}`,
    };
    const sendContentStr = Object.keys(finalInquiryInfo).reduce((str, key) => {
      // eslint-disable-next-line no-param-reassign
      str += `${key}${finalInquiryInfo[key]}\n`;
      return str;
    }, '');

    const sendInfo = {
      email: inquiryInfo.email,
      content: sendContentStr,
      attachmentUrl: activeSku?.imageList?.[0] || activeSku?.image || spuInfo?.images?.[0],
    };

    const loading = new Loading({ duration: 0 });
    loading.open();
    await this.sendInquiryInfoRun(sendInfo, loading);
  }

  async sendInquiryInfoRun(info, loading) {
    const response = await http.post('/mc/shop/online/send', info);

    loading.close();
    if (response.code === 'SUCCESS') {
      this.hideModal();
      toast.open(t('productDetail.inquiry.submitSuccess'));
    } else {
      toast.open(t('productDetail.inquiry.submitFailed'));
    }
  }

  showModal() {
    this.modalInstance.show();
  }

  hideModal() {
    this.modalInstance.hide();
  }

  static getFieldsConfig() {
    // 
    const fields = [
      {
        name: 'email',
        value: '',
        rules: [
          { message: t('productDetail.inquiry.emailEmptyErr'), required: true },
          { message: t('productDetail.inquiry.emailFormatErr'), pattern: emailRE },
        ],
      },
      {
        name: 'message',
        value: '',
        rules: [
          { message: t('productDetail.inquiry.messageEmptyErr'), required: true },
          {
            message: t('productDetail.inquiry.messageLengthErr'),
            validator(val) {
              return val.length <= 1500;
            },
          },
        ],
      },
      {
        name: 'name',
        value: '',
      },
      { name: 'mobile', value: '' },
      { name: 'region', value: '' },
    ];

    return fields;
  }

  validateForm() {
    return new Promise((resolve, reject) => {
      this.formInstance.validateFields().then((res) => {
        if (res.pass) {
          resolve();
        } else {
          reject(res);
        }
      });
    });
  }

  isPreview() {
    const currentUrl = window.location.pathname;
    return /^[/]preview[/].*/.test(currentUrl);
  }
}
