/* eslint-disable prefer-promise-reject-errors */
// eslint-disable-next-line import/no-unresolved
import PayPal from '@payment/gateway-sdk';
import { adaptor } from '@yy/sl-theme-shared/utils/url-adaptor';
import loggerService from '@yy/sl-theme-shared/utils/logger';
import { getPaypalKey, initialData } from './config';
import { preparePay, getPaypalConfig } from './services';
import { conversion, getPayPalServerConfig, getConfigCommit, getQueryParams } from './utils';
import checkout from '../../utils/checkout';
import loadScript from '../../utils/load-script';
import { setAddtoCart } from '../../utils/tradeReport';
import { SL_EventBus } from '../../utils/event-bus';

const logger = loggerService.pipeOwner({
  owner: 'PayPal',
  onTag: (key) => {
    switch (key) {
      // 
      case 'abandonedSeq':
        return true;
      default:
        return false;
    }
  },
});

// Paypal SDK 
function getPaypalSDK() {
  return PayPal;
}

let PayPalReturnUrl = window.location.href;
const dynamicCallBackName = 'rememberedCb';
const PayPalRememberedScript = (callbackName) => {
  return `https://www.paypal.com/checkoutnow/remembered?callback=${callbackName}`;
};

const noop = () => {};

const isFn = (object) => typeof object === 'function';

/** paypal */
const dynamicRemembered = {
  /**  0 1 2 */
  loadingStatus: 0,
  /** paypal jsonp{ paypal: boolean; venmo: boolean } */
  info: {},
  /** render.jsonp */
  cbFnList: [],
};
/**  */
let renderObserver;

class PayPalSmartPayButton {
  /**
   *
   * @param {{
   * switchdelay
   * timeout?:{switch: boolean, delay?: number}
   * domId: string
   * onToast?: function
   * onDynamicNotify?: function
   * height?: number
   * wrapperClass?: string;
   * wrapperStyle?: string;
   * dynamic?: boolean
   * onInit?: (data, actions) => Promise<any>
   * 
   * onCancel?: (data) => Promise<string>
   * beforeCreateOrder?: (this) => {
   *   associateCart: boolean;
   *   discountCode: string | null;
   *   bundledActivitySeq: string | null;
   *   associatePayPal: boolean;
   *   query: object;
   *  }
   * beforeContinue?: () => Promise<any>
   * }
   * createOrder?: () => Promise<{ chDealId: string; returnUrl: string }>
   * payMode?: 'continue' | 'pay_now'
   * paypalStyle?: {
   *  layout?: 'vertical' | 'horizontal'
   *  color?: 'gold' | 'blue' | 'silver' | 'white' | 'black'
   *  shape?: 'rect' | 'pill'
   *  label?: 'paypal' | 'checkout' | 'buynow' | 'pay' | 'installment'
   *  height?: number // set the style.height option to a value from 25 to 55.
   *  tagline?: boolean
   * }
   * onError?: (error) => any;
   * : a=1&b=2. [ url Query ](https://developer.paypal.com/docs/checkout/reference/customize-sdk/#query-parameters)
   * queryParams?: string,
   *  [script ](https://developer.paypal.com/docs/checkout/reference/customize-sdk/#script-parameters)
   * scriptParams?: Record<string, string>
   * } config
   */
  constructor(config) {
    // ! 
    const btnShape = document.body?.dataset?.button_style === 'rounded' ? 'pill' : 'rect';
    // sentry
    this.loggerPrefix = `[PayPal][dom:${config.domId}]`;
    // 
    this.payMode = config.payMode || 'continue';

    this.config = {
      ...this.config,
      ...initialData,
      timeout: { switch: false },
      commit: getConfigCommit(config),
      disableFunding: config.disableFunding,
      domId: config.domId,
      pageType: config.pageType,
      stage: config.stage,
      needReport: config.needReport,
      styleOption: {
        commonStyle: {
          paypalSPBStyle: {
            ...initialData.styleOption.commonStyle.paypalSPBStyle,
            ...(config.paypalStyle || {}),
            height: this.getDomHeight(config.height),
            shape: btnShape,
          },
        },
      },
      paypalScriptParams: config.scriptParams || {},
    };
    const { switch: timeoutSwitch, delay } = config.timeout ?? {};
    if (timeoutSwitch) {
      this.config.timeout.switch = timeoutSwitch;
      this.config.timeout.delay = delay && typeof delay === 'number' ? delay : initialData.initTimeout;
    }
    this.wrapperClass = config.wrapperClass;
    this.wrapperStyle = config.wrapperStyle;

    this.style = {
      height: this.getDomHeight(config.height),
    };

    this.products = null;
    this.dynamic = config.dynamic;
    this.jsonpCallback = this.jsonpCallback.bind(this);
    this.onToast = isFn(config.onToast) ? config.onToast : noop;
    this.onDynamicNotify = isFn(config.onDynamicNotify) ? config.onDynamicNotify : noop;

    this.onInit = isFn(config.onInit) ? config.onInit : noop;
    this.beforeCreateOrder = isFn(config.beforeCreateOrder) ? config.beforeCreateOrder : initialData.beforeCreateOrder;
    this.createOrder = isFn(config.createOrder) ? config.createOrder : noop;
    this.beforeContinue = isFn(config.beforeContinue) ? config.beforeContinue : noop;
    this.onError = isFn(config.onError) ? config.onError : noop;
    this.payNowMode = this.payNowMode.bind(this);
    this.continueMode = this.continueMode.bind(this);
    this.onCancel = isFn(config.onCancel) ? config.onCancel : noop;
    this.configBeforeRender = isFn(config.beforeRender) ? config.beforeRender : noop;
    this.afterRender = isFn(config.afterRender) ? config.afterRender : noop;
    this.preparePayParams = null;
  }

  getDomHeight(height = 45) {
    // PayPal 25 <= height <= 55
    if (height > 55) {
      logger.error(`${this.loggerPrefix}[]`, { height });
      return 55;
    }
    if (height < 25) {
      logger.error(`${this.loggerPrefix}[]`, { height });
      return 25;
    }
    return height;
  }

  get isContinueMode() {
    return this.payMode === 'continue';
  }

  // 
  get isVerticalLayout() {
    return this.config.styleOption.commonStyle.paypalSPBStyle.layout === 'vertical';
  }

  // eslint-disable-next-line class-methods-use-this
  transProducts(products) {
    return products.map((item) => ({
      productNum: item.num,
      productSeq: item.spuId,
      productSku: item.skuId,
      productPrice: item.price,
    }));
  }

  /**
   * 
   * @param {Error} error 
   * @param {string} type  iniRender:
   */
  onPayPalError(error, type) {
    // sentry
    if (error?.message !== 'caught') {
      const splitter = ({ k, v }) => {
        return v ? `[${k}: ${v}]` : '';
      };
      logger.error(
        // eslint-disable-next-line max-len
        `${this.loggerPrefix}[onError]${splitter({ k: 'code', v: error?.code })}${splitter({
          k: 'message',
          v: error?.message || error?.msg,
        })}`,
        {
          ...error,
          code: error?.code,
          message: error?.message,
        },
      );
      const text = conversion(error);
      this.onToast({ message: text });
    }

    this.onError(error, type);
  }

  async continueMode() {
    let dataReportReq;
    try {
      if (isFn(this.config.needReport)) {
        const eventID = this.config.needReport();
        let price = 0;
        this.products.forEach((item) => {
          price += item.productPrice * item.productNum;
        });
        dataReportReq = setAddtoCart(price, window?.SL_State.get('storeInfo.currency'), `addToCart${eventID}`);
      }
    } catch (error) {
      this.onPayPalError(error);
    }

    // *try..catchPayPalonerrorPayPalonError
    const { originUrl, query } = adaptor(this.createOrderParams.returnUrl);
    const { engineType, preview, themeId, ignoreRedirect } = query;
    const { url: wrapperUrl } = adaptor(originUrl, {
      query: {
        engineType,
        preview,
        themeId,
        ignoreRedirect,
      },
      fullQuery: false,
    });

    const { manualCapture } = getPayPalServerConfig();

    const params = {
      payMethod: 'Paypal',
      dataReportReq,
      abandonedOrderInfo: this.preparePayParams,
      returnUrl: wrapperUrl,
      extChannelInfo: {
        manualCapture,
      },
    };
    try {
      const { data = {} } = await preparePay(params);
      const { chDealId, returnUrl: continueRedirectUrl } = data;
      // 
      SL_EventBus.emit('trade:spb:report', {
        data: {
          event_status: chDealId ? 1 : 0,
          stage: this.config.stage ?? this.config.pageType,
          product: this.products,
        },
      });
      PayPalReturnUrl = continueRedirectUrl;
      logger.debug(`${this.loggerPrefix}[][createOrder][chDealId][${chDealId ? '' : ''}]`, {
        ...data,
      });
      return chDealId;
    } catch (e) {
      SL_EventBus.emit('trade:spb:report', {
        data: {
          event_status: 0,
          stage: this.config.stage ?? this.config.pageType,
          product: this.products,
        },
      });
    }
  }

  async payNowMode() {
    try {
      const { chDealId, returnUrl: payNowReturnUrl } = await this.createOrder(this.createOrderParams);
      PayPalReturnUrl = payNowReturnUrl;
      logger.debug(`${this.loggerPrefix}[][createOrder][chDealId][${chDealId ? '' : ''}]`, {
        chDealId,
        returnUrl: payNowReturnUrl,
      });
      return chDealId;
    } catch (error) {
      const text = conversion(error);
      this.onToast({ message: text });
      this.onPayPalError(error);
      return Promise.reject({ message: text });
    }
  }

  async onUserCancel(data, { redirect }) {
    logger.info(`${this.loggerPrefix}[PayPal]`, {
      abandonedSeq: this.abandonedSeq,
    });
    const redirectUrl = await this.onCancel(data);
    if (redirectUrl) {
      redirect(redirectUrl);
    }
  }

  async onCurrentInit(data, actions) {
    if (this.initTimer) {
      clearTimeout(this.initTimer);
    }
    this.initActions = actions;
    logger.debug(`${this.loggerPrefix}[]`);
    this.onInit(data, actions);
  }

  /** onClick cbFn */
  async onClick({ fundingSource } = {}, actions) {
    // fundingSource paypal|card|paylater
    logger.info(`${this.loggerPrefix}[][${fundingSource}]`);
    let orderParams = await this.beforeCreateOrder(this)?.catch((error) => {
      this.onPayPalError(error);
      return actions.reject();
    });
    logger.debug(`${this.loggerPrefix}[][beforeCreateOrder][]`);
    // validfalse,
    if (orderParams?.valid === false) {
      return actions.reject();
    }
    // 
    this.initActions.enable();
    // 
    orderParams = orderParams || { products: this.products }; // {} || { products, extra }
    this.createOrderParams = orderParams;
    /**
     * extra: {
     *  abandonedOrderInfo: {
     *       seq: string;
     *       mark: string;
     *  }
     * }
     * */
    const { products, ...extra } = orderParams;
    if (!products || products.length <= 0) {
      this.onPayPalError({ message: `products empty, request aborted: ${products}` });
      return actions.reject();
    }

    if (!this.isContinueMode) {
      return;
    }

    this.products = products;

    const {
      url: returnUrl,
      needLogin,
      abandonedInfo,
    } = await checkout.save(products, {
      ...extra,
      stage: this.config.stage, // 
      query: {
        ...extra.query,
        spb: true,
      },
    });
    this.createOrderParams.returnUrl = returnUrl;
    this.preparePayParams = abandonedInfo;
    if (needLogin) {
      window.location.href = returnUrl;
      return actions.reject();
    }
  }

  async onCreateOrder() {
    logger.debug(`${this.loggerPrefix}[][createOrder][chDealId]`);
    try {
      if (this.isContinueMode) {
        return this.continueMode();
      }

      return this.payNowMode();
    } catch {
      return '';
    }
  }

  // !onClick
  get abandonedSeq() {
    if (this.isContinueMode) {
      return this.preparePayParams?.seq;
    }
    // !
    // TODO: tag
    return this.createOrderParams?.abandonedOrderSeqInfo?.seq;
  }

  /** createToken */
  onContinue() {
    const tipsTxt = this.isContinueMode ? '' : '';
    logger.info(`${this.loggerPrefix}[${tipsTxt}][][onContinue][${PayPalReturnUrl}]`, {
      jumpUrl: PayPalReturnUrl,
      abandonedSeq: this.abandonedSeq,
    });
    // eslint-disable-next-line no-unused-expressions
    isFn(this.beforeContinue) && this.beforeContinue({ returnUrl: PayPalReturnUrl });
    window.location.href = PayPalReturnUrl;
    logger.info(`${this.loggerPrefix}[${tipsTxt}][]`, {
      abandonedSeq: this.abandonedSeq,
    });
    return false;
  }

  /**
   *
   * @param {Array<{
      spuId: string;
      skuId: string;
      name: string;
      num: number;
      price: number;
    }>} products
   */
  setProducts(products) {
    logger.debug(`${this.loggerPrefix}[PayPal]`, { products });
    this.products = this.transProducts(products);
  }

  setDisabled(disabled) {
    if (!this.node) {
      return;
    }

    this.disabled = disabled;
    try {
      if (disabled) {
        // disable
        this.initActions?.disable();
        this.createDisabledStyle(this.node);
        return;
      }
      this.initActions?.enable();

      // eslint-disable-next-line no-unused-expressions
      this.disabledStyleTag && this.node.parentElement && this.node.parentElement.removeChild(this.disabledStyleTag);
    } catch (err) {
      console.log('', err);
    }
  }

  createStyle(node) {
    const skeleton = document.createElement('div');
    this.skeleton = skeleton;
    skeleton.classList.add('paypal__skeleton--animated');
    const styleTag = document.createElement('style');
    const linearGradient = [
      `90deg`,
      `hsla(0, 0%, 74.5%, 0.2) 25%`,
      `hsla(0, 0%, 50.6%, 0.24) 37%`,
      `hsla(0, 0%, 74.5%, 0.2) 63%`,
    ].join(', ');

    const skeletonAnimated = `
      @keyframes skeleton {
        0% {
          background-position: 100% 50%;
        }
        100% {
          background-position: 0 50%;
        }
      }
    `;
    // 
    styleTag.innerHTML =
      this.isContinueMode && !this.isVerticalLayout
        ? `
      #${this.config.domId} {
        overflow: hidden;
        text-align: center;
        height: ${this.style.height}px;
      }
      /**  */
      #${this.config.domId}.paypal__bg::before,
      #${this.config.domId}.paypal__bg::after {
        content:'';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: calc(50% - 3px);
        border-radius: 4px;
        background-color: #ffc439;
      }
      #${this.config.domId}.paypal__bg::after {
        left: unset;
        right: 0;
      }
      [data-button_style=square] .paypal__bg::before,
      [data-button_style=square] .paypal__bg::after{
        border-radius: 0 !important;
      }
      [data-button_style=rounded] .paypal__bg::before,
      [data-button_style=rounded] .paypal__bg::after{
        border-radius: 9999px !important;
      }
    `
        : `
      #${this.config.domId} {
        min-height: ${this.style.height}px;
      }
      `;
    styleTag.innerHTML += `

    #${this.config.domId} {
      position: relative;
    }
    #${this.config.domId} .paypal__skeleton--animated {
      margin: 0 auto;
      border-radius: 4px;
      width: 100%;
      height: ${this.style.height}px;
      position: absolute;
    }

    .paypal__skeleton--animated {
      background: linear-gradient(${linearGradient});
      background-size: 400% 100%;
      animation: skeleton 2s linear infinite;
    }

    ${skeletonAnimated}
    `;

    if (!node) {
      return;
    }

    if (this.wrapperClass) {
      node.classList.add(this.wrapperClass);
    }

    if (this.wrapperStyle) {
      let wrapperStyle = {};
      const nodeStyleAttr = node.getAttribute('style').replace(/\s+/g, '');
      const convertInputStyle = (stringTypeStyle) => {
        const style = {};
        const input = stringTypeStyle.split(';');
        input.forEach((s) => {
          const [k, v] = s.split(':');
          const key = k.replace(/-[^0-9]/g, (v) => v.charAt(1).toUpperCase());
          style[key] = v;
        });
        return style;
      };

      const convertOutputStyle = (cssObjectStyle) => {
        if (typeof cssObjectStyle !== 'object') {
          return '';
        }

        return Object.entries(cssObjectStyle)
          .map((o) => o.join(':'))
          .join(';')
          .replace(/[A-Z]/g, (v) => `-${v.charAt(0).toLowerCase()}`);
      };

      if (typeof this.wrapperStyle === 'string') {
        wrapperStyle = {
          ...convertInputStyle(nodeStyleAttr),
          ...convertInputStyle(this.wrapperStyle),
        };
      } else if (typeof this.wrapperStyle === 'object') {
        wrapperStyle = {
          ...convertInputStyle(nodeStyleAttr),
          ...this.wrapperStyle,
        };
      }

      // eslint-disable-next-line no-param-reassign
      node.style = convertOutputStyle(wrapperStyle);
    }
    node.parentElement.prepend(styleTag);
    node.appendChild(skeleton);
  }

  createDisabledStyle(node) {
    const styleTag = document.createElement('style');
    this.disabledStyleTag = styleTag;
    styleTag.innerHTML = `
      #${this.config.domId || 'shopline-paypal'}::before {
        width: 0 !important;
      }
      #${this.config.domId || 'shopline-paypal'}::after {
        content: '';
        display: block;
        z-index: 100;
        width: 100% !important;
        height: ${this.style.height}px;
        border-radius: 4px;
        cursor: not-allowed;
        position: absolute;
        top: 0;
        left: 0 !important;
        background: transparent !important;
      }

      #${this.config.domId} {
        opacity: ${this.disabled ? 0.3 : 1};
      }
    `;

    if (!node) {
      return;
    }

    node.parentElement.prepend(styleTag);
  }

  removeSkeleton(node) {
    const { skeleton } = this;
    clearTimeout(this.removeSkeletonTimer);
    this.removeSkeletonTimer = setTimeout(() => {
      if (node) {
        node.removeChild(skeleton);

        // 
        if (this.isContinueMode) {
          node.classList.add('paypal__bg');
        }
      }
    }, 300);
  }

  // 
  dynamicNotify(remembered) {
    const { paypal } = remembered || {};
    this.isHidePayPalButton = !paypal;
    if (!paypal && this.onDynamicNotify) {
      this.onDynamicNotify({ ...remembered, isHidePayPalButton: !paypal });
    }
  }

  /**
   *
   * @param {{ paypal: boolean; venmo: boolean }} remembered
   */
  jsonpCallback(remembered) {
    /** paypal: true  paypal */
    logger.debug(`${this.loggerPrefix}[cookie][remembered]`, { remembered });

    dynamicRemembered.info = remembered;
    dynamicRemembered.loadingStatus = 2;
    this.dynamicNotify(remembered);
    if (dynamicRemembered.cbFnList) {
      try {
        dynamicRemembered.cbFnList.forEach((cbFn) => cbFn(remembered));
        dynamicRemembered.cbFnList = [];
      } catch (e) {
        dynamicRemembered.cbFnList = [];
      }
    }
  }

  async beforeRender() {
    try {
      this.configBeforeRender();
    } catch (error) {
      this.onPayPalError({ message: '[beforeRender]', error });
    }

    let paypalConfig = {};
    try {
      const { data } = await getPaypalConfig();
      paypalConfig = data || {};
    } catch (error) {
      this.onPayPalError({ message: '[paypal]', error });
    }

    return {
      // SLClientKey
      // paypalconfigclientId
      clientKey: getPaypalKey(paypalConfig),
      paypalQueryParams: getQueryParams(this.config, { isContinueMode: this.isContinueMode, paypalConfig }),
    };
  }

  async render() {
    /**
     * 
     */
    if (this.dynamic) {
      if (dynamicRemembered.loadingStatus !== 2) {
        // 
        if (dynamicRemembered.loadingStatus === 1) {
          // render
          dynamicRemembered.cbFnList.push(this.render.bind(this));
          return;
        }
        // 
        dynamicRemembered.loadingStatus = 1;
        window[dynamicCallBackName] = this.jsonpCallback;
        try {
          await loadScript(PayPalRememberedScript(dynamicCallBackName));

          logger.debug(`${this.loggerPrefix}[ script]: ${dynamicCallBackName}`);
        } catch (error) {
          this.onPayPalError(error);
        }
      } else {
        // 
        this.dynamicNotify(dynamicRemembered.info);
      }
    }
    if (this.isHidePayPalButton !== undefined && this.isHidePayPalButton) {
      return null;
    }

    if (!this.config.domId || this.rendered) return null;

    const node = document.getElementById(this.config.domId);

    if (!node) {
      logger.error(`${this.loggerPrefix}[onError][][DOM]`);

      return null;
    }

    this.node = node;

    if (this.config.timeout?.switch) {
      // dom
      if (renderObserver) {
        // 
        renderObserver.disconnect();
        // 
        renderObserver = null;
      }
      this.initTimer = setTimeout(() => {
        // 
        this.node.innerHTML = '';

        // FirefoxChrome
        const MutationObserver = window.MutationObserver || window.WebKitMutationObserver || window.MozMutationObserver;

        // 
        renderObserver = new MutationObserver((mutations) => {
          mutations.forEach(() => {
            // PayPal
            this.node.innerHTML = '';
          });
        });
        // :
        const config = {
          childList: true, // 
          attributes: false, // 
          subtree: false, //  false
        };
        // 
        // eslint-disable-next-line no-unused-expressions
        node && renderObserver.observe(node, config);
        // 5sobserver
        setTimeout(() => {
          // 
          if (!renderObserver) {
            return;
          }
          // 
          renderObserver.disconnect();
          // 
          renderObserver = null;
        }, 5000);
        this.initError = true;
        this.onPayPalError(
          new Error(`[][onInit][:${this.config.timeout.delay}ms]`),
          'initRender',
        );
        throw new Error(`[][onInit][:${this.config.timeout.delay}ms]`);
      }, this.config.timeout.delay);
    }
    this.createStyle(node);
    const { clientKey, paypalQueryParams } = await this.beforeRender();

    const paypalSdkInitParams = {
      ...this.config,
      paypalQueryParams,
      createOrder: this.onCreateOrder.bind(this),
      createToken: this.onContinue.bind(this),
      paypalProps: {
        onClick: this.onClick.bind(this),
        onError: this.onPayPalError.bind(this),
        onInit: this.onCurrentInit.bind(this),
        onCancel: this.onUserCancel.bind(this),
      },
    };
    const PayPalInstance = new (getPaypalSDK())(clientKey, paypalSdkInitParams);
    // eslint-disable-next-line no-unused-expressions
    PayPalInstance && this.removeSkeleton(node);

    this.rendered = true;
    this.afterRender({ dynamicInfo: dynamicRemembered.info });
    logger.debug(`${this.loggerPrefix}[SDK]`, {
      ...paypalSdkInitParams,
    });

    return PayPalInstance;
  }
}

export default PayPalSmartPayButton;
