import url from 'url';
import querystring from 'querystring';

// url
export function changeURLArg(urlStr, arg, argVal) {
  const durl = decodeURIComponent(urlStr);
  const pattern = `${arg}=([^&]*)`;
  const replaceText = `${arg}=${argVal}`;
  if (durl.match(pattern)) {
    let tmp = `/(${arg}=)([^&]*)/gi`;
    // eslint-disable-next-line
    tmp = durl.replace(eval(tmp), replaceText);
    return tmp;
  }
  if (durl.match('[?]')) {
    return `${durl}&${replaceText}`;
  }
  return `${durl}?${replaceText}`;
}

// url
export function getUrlQuery(key) {
  if (typeof window !== 'undefined') {
    const locationHref = window.location.href;
    const urlQuery = url.parse(decodeURIComponent(locationHref))?.query;
    const urlQueryObj = querystring.parse(urlQuery);
    const hitUrlQuery = urlQueryObj[key];
    if (hitUrlQuery) {
      //   sku=xxxx?sl_source_ad_platform=fb -> sku=xxxx&sl_source_ad_platform=fb
      if (hitUrlQuery && hitUrlQuery.indexOf('?') !== -1) {
        window.history.replaceState({}, document.title, changeURLArg(locationHref, key, `${hitUrlQuery.replace('?', '&')}`));
        return hitUrlQuery.split('?')[0];
      }
      return hitUrlQuery;
    }
    return null;
  }
  return null;
}

/**
 *  url 
 * @param href
 */
export function getUrlAllQuery(href) {
  const locationHref = href || window.location.href;
  const urlQuery = url.parse(decodeURIComponent(locationHref))?.query;
  const urlQueryObj = querystring.parse(urlQuery);

  return urlQueryObj;
}

// url
export function delParam(paramKey) {
  let { href } = window.location; // url
  const urlParam = window.location.search.substr(1); // 
  const beforeUrl = href.substr(0, href.indexOf('?')); // 
  let nextUrl = '';

  const arr = [];
  if (urlParam !== '') {
    const urlParamArr = urlParam.split('&'); // &
    urlParamArr.forEach((segment) => {
      const paramArr = segment.split('='); // 
      // 
      if (paramArr[0] !== paramKey) {
        arr.push(segment);
      }
    });
  }
  if (arr.length > 0) {
    nextUrl = `?${arr.join('&')}`;
  }
  href = beforeUrl + nextUrl;
  return href;
}

/**
 * :useParams
 * @param u
 * @param index
 */
export function getUrlPathId(u = window.location.href, index = -1) {
  const urlArr = url.parse(u).pathname?.replace(/^\//, '').split('/') || [];

  if (index < 0) {
    return urlArr[urlArr.length + index];
  }
  return urlArr[index];
}

export function updateUrlQueryParam(urlStr, param, value) {
  const re = new RegExp(`([?&])${param}=.*?(&|$)`, 'i');
  const separator = urlStr.indexOf('?') !== -1 ? '&' : '?';
  if (urlStr.match(re)) {
    return urlStr.replace(re, `$1${param}=${value}$2`);
  }
  return `${urlStr}${separator}${param}=${value}`;
}
