import http from '@yy/sl-theme-shared/utils/request';

// {{}}
const legalUrl = typeof window === 'undefined' ? '' : window.location.href.replace(/(&?\w+={.*?})|{|}/g, '');

const extChannelInfo = {
  shopper_action: 'CONTINUE',
  sub_channel: 'SPB',
  cancel_url: legalUrl,
  // ! PayPalSPB
  landing_page: 'LOGIN',
  locale: '',
};

/**
 * 
 * @param {{
    payMethod: 'Paypal';
    products: {
      skuId?: string;
      spuId?: string;
      name?: string;
      price?: string;
      quantity?: number;
      num?: number;
      skuInfo?: string;
    }
    returnUrl: string;
    extChannelInfo?: JSON;
    userPayInfo?: any;
  }} params
 * @returns {Promise<{ payOrderId: string; chDealId: string; returnUrl: string; }>}
 */
export const preparePay = (params) => {
  const { extChannelInfo: ext, ...rest } = params;
  return http.post('/trade/center/pay/pay-pal/prepare-pay', {
    extChannelInfo: JSON.stringify({
      ...extChannelInfo,
      ...(ext || {}),
    }),
    ...rest,
  });
};

export const PaymentChannelCodeEnum = {
  OceanPay: 'Oceanpay',
  Paypal: 'Paypal',
  Custom: 'CUSTOM',
};

export const getPaymentList = () => {
  return http.post('/merchant/payments/list', []);
};

export const getMerchantSwitch = (params) => {
  return http.get('/merchant/setting/get/switch', {
    params: params || {
      module: 'order_base',
    },
  });
};

export const checkCart = (data = []) => {
  return http.post('/carts/cart/check', data);
};

/**
 * paypal
 * http://apidoc.inshopline.com/#/project/78/feature%2FJY-795%2Fpaypal_pay_later/interface/73e9cd583825df66b83f0a1d10990672
 * @returns {Promise<{ paypalMerchantId: string; clientId: string; }>}
 */
export const getPaypalConfig = () => {
  return http.get('/trade/pay/payment/paypal/config/data');
};
