import { SL_State } from '@yy/sl-theme-shared/utils/state-selector';
import Timeout from './timeout';
import Percentage from './percentage';

class FlashSale {
  constructor() {
    this.shouldInit = false;
    const salesPlugins = SL_State.get('product')?.productSale?.saleActivities?.filter?.((item) => {
      return item.promotionSubType === 1 && item.promotionType === 1;
    });
    if (salesPlugins?.length > 0 && salesPlugins[0].salesPlugin) {
      this.shouldInit = true;
      this.startTime = salesPlugins[0]?.salesPlugin?.countDownTimerPlugin?.startTime;
      this.endTime = salesPlugins[0]?.salesPlugin?.countDownTimerPlugin?.endTime;
      this.saleNum = salesPlugins[0]?.salesPlugin?.progressPlugin?.saleNum;
      this.totalNum = salesPlugins[0]?.salesPlugin?.progressPlugin?.totalNum;
    }
  }

  // 
  init() {
    if (this.shouldInit) {
      const timeout = new Timeout({ startTime: this.startTime, endTime: this.endTime });
      const percentage = new Percentage({ saleNum: this.saleNum, totalNum: this.totalNum });
      timeout.changeTime();
      percentage.percentageInit();
    }
  }
}

export default FlashSale;
