import { HiidoReport } from '../enum';

const external = window?.Shopline.event;

let initialized = false;

function onHiidoReport(data) {
  if (!data || !data.event_id) {
    console.error(`failed to report to hiido without data and event_id`);
    return;
  }
  const { event_id, ...params } = data;
  window.HdSdk?.shopTracker.report(event_id, params);
}

function on() {
  if (!external) {
    console.error(`failed to init ${HiidoReport} listener: window?.Shopline.event not found`);
    return;
  }

  if (initialized) return;

  external.on(HiidoReport, onHiidoReport);
  initialized = true;
}

export default {
  on,
};
