import axios from 'axios';
import qs from 'query-string';

const http = axios.create({
  baseURL: '/leproxy',
  timeout: 30e3,
  withCredentials: true,
  paramsSerializer(params) {
    return qs.stringify(params);
  },
});

/**
 * hover 
 */
let timer = null;
$('body').on('mouseenter', '.site-nav__sortation-link', (e) => {
  e.preventDefault();
  e.stopPropagation();
  if (!timer) {
    timer = setTimeout(() => {
      getItemHtml(e);
      timer = null;
    }, 300);
  }
});
$('body').on('mouseleave', '.site-nav__sortation-link', () => {
  if (timer != null) {
    clearTimeout(timer);
    timer = null;
  }
});

async function getItemHtml(e) {
  const $current = $(e.currentTarget);
  const sortationId = $current.data('sortation-id'); // id
  const liId = $current.data('li-id'); // menu id
  const nodeType = $current.data('node-type');

  const itemLength = $(`li[data-site-nav-menu-id="${liId}"] .nav__sortation--item[data-navnode-id="${sortationId}"]`).length;
  if (itemLength) {
    // 
    $(`li[data-site-nav-menu-id="${liId}"] .nav__sortation--item[data-navnode-id="${sortationId}"]`).addClass('active');
    $(`li[data-site-nav-menu-id="${liId}"] .nav__sortation--item[data-navnode-id="${sortationId}"`).siblings().removeClass('active');
  } else if (Number(nodeType) === 3) {
    // step1: item
    const { data } = await getSortationInfo(sortationId);
    if (data.data.length && data.data[0]?.productList && data.data[0]?.productList.length) {
      // step2: item,
      const res = await getSection(data.data[0]?.productList[0]);
      if (res.status === 200) {
        const html = `<div class="nav__sortation--item active" data-navnode-id="${sortationId}">${res.data}</div>`;
        $(`li[data-site-nav-menu-id="${liId}"] .nav__product--container`).append(html);
        // hover
        $(`li[data-site-nav-menu-id="${liId}"] .nav__sortation--item[data-navnode-id="${sortationId}"`).siblings().removeClass('active');
      }
    }
  }
}

/**
 * hover 
 */
$('body').on('mouseenter', '.site-nav__item-link', (e) => {
  e.preventDefault();
  e.stopPropagation();
  const $current = $(e.currentTarget);
  // menu id
  const nodeId = $current.data('site-nav-menu-id');
  const itemLength = $(`li[data-site-nav-menu-id="${nodeId}"] .nav__sortation--item`).length;
  if (itemLength) {
    $(`li[data-site-nav-menu-id="${nodeId}"] .nav__sortation--item`).removeClass('active');
    $(`li[data-site-nav-menu-id="${nodeId}"] .nav__sortation--item`).first().addClass('active');
  } else if (!timer) {
    timer = setTimeout(() => {
      getfirstNavItemHtml(e);
      timer = null;
    }, 300);
  }
});
$('body').on('mouseleave', '.site-nav__item-link', () => {
  if (timer != null) {
    clearTimeout(timer);
    timer = null;
  }
});

/**
 * 
 */
async function getfirstNavItemHtml(e) {
  const $current = $(e.currentTarget);
  // menu id
  const nodeId = $current.data('site-nav-menu-id');

  const main_menu_link_list = $('.desktop-site-nav')?.data('main-menu-link-list') || '';
  const nodeTree = window.SL_State.get('navigations')[main_menu_link_list]?.nodeTree || [];
  const nodeItem = nodeTree.find((item) => {
    return String(item.id) === String(nodeId);
  });

  // step1: id
  const ids = Array.from(new Set(getSortationIdS(nodeItem)));
  if (ids.length === 0) return;

  // step2: item
  const reList = await pathGetInfo(groupArr(ids, 20));
  const dataList = [];
  reList.forEach((res) => {
    if (res.status === 200 && res?.data?.data && res?.data?.data?.length) {
      dataList.push(...res.data.data);
    }
  });

  let dataItem = null;
  for (let i = 0; i < ids.length; i++) {
    dataItem = dataList.filter((item) => {
      return String(item.sortationId) === String(ids[i]) && item?.productList.length > 0;
    });
    if (dataItem.length) break;
  }

  // step3: item,
  if (dataItem.length) {
    const res = await getSection(dataItem[0]?.productList[0]);
    if (res.status === 200) {
      const html = `<div class="nav__sortation--item active" data-navnode-id="${dataItem[0].sortationId}">${res.data}</div>`;
      $(`li[data-site-nav-menu-id="${nodeId}"] .nav__product--container`).append(html);
      // hover
      $(`li[data-site-nav-menu-id="${nodeId}"] .nav__sortation--item[data-navnode-id="${dataItem[0].sortationId}"`).siblings().removeClass('active');
    }
  }
}

/**
 * id
 * 
 * @param {Object} nodeItem 
 * @param {Array} sortationIdS id
 * @return {Array} id
 */
function getSortationIdS(nodeItem, sortationIdS = []) {
  const ids = [...sortationIdS];
  let path = nodeItem.childNodes.reduce((result, current) => {
    return [].concat(result, current);
  }, []);

  while (path.length > 0) {
    const node = path.shift();
    if (node.nodeType === 3) {
      ids.push(node.pageLink);
    }
    const target = node?.childNodes || [];

    if (target.length !== 0) {
      const aa = target.reduce((acc, cur) => {
        return [].concat(acc, cur);
      }, []);
      path = [].concat(path, target.length === 1 ? target : aa);
    }
  }
  return ids;
}

// 
export function groupArr(array, subGroupLength) {
  let index = 0;
  const newArray = [];
  while (index < array.length) {
    newArray.push(array.slice(index, (index += subGroupLength)));
  }
  return newArray;
}

// 
export function pathGetInfo(idList) {
  const reqList = [];

  for (let i = 0; i < idList.length; i++) {
    const req = http.get(`/api/product/list/sortation/info/batch/query`, {
      params: {
        sortationIdStr: idList[i].join(','),
        needProductNum: 1,
      },
    });
    reqList.push(req);
  }

  return axios.all(reqList).then(
    axios.spread((...res) => {
      return res;
    }),
  );
}

// 
export function getSortationInfo(sortationIds) {
  return http.get(`/api/product/list/sortation/info/batch/query`, {
    params: {
      sortationIdStr: sortationIds,
      needProductNum: 1,
    },
  });
}

// 
export function getSection(spu) {
  return http.post(`/page/section`, {
    sectionId: 'header-nav-product-item',
    context: {
      spu,
      imageList: spu?.imageList,
      closeLazy: true,
    },
  });
}
