import { SL_EventEmitter } from '@yy/sl-theme-shared/utils/event-bus';

/**  */
export const ValidateTrigger = {
  /** onchangeinput */
  ONCHANGE: 'onChange',
  /**  */
  MANUAL: 'manual',
  /**  */
  ONBLUR: 'manual',
  /** change */
  ONNATIVECHANGE: 'onNativeChange',
};

const isPromiseFulfilledResult = (result) => {
  return result.status === 'fulfilled';
};

const formItemName = 'sl-form-item-name';
const REQUIRED_SYMBOL = Symbol('required');

class CustomForm {
  fid = '';

  /**  */
  onDestory = (fid) => {};

  formEntity = null;

  validateRecord = {};

  config = {
    validateTrigger: ValidateTrigger.MANUAL,
    requiredErrMsg: `!`,
    defaultErrMsg: '',
    errContainerClss: 'errClass',
    // errInpWrapperClss: 'is-error',
    validateWhenInit: false,
    emitChangeWhenInit: false,
  };

  canValidate = false;

  canEmitChange = true;

  hadInit = false;

  el = null;

  eventBus = null;

  on = null;

  emit = null;

  constructor(fid, { onDestory } = { onDestory: () => {} }) {
    this.fid = fid;
    this.onDestory = onDestory;
    this.el = document.querySelector(`#${fid}`);
    const eventBus = new SL_EventEmitter();
    this.eventBus = eventBus;
    this.on = this.eventBus.on.bind(eventBus);
    this.emit = this.eventBus.emit.bind(eventBus);
  }

  init(config) {
    if (this.hadInit) return false;
    try {
      // form
      this.initFormConfig(config);

      // form
      const formItems = this.el.querySelectorAll(`[${formItemName}]`);

      // name & input/select
      const { initialData, childrenProps } = this.calculatePropsAndInitialData(formItems);
      this.formEntity = { data: initialData, childrenProps, el: this.el };

      // 
      this.initEventListener(this.el);

      this.hadInit = true;
    } catch (e) {
      console.warn(`${this.fid} is not found, ${e}`);
    }
  }

  initFormConfig(config) {
    if (config) {
      Object.assign(this.config, config);
      if (config?.validateWhenInit) this.canValidate = true;
      if (config?.emitChangeWhenInit) this.canEmitChange = true;
    }
  }

  initEventListener(el) {
    el.addEventListener('input', this.handleFormInputEvent);

    const formItems = el.querySelectorAll(`[${formItemName}]`);
    formItems.forEach((el) => {
      const inp = el.querySelector('input,textarea');
      if (inp) {
        inp.addEventListener('change', (e) => {
          console.log('input-change', this.config.validateTrigger);
          if (this.config.validateTrigger === ValidateTrigger.ONNATIVECHANGE) {
            const target = e.target;
            const parentNode = this.recursionFindParent(target, formItemName);
            if (!parentNode) return;
            const targetName = parentNode.getAttribute(formItemName);
            this.validateFields([targetName]);
          }
        });
        inp.addEventListener('blur', (e) => {
          const target = e.target;
          const parentNode = this.recursionFindParent(target, formItemName);
          if (!parentNode) return;
          const targetName = parentNode.getAttribute(formItemName);
          if (this.config.validateTrigger === ValidateTrigger.ONBLUR) {
            this.validateFields([targetName]);
          }
          if (this.config.blurSucHandler || this.config.blurErrHandler) {
            this.validateFields([targetName], false).then((res) => {
              if (!res) return;
              if (res.pass) {
                this.config.blurSucHandler?.(targetName, target.value, this.formEntity.data);
              } else {
                this.config?.blurErrHandler(res);
              }
            });
          }
        });
      }
    });
  }

  handleFormInputEvent = (e) => {
    if (!this.fid) return;
    const target = e.target;
    const parentNode = this.recursionFindParent(target, formItemName);
    if (!parentNode) return;
    const targetName = parentNode.getAttribute(formItemName);
    if (targetName) {
      // value 
      this.canValidate = true;
      if (this.isRadioOrCheckbox(target, ['checkbox'])) {
        this.setLocalsValue(targetName, target.checked);
      } else {
        this.setLocalsValue(targetName, target.value);
      }
    }
  };

  recursionFindParent(el, attr) {
    const parent = el.parentElement;
    if (!parent) return null;
    const val = parent.getAttribute(attr);
    if (val) return parent;
    return this.recursionFindParent(parent, attr);
  }

  calculatePropsAndInitialData(nodeList) {
    if (nodeList.length === 0) return { initialData: {}, childrenProps: [] };
    let childrenProps = [];
    const initialData = {};
    try {
      childrenProps = Array.from(nodeList).map((formItem) => {
        const name = formItem.getAttribute(formItemName);
        const child = formItem.querySelector('input,select,textarea');
        const attrs = child.getAttributeNames();
        const childAttrs = attrs.reduce((acc, curAttr) => {
          if (curAttr === 'value') {
            initialData[name] = child.getAttribute(curAttr);
          }
          return {
            ...acc,
            [curAttr]: child.getAttribute(curAttr),
          };
        }, {});
        return Object.assign(childAttrs, { name });
      });
    } catch (e) {
      console.error(`${this.fid} calculate form item error: `, e);
    }

    return { initialData, childrenProps };
  }

  // dom value & 
  setValues(fields) {
    fields.forEach(({ name, value, rules }) => {
      // dom
      this.setDomValue(this.el, name, value);
      // 
      this.setRule(rules, name);
      // 
      this.setLocalsValue(name, value);
    });
  }

  setRule(rules, name) {
    const target = this.formEntity.childrenProps.find((prop) => prop.name === name);
    if (target) target.rules = rules || [];
  }

  isRadioOrCheckbox(target, nodeTypeList = ['radio', 'checkbox']) {
    const nodeType = target?.getAttribute('type');
    if (!nodeType) return false;
    return target.nodeName.toLocaleLowerCase() === 'input' && nodeTypeList.includes(nodeType);
  }

  setDomValue(parent, name, value) {
    // or form item
    const targets = parent.querySelectorAll(
      `[${formItemName}=${name}] input,[${formItemName}=${name}] select,[${formItemName}=${name}] textarea`,
    );

    if (targets.length) {
      targets.forEach((target) => {
        if (this.isRadioOrCheckbox(target)) {
          // or value set checked
          if (target.value === value) {
            // target.setAttribute('checked', 'checked');
            // radiochecked
            // $(target).trigger('click');
            target.click();
          }
        } else if (!target.name || target.name === name) {
          target.value = value ?? '';
        }
      });
    }
  }

  setLocalsValue = (name, value) => {
    const changedValue = { [name]: value };
    const allValues = Object.assign(this.formEntity?.data, changedValue);
    let validateResultPro = null;
    if (this.canValidate && this.config.validateTrigger === ValidateTrigger.ONCHANGE) {
      validateResultPro = this.validateFields([name]);
    }
    if (this.canEmitChange) {
      this.eventBus.emit('valuesChange', {
        changedValue,
        allValues,
        validateResult: validateResultPro,
      });
    }
  };

  flattenRulesList(list) {
    return list.reduce((acc, field) => {
      const subRules = field?.rules.map((rule) => ({ ...rule, name: field.name })) || [];
      return [...acc, ...subRules];
    }, []);
  }

  getErrTmpStr(messages, className = '') {
    return messages.reduce(
      (acc, message) =>
        (acc += `<div class="${className}" style="margin-top: 6px;color: #f04949;font-size: 12px;line-height: 1.4;">${message}</div>`),
      '',
    );
  }

  getErrListContainer(id, formItemContainer) {
    const target = this.el.querySelector(`[${id}]`);
    if (target) return target;
    const div = document.createElement('div');
    div.setAttribute(id, '1');
    formItemContainer.appendChild(div);
    return div;
  }

  setErrMsgIntoDom(errFields) {
    errFields.forEach(({ name, messages }) => {
      const errTmp = this.getErrTmpStr(messages, this.config.errContainerClss);
      const id = `cf-${this.fid}-${name}`;
      const target = this.el.querySelector(`[${formItemName}=${name}]`);
      // form item
      if (!Array.from(target.classList).includes(this.config.errContainerClss)) {
        target.classList.add(this.config.errContainerClss);
      }
      const container = this.getErrListContainer(id, target);
      container.innerHTML = errTmp;
    });
  }

  removeErrList(fields) {
    // 
    fields.forEach((name) => {
      const target = this.el.querySelector(`[cf-${this.fid}-${name}]`);
      if (target) target.remove();
      // 
      if (this.config.errContainerClss) {
        const formItemWrapper = this.el.querySelector(`[${formItemName}=${name}]`);
        formItemWrapper.classList?.remove?.(this.config.errContainerClss);
      }
    });
  }

  setErrFields = (target, errFields, name, errMsg) => {
    if (!target) {
      errFields.push({
        name,
        messages: [errMsg],
      });
    } else {
      target.messages.push(errMsg);
    }
  };

  /**
   * @param fields: FieldDataType[]
   * @param callback: 
   * @param needEmit: valuesChangetrue
   * @param needValidate: false
   */
  setFields(fields, callback, needEmit = true, needValidate = false) {
    try {
      this.canValidate = needValidate;
      this.canEmitChange = needEmit;
      this.setValues(fields);
      callback?.();
    } catch (e) {
      console.warn(`${this.fid} set fields fail,`, e);
    } finally {
      this.canEmitChange = true;
    }
  }

  /**
   * 
   * @param rulesField: {name: string, rules: IRule[]
   */
  setRules(rulesField) {
    rulesField.forEach(({ name, rules }) => {
      this.setRule(rules, name);
    });
  }

  getFieldValue(fieldName) {
    this.init();
    return this.formEntity.data[fieldName];
  }

  getFieldsValue() {
    this.init();
    return this.formEntity?.data;
  }

  /**
   * 
   * @returns Record<string, any>
   */
  async getLegalFieldsValue() {
    const result = await this.validateFields([], false);
    if (result?.pass) return this.formEntity?.data;

    const unPassFields = result.errFields?.map((field) => field.name);
    return Object.entries(this.formEntity.data).reduce((acc, [k, v]) => {
      if (unPassFields.includes(k)) return acc;
      return { ...acc, [k]: v };
    }, {});
  }

  /**
   * @param fields
   * @param handleError
   */
  async validateFields(fields, handleError = true) {
    // form
    if (!this.formEntity) return null;
    const { childrenProps } = this.formEntity;
    const data = JSON.parse(JSON.stringify(this.formEntity.data));
    const needValidateFieldsName = fields?.length ? fields : Object.keys(this.formEntity.data);
    const needValidatefields = childrenProps.filter(
      (prop) => prop.rules?.length > 0 && needValidateFieldsName?.includes(prop.name),
    );
    const validateList = [];
    const needValidateRules = this.flattenRulesList(needValidatefields);
    try {
      needValidateRules.forEach((rule) => {
        const { required, validator, pattern, name } = rule || {};
        const value = data[name];

        // 
        this.validateRecord[name] = value;

        //  
        if (value) {
          if (validator) {
            validateList.push(validator(value, data));
          } else if (pattern) {
            validateList.push(new RegExp(pattern).test(value));
          } else {
            validateList.push(true);
          }
        } else if (validator && required) {
          //  
          validateList.push(validator(value, data));
        } else {
          validateList.push(required ? REQUIRED_SYMBOL : true);
        }
      });
    } catch (e) {
      console.warn(`${this.fid} calculate validator list fail:`, e);
    }

    const validateResult = await Promise.allSettled(validateList);
    const errFields = [];
    const successFields = new Set();
    for (let i = 0; i < validateResult.length; i++) {
      const { name, message } = needValidateRules[i];
      // 
      if (this.validateRecord[name] !== data[name]) return null;
      const result = validateResult[i];
      // 
      const target = errFields.find((err) => err.name === name);

      // 
      if (isPromiseFulfilledResult(result)) {
        // 
        if (result.value === REQUIRED_SYMBOL) {
          const requiredErrMsg = needValidateRules[i]?.message || this.config.requiredErrMsg;
          if (!target) {
            errFields.push({
              name,
              messages: [requiredErrMsg],
            });
          }
        } else if (result.value === false) {
          const errMsg = message || this.config.defaultErrMsg;
          this.setErrFields(target, errFields, name, errMsg);
        } else {
          // 
          successFields.add(name);
        }
      } else {
        // rules.message > reject.reason > defaultErrMsg
        const errMsg = message || result.reason || this.config.defaultErrMsg;
        // 
        this.setErrFields(target, errFields, name, errMsg);
      }
    }

    // 
    if (handleError) {
      //   & 
      this.removeErrList(successFields);

      // DOM
      this.setErrMsgIntoDom(errFields);

      //  onChange
      if (errFields.length) {
        this.config.validateTrigger = ValidateTrigger.ONCHANGE;
      }
    }

    return errFields.length ? { pass: false, errFields } : { pass: true };
  }

  /**
   * 
   * @param fields: ,
   */
  resetErrStatus(fields = Object.keys(this.formEntity.data)) {
    this.removeErrList(fields);
  }

  destroy() {
    this.el.removeEventListener('input', this.handleFormInputEvent);
    this.hadInit = false;
    this.el = null;
    this.eventBus = null;
    this.formEntity = null;
    this.onDestory?.(this.fid);
    this.fid = null;
  }
}

export default CustomForm;
