// import cartChangeItemModel from './cartChangeItem';
import cartChangeItemModel from '@yy/sl-theme-shared/biz-com/trade/optimize-modal/cartChangeItem';
import cartItemModel from './cartItem';
import productVerifyTypeVO from '../vo/productVerifyType';

function makeVerifyList(cartItemList, checkList) {
  const checkItemMap = (checkList || []).reduce((result, item) => {
    result[`${cartChangeItemModel.getGroupId(item)}-${cartChangeItemModel.getSkuId(item)}`] = item;
    return result;
  }, {});

  const results = [];

  (cartItemList || []).forEach((item) => {
    if (item) {
      const changeItem = checkItemMap[`${cartItemModel.getGroupId(item)}-${cartItemModel.getSkuId(item)}`];
      results.push({
        verifyType: cartChangeItemModel.getVerifyType(changeItem),
        cartItem: item,
        cartChangeItem: changeItem,
      });
    }
  });

  return results;
}

function isVerifyFailed(list) {
  if (!Array.isArray(list)) return false;
  if (list.length <= 0) return false;

  return list.find((item) => {
    return productVerifyTypeVO.hasError(item);
  });
}

function filterFailedList(list) {
  if (!Array.isArray(list)) return [];
  if (list.length <= 0) return [];

  return list.filter((item) => {
    return productVerifyTypeVO.hasError(item);
  });
}

function getVerifiedCartItemList(list) {
  if (!list || list.length <= 0) return [];

  return list
    .map((verifyItem) => {
      const { cartItem, cartChangeItem } = verifyItem;
      const { errorInfo } = cartChangeItem || {};
      if (!errorInfo) {
        return {
          ...cartItem,
        };
      }
      const { targetNum } = errorInfo;
      if (targetNum > 0) {
        return {
          ...cartItem,
          num: targetNum,
        };
      }
      // if (productVerifyTypeVO.noError(verifyItem)) {
      //   return {
      //     ...cartItem,
      //   };
      // }

      // if (productVerifyTypeVO.isUnderStock(verifyItem)) {
      //   const stock = cartChangeItemModel.getStock(cartChangeItem);
      //   if (stock <= 0) {
      //     return null;
      //   }

      //   return {
      //     ...cartItem,
      //     num: stock,
      //   };
      // }

      return null;
    })
    .filter((v) => !!v);
}

export default {
  makeVerifyList,
  isVerifyFailed,

  filterFailedList,
  getVerifiedCartItemList,
};

/**
 * type ICartVerifyItem = {verifyType, cartItem, cartChangeItem};
 * type ICartVerifyItemList = ICartVerifyItem[];
 * */
